import './ModalSinglebuy.css'

import React, {useImperativeHandle, useRef, useState} from 'react'
import {IonModal} from '@ionic/react'
import * as FIcon from 'react-feather'
import {HeaderRegistro} from './HeaderRegistro'
import {useStores} from "../models/root-store/root-store-context"

type ModalBancardSinglebuyProps = {
  ref: any
  successCallback: any
}

export const ModalSinglebuy: React.FC<ModalBancardSinglebuyProps> = React.forwardRef((props, ref) => {
  const rootStore = useStores()
  const { carritoStore } = rootStore
  const [Url, setUrl] = useState(null as any)
  const [Visible, setVisible] = useState(false)
  const [IframeHeight, setIframeHeight] = useState(350)
  const titulo = carritoStore.medioPago === 3 ? 'Tigo Money' : 'Tarjeta de débito o crédito ocasional'
  const intervalVerificar = useRef(null as any)
  const transaccionId = useRef(null as any)

  useImperativeHandle(ref, () => ({
    iniciarPago(transaccion_id: string, url: string) {
      transaccionId.current = transaccion_id
      setUrl(url)
      setVisible(true)
    },
  }))

  const verificar_transaccion = (tipo: string) => {
    if (!transaccionId.current) {
      // Esto no debería ocurrir pero hay casos donde está pasando
      return
    }
    rootStore.environment.api.verificarTransaccion(transaccionId.current, tipo).then((result) => {
      if (result.kind === 'ok') {
        if (carritoStore.medioPago === 3) {
          // Tigo Money
          if (result.data.status === 'success') {
            clearInterval(intervalVerificar.current)
            setVisible(false)
            props.successCallback()
          } else if (result.data.status === 'fail' || result.data.status === 'reverted') {
            clearInterval(intervalVerificar.current)
            alert('Lo sentimos, ocurrió un error al procesar el pago')
            setVisible(false)
          } else if (result.data.status === 'cancel') {
            clearInterval(intervalVerificar.current)
            setVisible(false)
          }
        } else {
          if (result.data.transaccion_error_description) {
            clearInterval(intervalVerificar.current)
            alert(result.data.transaccion_error_description)
            setVisible(false)
          } else if (result.data.transaccion_response_code === '00') {
            clearInterval(intervalVerificar.current)
            setVisible(false)
            props.successCallback()
          }
        }
      }
    })
  }

  const handleMessage = (t: { data: { message: string; details: any; iframeHeight: number } }) => {
    if (t.data.message && ['payment_fail', 'payment_success'].indexOf(t.data.message) !== -1) {
      if (t.data.message === 'payment_success') {
        props.successCallback()
      } else {
        if (t.data.details) {
          alert(t.data.details)
          setVisible(false)
        } else {
          verificar_transaccion('bancard')
        }
      }
    }
    if (t.data.iframeHeight) {
      setIframeHeight(t.data.iframeHeight + 1)
    }
  }

  React.useEffect(() => {
    window.addEventListener('message', handleMessage)
    return () => {
      window.removeEventListener('message', handleMessage)
    }
  }, [])

  React.useEffect(() => {
    // Monitorear estado de la transacción (para bancard se usa handleMessage)
    if (carritoStore.medioPago === 3) {
      intervalVerificar.current = setInterval(() => verificar_transaccion('tigo'), 3000)
    }

    return () => {
      if (intervalVerificar.current) {
        clearInterval(intervalVerificar.current)
      }
    }
  }, [])

  if (!Visible) {
    return null
  }

  return (
    <IonModal
      isOpen={Visible}
      id={'modal-singlebuy'}
      swipeToClose={false}
      backdropDismiss={false}
    >
      <HeaderRegistro
        color={'var(--naranja-abi)'}
        botonIzquierdo={
          <span onClick={() => {
            setVisible(false)
            setUrl(null)
          }}>
            <FIcon.ChevronLeft size={30} color={'#ffffff'} />
          </span>
        }
        titulo={titulo}
      />

      <div style={{padding: 20}}>
        {!!Url && <iframe src={Url} height="100%" width="100%" style={{ border: 0, height: IframeHeight }} />}
      </div>

      {/* Fix gráfico */}
      <div>&nbsp;</div>
    </IonModal>
  )
})

export default ModalSinglebuy
