import './ModalDireccion.css'

import { IonFooter, IonModal, IonSpinner } from '@ionic/react'
import React, { useEffect, useRef, useState } from 'react'
import { observer } from 'mobx-react-lite'
import Leaflet, { LatLngTuple } from 'leaflet'
import { useStores } from '../models/root-store/root-store-context'
import { AdminItemField, HeaderRegistro, SelectorModal, SMap } from '../components'
import { autocompletar_calles, setearSoloCalle, useApiToaster, useToaster } from '../helpers-ts'
import { Geolocation } from '@capacitor/geolocation'
import MapaSelectorModal from './MapaSelectorModal'
import * as FIcon from 'react-feather'

Leaflet.Icon.Default.imagePath = 'images/'

type ModalDireccionProps = {
  visible: boolean
  direccion?: any
  successCallback: any
}

export const ModalDireccion: React.FC<ModalDireccionProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Ready, setReady] = useState(false) // Fix de problema de render de mapa
  const [Loading, setLoading] = useState(true)
  const [Errors, setErrors] = useState({} as any)
  const [Datos, setDatos] = useState({} as any)
  const [Visible, setVisible] = useState(false)
  const apiToaster = useApiToaster()
  const addToast = useToaster()

  // Ubicación
  const MapaRef = useRef() // Para el modal
  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 } as any)
  const [Lat, setLat] = useState<number | null>(null)
  const [Lng, setLng] = useState<number | null>(null)
  const [UbicacionUser, setUbicacionUser] = useState<LatLngTuple | null>(null)

  React.useEffect(() => {
    setVisible(props.visible)
  }, [props.visible])

  React.useEffect(() => {
    if (!props.direccion) {
      setDatos({})
      actualizarUbicacionUser()
      return
    }
    obtenerDireccion(props.direccion.id)
  }, [props.direccion])

  const setup = async () => {
    obtenerCiudades()
    //
    if (!props.direccion) {
      setLoading(false)
    }
    // Timeout para renderizar correctamente el mapa
    setTimeout(() => {
      setReady(true)
    }, 700)
  }

  useEffect(() => {
    // Evitamos usar useIonViewWillEnter porque se ejecuta cada vez que se entra a una vista
    setup()
  }, [])

  const obtenerDireccion = (direccion_id: number) => {
    setLoading(true)
    rootStore.environment.api.getDireccion(direccion_id).then((result) => {
      if (result.kind === 'ok') {
        const datos = { ...result.data } as any
        datos.calle_principal = { label: datos.calle_principal, value: datos.calle_principal }
        datos.calle_secundaria = { label: datos.calle_secundaria, value: datos.calle_secundaria }
        setDatos(datos)
        if (!!result.data.ubicacion) {
          const ubicacion = result.data.ubicacion.split(',')
          // @ts-ignore
          setLat(parseFloat(ubicacion[0]))
          // @ts-ignore
          setLng(parseFloat(ubicacion[1]))
          setCenter({ lat: parseFloat(ubicacion[0]), lng: parseFloat(ubicacion[1]) })
        }
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
      setLoading(false)
    })
  }

  useEffect(() => {
    if (rootStore.hasLocationPermission) {
      actualizarUbicacionUser().catch((error) => {
        console.log(error)

        if (String(error).includes('location disabled')) {
          addToast('La ubicación está desactivada.')
        }
      })
    }
  }, [rootStore.hasLocationPermission])

  const actualizarUbicacionUser = async () => {
    const res = await Geolocation.getCurrentPosition({
      enableHighAccuracy: true,
      timeout: 15000,
      maximumAge: 60 * 60 * 1000,
    })
    setUbicacionUser([res.coords.latitude, res.coords.longitude])
    setLat(res.coords.latitude)
    setLng(res.coords.longitude)
    setCenter({ lat: res.coords.latitude, lng: res.coords.longitude })
  }

  const obtenerCiudades = () => {
    if (!rootStore.ciudades.length) {
      setLoading(true)
      rootStore.actualizarDatosInicio(false).then(() => {
        setLoading(false)
      })
    }
  }

  const guardar = () => {
    setLoading(true)
    setErrors({})
    const ubicacion = Lat + ',' + Lng
    const calle_principal = Datos.calle_principal
    const calle_secundaria = Datos.calle_secundaria
    const datos = {
      ...Datos,
      ubicacion: ubicacion,
      calle_principal: calle_principal ? calle_principal.value : '',
      calle_secundaria: calle_secundaria ? calle_secundaria.value : '',
    }
    if (datos.ciudad && typeof datos.ciudad === 'object') {
      datos.ciudad = (datos.ciudad as any).id
    }
    datos.usuario = loginStore.id
    rootStore.environment.api.guardarDireccion(datos).then((result: any) => {
      apiToaster(result, 'Dirección guardada correctamente', 'guardar')
      if (result.kind === 'ok') {
        if (props.successCallback) {
          props.successCallback()
        }
        setVisible(false)
      } else if (result.kind === 'bad-data') {
        setErrors(result.errors)
      }
      setLoading(false)
    })
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const seleccionarUbicacion = (lat: any, lng: any) => {
    setLat(lat)
    setLng(lng)
    setCenter([lat, lng])
  }

  useEffect(() => {
    if ((Ready || !props.direccion) && Lat && Lng) {
      rootStore.environment.api.infoCalle(Lat, Lng).then((result: any) => {
        if (result.kind === 'ok') {
          if (result.response.info) {
            const i = result.response.info
            const dato: any = { ...Datos }
            if (i.calle) {
              dato.calle_principal = { value: i.calle, label: i.calle }
            }
            if (i.ciudad_obj) {
              dato.ciudad = rootStore.ciudades.find((c) => c.id === i.ciudad_obj.id)
            }
            if (i.secondary) {
              dato.calle_secundaria = { value: i.secondary, label: i.secondary }
            }
            setDatos(dato)
          }
        } else {
          console.log('error infoCalle', result)
        }
      })
    }
  }, [Lat, Lng])

  const dataSelectorModal = (data: any) => {
    setDato('ciudad', data[0])
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  if (!Visible) {
    return null
  }

  return (
    <IonModal isOpen={Visible} id={'modal-tarjeta'} swipeToClose={false} backdropDismiss={false}>
      <HeaderRegistro
        color={'var(--naranja-abi)'}
        botonIzquierdo={
          <span
            onClick={() => {
              setVisible(false)
            }}
          >
            <FIcon.ChevronLeft size={30} color={'#ffffff'} />
          </span>
        }
        titulo={`${Datos.id ? 'Editar' : 'Crear'} dirección`}
      />
      {!Ready && (
        <div
          style={{ display: 'flex', flexGrow: 1, alignSelf: 'stretch', justifyContent: 'center', alignItems: 'center' }}
        >
          <IonSpinner />
        </div>
      )}
      {Ready && (
        <div style={{ padding: 20, overflowY: 'auto' }}>
          <MapaSelectorModal ref={MapaRef} lat={Lat} lng={Lng} onAccept={seleccionarUbicacion} />
          <p>
            Ingrese la dirección de su casa o destino deseado para la entrega de los pedidos realizados en la
            plataforma.
          </p>
          <div className={'row'}>
            <AdminItemField
              deshabilitado={!Loading}
              titulo={'Nombre'}
              tipo={'texto'}
              campo={'nombre'}
              valor={Datos ? Datos.nombre : ''}
              error={Errors && Errors.nombre}
              set_dato={setDatoInput}
              columnas={12}
              columnas_sm={6}
            />
            <div className={'col-12'}>
              <div className={'row my-2'}>
                <div className={'col-12 col-lg-6'}>
                  <label className={'font-weight-bold text-primary'}>Seleccione la ubicación</label>
                </div>
                {Ready && (
                  <div className={'col-12'} style={{ height: 300, width: '100%', cursor: 'pointer' }}>
                    <SMap
                      lat={Lat}
                      lng={Lng}
                      zoom={Lat ? 16 : 14}
                      center={Center}
                      bloquear_interaccion
                      onClick={() => {
                        setTimeout(() => {
                          ;(MapaRef.current as any).open()
                        }, 300)
                      }}
                    />
                  </div>
                )}
              </div>
            </div>

            <AdminItemField
              deshabilitado={!Loading}
              titulo={'Calle principal'}
              tipo={'select2async'}
              campo={'calle_principal'}
              valor={Datos ? Datos.calle_principal : ''}
              error={Errors && Errors.calle_principal}
              set_dato={(campo: string, data: any) => {
                if (!data) {
                  return
                }
                setDato(campo, data)
              }}
              select_placeholder={'Calle principal'}
              select_onchange={(valor: any) => {
                setearSoloCalle(rootStore, setDato, setDatos, Datos, 'calle_principal', valor)
              }}
              opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
              select_no_options_text={'No se encuentran sugerencias'}
              columnas={12}
              columnas_sm={6}
            />
            <AdminItemField
              deshabilitado={!Loading}
              titulo={'Calle secundaria'}
              tipo={'select2async'}
              campo={'calle_secundaria'}
              valor={Datos ? Datos.calle_secundaria : ''}
              error={Errors && Errors.calle_secundaria}
              set_dato={(campo: string, data: any) => {
                if (!data) {
                  return
                }
                setDato(campo, data)
              }}
              select_placeholder={'Calle secundaria'}
              select_onchange={(valor: any) => {
                setearSoloCalle(rootStore, setDato, setDatos, Datos, 'calle_secundaria', valor)
              }}
              opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
              select_no_options_text={'No se encuentran sugerencias'}
              columnas={12}
              columnas_sm={6}
            />
            <AdminItemField
              deshabilitado={!Loading}
              titulo={'Número de casa'}
              tipo={'texto'}
              campo={'numero'}
              valor={Datos ? Datos.numero : ''}
              error={Errors && Errors.numero}
              set_dato={setDatoInput}
              columnas={12}
              columnas_sm={6}
            />
            <AdminItemField
              deshabilitado={!Loading}
              titulo={'Referencia'}
              tipo={'textarea'}
              campo={'referencia'}
              valor={Datos ? Datos.referencia : ''}
              error={Errors && Errors.referencia}
              set_dato={setDatoInput}
              columnas={12}
            />
          </div>
          <SelectorModal
            titulo={'Ciudad'}
            titulo_label={'Seleccioná la ciudad'}
            listado_elementos={rootStore.ciudades}
            // @ts-ignore
            listado_elementos_seleccionados={Datos['ciudad']}
            datos_filtrados={dataSelectorModal}
            multiple={false}
            llave_label_opcion={'nombre'}
          />
          {Errors.ciudad && <div className={'text-danger'}>{Errors.ciudad}</div>}
          <div className={'form-group'}>
            <label className={'font-weight-bold text-gray'}>Elegí un icono distintivo</label>
            <div className="campo-registro-abi" style={{ width: '70%', marginTop: 10, marginBottom: 10 }}>
              <div
                onClick={() => setDato('icono', 'casa')}
                className={Datos.icono === 'casa' ? 'icono-direccion-seleccionado' : 'icono-direccion'}
              >
                <img
                  src={'assets/icon/casa.png'}
                  alt={'casa'}
                  className={Datos.icono === 'casa' ? 'filter-invert-imagen' : ''}
                  height={32}
                />
              </div>
              <div
                onClick={() => setDato('icono', 'empresa')}
                className={Datos.icono === 'empresa' ? 'icono-direccion-seleccionado' : 'icono-direccion'}
              >
                <img
                  src={'assets/icon/empresa.png'}
                  alt={'empresa'}
                  height={32}
                  className={Datos.icono === 'empresa' ? 'filter-invert-imagen' : ''}
                />
              </div>
              <div
                onClick={() => setDato('icono', 'maletin')}
                className={Datos.icono === 'maletin' ? 'icono-direccion-seleccionado' : 'icono-direccion'}
              >
                <img
                  src={'assets/icon/maletin.png'}
                  alt={'maletin'}
                  className={Datos.icono === 'maletin' ? 'filter-invert-imagen' : ''}
                  height={32}
                />
              </div>
              <div
                onClick={() => setDato('icono', 'corazon')}
                className={Datos.icono === 'corazon' ? 'icono-direccion-seleccionado' : 'icono-direccion'}
              >
                <img
                  src={'assets/icon/corazon.png'}
                  alt={'corazon'}
                  className={Datos.icono === 'corazon' ? 'filter-invert-imagen' : ''}
                  height={32}
                />
              </div>
              <div
                onClick={() => setDato('icono', 'alfiler')}
                className={Datos.icono === 'alfiler' ? 'icono-direccion-seleccionado' : 'icono-direccion'}
              >
                <img
                  src={'assets/icon/alfiler.png'}
                  alt={'alfiler'}
                  className={Datos.icono === 'alfiler' ? 'filter-invert-imagen' : ''}
                  height={32}
                />
              </div>
            </div>
          </div>
          <div className={'form-group'}>
            <input type={'checkbox'} id={'chk-default'} checked={Datos.default} />
            <label
              onClick={(evt) => {
                // @ts-ignore
                setDato('default', !Datos.default)
              }}
            >
              Dirección por defecto
            </label>
          </div>
        </div>
      )}
      <IonFooter>
        <div className={'d-flex'}>
          <button
            className={'btn btn-secondary btn-block py-2'}
            style={{ borderRadius: 0 }}
            onClick={() => setVisible(false)}
          >
            Cancelar
          </button>
          <button
            className={'btn btn-primary btn-block py-2 mt-0'}
            style={{ borderRadius: 0 }}
            disabled={Loading}
            onClick={guardar}
          >
            {Loading && <IonSpinner name="crescent" />}
            {!Loading && <span>Guardar</span>}
          </button>
        </div>
      </IonFooter>
    </IonModal>
  )
})

export default ModalDireccion
