import React from 'react';
import {observer} from "mobx-react-lite";
import {useHistory} from "react-router-dom";
import * as FIcon from "react-feather";

type HeaderCompactoProps = {
  botonIzquierdo?: any
  botonDerecho?: any
};

export const HeaderCompacto: React.FC<HeaderCompactoProps> = observer((props) => {
  const history = useHistory()
  const botonIzquierdo = props.botonIzquierdo || <div style={{padding: 5, paddingLeft: 10}} onClick={() => history.goBack()}>
    <FIcon.ChevronLeft size={26} style={{color: '#FFF'}}/>
  </div>
  const botonDerecho = props.botonDerecho || <div style={{width: 46}} />

  return <div className={'header'}>
    <div
      className={'top'}
      style={{height: 55, display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}
    >
      {botonIzquierdo}
      <a onClick={() => history.push('/')}>
        <img src={'assets/abi-logo-white.png'} alt={'Logo de ABi'} style={{height: 30}}/>
      </a>
      {botonDerecho}
    </div>
  </div>
});

export default HeaderCompacto;
