import './FabricanteSidebar.css'

import { IonAlert, useIonViewWillEnter } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import { useHistory, useRouteMatch } from 'react-router-dom'
import { RUTAS, RUTAS_APP_FABRICANTE } from '../../constants'
import { cambiarModo, isMovil, useApiToaster } from '../../helpers-ts'
import { ICONOS } from '../../helpers-ts/iconos'
import { useStores } from '../../models/root-store/root-store-context'

type AdminSidebarProps = {}

export const FabricanteSidebar: React.FC<AdminSidebarProps> = observer(() => {
  const rootStore = useStores()
  const { loginStore, adminStore, transportistaStore } = rootStore
  const [showAlert, setShowAlert] = useState(false)
  const history = useHistory()
  const url = history.location.pathname
  const [Url, setUrl] = useState(url)
  const Movil = isMovil()
  const apiToaster = useApiToaster()

  const match = useRouteMatch()

  const url_matches_any = (...args: string[]) => {
    return Array.prototype.some.call(args, (value) => url.indexOf(value) !== -1)
  }

  const isAdministracionOpened = () => {
    return url_matches_any(
      'admin/configuracion',
      'admin/staff',
      'admin/tipo-vehiculo',
      RUTAS.ADMIN_RUBRO,
      RUTAS.ADMIN_CATEGORIA,
      RUTAS.ADMIN_PREGUNTAS_FRECUENTES,
    )
  }

  const isMovimientosOpened = () => {
    return url_matches_any('admin/pedido', 'admin/orden_venta', 'admin/hoja_ruta', RUTAS.ADMIN_BALANCE)
  }

  const isFacturacionOpened = () => {
    return url_matches_any(
      RUTAS.ADMIN_RENDICION,
      RUTAS.ADMIN_PAGO_PROVEEDOR,
      RUTAS.ADMIN_PAGO_CLIENTE,
      RUTAS.ADMIN_FACTURA_PROVEEDOR,
      RUTAS.ADMIN_FACTURA_CLIENTE,
    )
  }

  const isModeracionOpened = () => {
    return url_matches_any(
      'admin/driver/pendiente',
      'admin/proveedor/pendiente',
      'admin/producto/pendiente',
      'admin/vehiculo/pendiente',
    )
  }

  const matchtUrlRuta = (url: string) => {
    return url_matches_any(
      'admin/cliente',
      'admin/proveedor/moderado',
      'admin/driver/moderado',
      'admin/vehiculo/moderado',
    )
  }

  const isMaestrosOpened = () => {
    return url_matches_any(
      'admin/cliente',
      'admin/proveedor/moderado',
      'admin/driver/moderado',
      'admin/vehiculo/moderado',
    )
  }

  const isProductosOpened = () => {
    return url_matches_any('admin/precio', 'admin/inventario', 'admin/producto/moderado')
  }

  const isApiOpened = () => {
    return url_matches_any(RUTAS.ADMIN_API_CONFIG, RUTAS.ADMIN_API_VIEW, RUTAS.ADMIN_API_DOC, RUTAS.ADMIN_API_LOG)
  }

  const defaultValues = {
    administracion: isAdministracionOpened(),
    movimientos: isMovimientosOpened(),
    moderacion: isModeracionOpened(),
    facturacion: isFacturacionOpened(),
    maestros: isMaestrosOpened(),
    productos: isProductosOpened(),
    api: isApiOpened(),
  }

  const [opened, setOpened] = useState(defaultValues)

  const toggleOpened = (e: any) => {
    e.preventDefault()
    const openedC = { ...opened }
    const item = e.target.dataset.menu
    // @ts-ignore
    openedC[item] = !openedC[item]
    setOpened(openedC)
    return false
  }

  useEffect(() => {
    if (adminStore.sidebarType === 'full') {
      setOpened({
        administracion: isAdministracionOpened(),
        movimientos: isMovimientosOpened(),
        moderacion: isModeracionOpened(),
        facturacion: isFacturacionOpened(),
        maestros: isMaestrosOpened(),
        productos: isProductosOpened(),
        api: isApiOpened(),
      })
    } else {
      setOpened({
        administracion: false,
        movimientos: false,
        moderacion: false,
        facturacion: false,
        maestros: false,
        productos: false,
        api: false,
      })
    }
  }, [Url])

  useIonViewWillEnter(async () => {
    setUrl(history.location.pathname)
  })

  return (
    <>
      {!Movil && (
        <div id="sidebar-fabricante" className="col-12 col-md-3 col-lg-3 col-xl-2 mb-3">
          <div className="scroll-sidebar">
            <IonAlert
              isOpen={showAlert}
              onDidDismiss={() => setShowAlert(false)}
              header={'Cambiar a modo cliente'}
              message={'Confirma que desea cambiar a modo cliente?'}
              buttons={[
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'btn-secondary',
                },
                {
                  text: 'Cambiar',
                  cssClass: 'btn-primary',
                  handler: () => {
                    cambiarModo(rootStore, history, 10)
                  },
                },
              ]}
            />
            <nav className="sidebar-nav">
              <a
                className={url_matches_any('proveedor/pedidos/pendiente') ? 'resaltado' : ''}
                onClick={() => history.replace(RUTAS_APP_FABRICANTE.PEDIDOS_PENDIENTES)}
              >
                <div className="txt-dotsidebar">
                  Pendientes
                  {rootStore.pendientes > 0 && <span className="dot"></span>}
                </div>
              </a>
              <a
                className={url_matches_any('proveedor/pedidos/retiro') ? 'resaltado-naranja' : ''}
                onClick={() => history.replace(RUTAS_APP_FABRICANTE.PEDIDOS_RETIRO)}
              >
                <div className="txt-dotsidebar">
                  Retiro
                  {rootStore.retiro > 0 && <span className="dot"></span>}
                </div>
              </a>
              <a
                className={url_matches_any('proveedor/pedidos/todo') ? 'resaltado' : ''}
                onClick={() => history.replace(RUTAS_APP_FABRICANTE.PEDIDOS)}
              >
                <img src="assets/icon/todo.png" />
                <div>Todos</div>
              </a>
              <a
                className={
                  url_matches_any(RUTAS_APP_FABRICANTE.CATALOGO, RUTAS_APP_FABRICANTE.PRODUCTO_CREATE)
                    ? 'resaltado'
                    : ''
                }
                onClick={() => history.replace(RUTAS_APP_FABRICANTE.CATALOGO)}
              >
                {ICONOS.caja}
                <div>Catálogo</div>
              </a>

              <a
                className={url_matches_any('proveedor/balance') ? 'resaltado' : ''}
                onClick={() => history.replace(RUTAS_APP_FABRICANTE.BALANCE)}
              >
                {ICONOS.facturacion}
                <div>Balance</div>
              </a>
              <a
                className={url_matches_any(RUTAS_APP_FABRICANTE.VENTAS) ? 'resaltado' : ''}
                onClick={() => history.replace(RUTAS_APP_FABRICANTE.VENTAS)}
              >
                {ICONOS.movimientos}
                <div>Ventas</div>
              </a>

              <a
                className={
                  url_matches_any(
                    '/proveedor/configuracion',
                    '/proveedor/configuracion/api',
                    '/proveedor/configuracion/documentos',
                    '/proveedor/configuracion/horario',
                  )
                    ? 'resaltado'
                    : ''
                }
                onClick={() => history.replace(RUTAS_APP_FABRICANTE.CONFIGURACION)}
              >
                <img src="assets/icon/permisos.png" />
                <div>Configuración</div>
              </a>
            </nav>
          </div>
        </div>
      )}
    </>
  )
})

export default FabricanteSidebar
