import { Capacitor } from '@capacitor/core'
import { IonContent, IonPage, useIonViewWillEnter, useIonViewDidLeave } from '@ionic/react'
import React, { useEffect, useImperativeHandle, useRef, useState } from 'react'
import { Redirect, useHistory, useLocation } from 'react-router'
import { UpdateRequired } from '.'
import { BUILD } from '..'

import { RUTAS_APP, RUTAS_APP_FABRICANTE, RUTAS_WEB_TRANSPORTISTA } from '../constants'
import { actualizarDatosInicioFabricante } from '../helpers-ts'
import { useStores } from '../models/root-store/root-store-context'
import AdminHeader from './Admin/AdminHeader'
import AdminSidebar from './Admin/AdminSidebar'
import ChatInterno from './Chat/ChatInterno'
import FabricanteHeader from './Fabricante/FabricanteHeader'
import FabricanteSidebar from './Fabricante/FabricanteSidebar'
import Footer from './Footer'
import Header from './Header'
import RegistroCliente from './Registro/RegistroCliente'
import RegistroFabricante from './Registro/RegistroFabricante'
import RegistroTransportista from './Registro/RegistroTransportista'
import SelectorRegistro from './Registro/SelectorRegistro'
import SelectorModo from './SelectorModo'
import { TituloCustom } from './TituloCustom'

export interface MetaTag {
  name: string
  content: string
}

type PageProps = {
  children?: any
  header?: any
  footer?: any
  pageStyles?: any
  obtener_datos?: any
  login_required: boolean
  solo_cliente?: boolean
  solo_admin?: boolean
  admin?: boolean
  fabricante?: boolean
  titulo?: string
  ref?: any
  hide_padding_margin_top_movil?: boolean
  id?: string
  disableScroll?: boolean
  metaTags?: MetaTag[]
}

export const Page: React.FC<PageProps> = React.forwardRef((props, ref) => {
  const rootStore = useStores()
  const history = useHistory()
  const { loginStore } = rootStore
  const [modalRegistro, setModalRegistro] = useState<'login' | 'registro' | null>(null)
  const [modalRegistroClosable, setModalRegistroClosable] = useState(false)
  const [registroTransportista, setRegistroTransportista] = useState(false)
  const [registoFabricante, setRegistroFabricante] = useState(false)
  const [selectorRegistro, setSelectorRegistro] = useState(false)
  const [selectorModo, setSelectorModo] = useState(false)
  const [upgradeRequired, setUpgradeRequired] = useState(false)
  const Content = useRef<HTMLIonContentElement | null>(null)
  const divRef = useRef(null as any)
  const isFocused = useRef(true)
  const CapacitorMovil = Capacitor.getPlatform() !== 'web'
  let header = props.header || <Header obtener_datos={props.obtener_datos} />
  const footer = props.footer || CapacitorMovil ? null : <Footer content={Content} container={divRef} />
  const pageStyles = props.pageStyles || { display: 'flex' }
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const location = useLocation()

  const setMetaTag = (metaTag: MetaTag) => {
    const element = document.querySelector(`meta[name='${metaTag.name}']`)
    element && element.remove()
    //
    const meta = document.createElement('meta') as HTMLMetaElement;
    meta.name = metaTag.name;
    meta.content = metaTag.content;
    document.getElementsByTagName('head')[0].appendChild(meta);
  }

  useIonViewWillEnter(() => {
    isFocused.current = true
  })

  useIonViewDidLeave(() => {
    isFocused.current = false
  })

  useEffect(() => {
    rootStore.updateCurrentPath(location)
  }, [location])

  useEffect(() => {
    const metaTags = props.metaTags
    if (!metaTags) {
      return
    }
    metaTags.forEach(metaTag => setMetaTag(metaTag))
  }, [props.metaTags])

  useEffect(() => {
    if (!isFocused.current) {
      return
    }
    rootStore.setEsMovil(Movil)
    //
    let currentUrlParams = new URLSearchParams(window.location.search)
    const registro = currentUrlParams.get('registro') === '1'
    const login = currentUrlParams.get('login') === '1'
    const modal = registro ? 'registro' : login ? 'login' : null
    if (modal) {
      if (modalRegistro === null && currentUrlParams.get('i')) {
        currentUrlParams.delete('i')
        history.push(window.location.pathname + '?' + currentUrlParams.toString())
      }
      setModalRegistroClosable(true)
      setModalRegistro(modal)
    } else if (modalRegistro && !modal) {
      setModalRegistro(null)
    }
    //
    const registrot = currentUrlParams.get('registrot') === '1'
    setRegistroTransportista(registrot)
    //
    const registrop = currentUrlParams.get('registrop') === '1'
    setRegistroFabricante(registrop)
    //
    const selector = currentUrlParams.get('sregistro') === '1'
    //
    const selectorModo = currentUrlParams.get('smodo') === '1'
    //
    setSelectorRegistro(selector)
    setSelectorModo(selectorModo)
  }, [window.location.search])

  useEffect(() => {
    const upgradeRequired = rootStore.minBuild > BUILD
    if (Movil || Capacitor.getPlatform() !== 'web') {
      // quitar movil dp!!
      setUpgradeRequired(upgradeRequired)
    }
    if (props.login_required && !loginStore.isLoggedIn) {
      setModalRegistro('login')
    } else if (props.solo_admin !== undefined && props.solo_admin) {
      if (!loginStore.administrador) {
        alert('No posee permisos para acceder a la página')
        document.location.href = '/'
      }
    }
  }, [])

  useEffect(() => {
    if (loginStore.modo_uso_app === 20 && loginStore.fabricante_id) {
      // Esta como fabricante
      actualizarDatosInicioFabricante(rootStore)
    }
  }, [])

  useImperativeHandle(ref, () => ({
    scrollTop() {
      if (!Content.current || !divRef.current) {
        // No deberia ocurrir, pero ocurre
        return
      }
      Content.current.scrollToTop(100)
      divRef.current.scrollTop = 0
    },
  }))

  if (props.solo_cliente !== undefined && props.solo_cliente) {
    if (loginStore.isLoggedIn && loginStore.modo_uso_app === 30) {
      if (Movil) {
        return <Redirect to={{ pathname: RUTAS_APP.HOME }} />
      } else {
        return <Redirect to={{ pathname: RUTAS_WEB_TRANSPORTISTA.INICIO }} />
      }
    } else if (loginStore.isLoggedIn && loginStore.modo_uso_app === 20) {
      return <Redirect to={{ pathname: RUTAS_APP_FABRICANTE.PEDIDOS_PENDIENTES }} />
    }
  }

  // Para vistas admin
  if (props.admin !== undefined && props.admin) {
    return (
      <IonPage style={pageStyles}>
        {/* {ConfirmDialog} */}
        <TituloCustom titulo={props.titulo} />
        <div
          data-theme="light"
          data-layout="vertical"
          data-navbarbg="skin6"
          data-sidebartype={'full'}
          data-sidebar-position="fixed"
          data-header-position="fixed"
          data-boxed-layout="full"
          className={'full'}
          style={{ height: '100%' }}
          id={props.id ? props.id + ' main-wrapper' : 'main-wrapper'}
        >
          <AdminHeader />
          {props.children}
          <AdminSidebar />
        </div>
      </IonPage>
    )
  }

  // Para vistas Fabricante
  const haySidebar = !Movil && props.fabricante
  if (props.fabricante) {
    header = <FabricanteHeader />
  }

  // Para vistas del cliente
  return (
    <IonPage style={pageStyles}>
      {props.fabricante && <ChatInterno />}
      {rootStore.isDebug && <div className={'debug-box'}>DBG</div>}
      {modalRegistro !== null && (
        <RegistroCliente
          isOpen={true}
          setVisible={(visible: boolean) => {
            // Siempre es false visible
            let currentUrlParams = new URLSearchParams(window.location.search)
            currentUrlParams.delete('login')
            currentUrlParams.delete('registro')
            currentUrlParams.delete('i')
            history.push(window.location.pathname + '?' + currentUrlParams.toString())
          }}
          login={modalRegistro === 'login'}
          closable={modalRegistroClosable}
        />
      )}
      {upgradeRequired && <UpdateRequired />}
      {selectorRegistro && <SelectorRegistro />}
      {registroTransportista && <RegistroTransportista />}
      {registoFabricante && <RegistroFabricante />}
      {selectorModo && <SelectorModo />}
      <TituloCustom titulo={props.titulo} />
      <IonContent scrollEvents={true} ref={(ref) => (Content.current = ref)}>
        {header}

        {haySidebar && (
          <div className={'container-fluid'}>
            <div className={'row'}>
              <FabricanteSidebar />
              <div id={props.id} ref={divRef} className={'col-12 col-md-9 col-lg-9 col-xl-10'}>
                {props.children}
              </div>
            </div>
          </div>
        )}
        {!haySidebar && (
          <div id={props.id} ref={divRef}>
            {props.children}
          </div>
        )}
        {footer}
      </IonContent>
    </IonPage>
  )
})

export default Page
