import React from 'react';
import {observer} from "mobx-react-lite";
import * as FIcon from "react-feather";
import './HeaderApp.css'

type HeaderAppProps = {
  titulo: string
  direccion: any
  boton_derecha?: any
};

export const HeaderApp: React.FC<HeaderAppProps> = observer((props) => {

  return <div className={'header-body'}>
    <div className={'col-2'}>
      <FIcon.X size={30} onClick={props.direccion} color={'white'}/>
    </div>
    <div className={'col-8 px-0'}>
      <span className={'titulo-header-app'}>{props.titulo}</span>
    </div>
    {props.boton_derecha && <div className={'col-2'}>
      {props.boton_derecha}
    </div>}
  </div>

});

export default HeaderApp;
