import { IonContent, IonModal } from '@ionic/react'
import { LatLngLiteral } from 'leaflet'
import { observer } from 'mobx-react-lite'
import moment from 'moment'
import React, { useEffect, useState } from 'react'
import { Button } from 'react-bootstrap'
import { MapContainer, Marker, Polyline, Popup, TileLayer } from 'react-leaflet'
import { useHistory } from 'react-router'

import { AdminItemFieldRegistro } from '../../components'
import { RUTAS } from '../../constants'
import { ICONOS_ITINERARIO, useApiToaster, useConfirm, useToaster } from '../../helpers-ts'
import '../../helpers-ts/helpers-leaflet.css'
import { useStores } from '../../models/root-store/root-store-context'
import { OrdenVenta, Pedido as PedidoApi } from '../../services/api'
import { DATE_FORMAT } from '../../utils/date'
import './AdminModalRuteo.css'

interface RuteoRowProps {
  orden: OrdenVenta
  setFecha: any
  fecha: string
}
const RuteoRow: React.FC<RuteoRowProps> = (props) => {
  const [HoraRetiro, setHoraRetiro] = useState('')

  React.useEffect(() => {
    if (HoraRetiro !== '' && props.fecha !== '') {
      const fecha = moment(`${props.fecha} ${HoraRetiro}`, DATE_FORMAT.DATEHOURMINUTE).format(DATE_FORMAT.ISO)
      props.setFecha(fecha)
    } else if (HoraRetiro === '') {
      props.setFecha('')
    }
  }, [props.fecha, HoraRetiro])

  return (
    <tr key={'orden-' + props.orden.id}>
      <td>
        {props.orden.fabricante}
        <br />
        <small>{moment(props.orden.fecha_retiro).format(DATE_FORMAT.DATEHOURMINUTE)}</small>
      </td>
      <td>
        <div className="row">
          <AdminItemFieldRegistro
            deshabilitado={true}
            titulo={'hh:mm'}
            tipo={'hora'}
            campo={'hora'}
            valor={HoraRetiro}
            error={''}
            set_dato={(campo: string, value: string) => setHoraRetiro(value)}
            columnas={6}
            columnas_sm={6}
          />
        </div>
      </td>
    </tr>
  )
}

type AdminModalRuteoProps = {
  visible: boolean
  setVisible: any
  pedido: PedidoApi
}

interface ItinerarioItemData {
  orden_id: number
  fecha: string
}

interface HojaRutaData {
  pedido_id: number
  fecha_entrega: string
  itinerarios_retiro: ItinerarioItemData[]
}

export const AdminModalRuteo: React.FC<AdminModalRuteoProps> = observer((props) => {
  const rootStore = useStores()

  const [OrdenesRuteo, setOrdenesRuteo] = useState<OrdenVenta[]>()
  const [Itinerarios, setItinerarios] = useState<ItinerarioItemData[]>()

  const [FechaRetiro, setFechaRetiro] = useState('')
  const [HoraEntrega, setHoraEntrega] = useState('')

  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 })
  const [Ready, setReady] = useState(false)
  const [Polylines, setPolylines] = useState<Number[][] | null>([[-57.63591, -25.30066]])
  const [UbicacionCliente, setUbicacionCliente] = useState<LatLngLiteral>()

  const addToast = useToaster()
  const apiToaster = useApiToaster()
  const history = useHistory()

  React.useEffect(() => {
    const Pedido = props.pedido
    if (!!Pedido && !!Pedido.ordenes_venta && Pedido.ordenes_venta?.length > 0) {
      const ubicacionCliente = Pedido.direccion.ubicacion.split(',')
      setUbicacionCliente({ lat: Number(ubicacionCliente[0]), lng: Number(ubicacionCliente[1]) })
      const orden = [...Pedido.ordenes_venta]
      orden.sort((a, b) => {
        const fechaA = new Date(a.fecha_confirmado)
        const fechaB = new Date(b.fecha_confirmado)

        if (fechaA.getTime() < fechaB.getTime()) {
          return -1
        }
        return 1
      })
      setOrdenesRuteo(orden)
    }
  }, [props.pedido])

  React.useEffect(() => {
    if (!!OrdenesRuteo && OrdenesRuteo.length > 0) {
      const itinerarios: ItinerarioItemData[] = OrdenesRuteo.map((orden) => {
        return {
          orden_id: orden.id,
          fecha: '',
        }
      })
      setItinerarios(itinerarios)
    }
  }, [OrdenesRuteo])

  React.useEffect(() => {
    const timeoutId = setTimeout(() => {
      setReady(true)
    }, 1000)

    return () => clearTimeout(timeoutId)
  }, [])

  React.useEffect(() => {
    if (OrdenesRuteo && props.pedido && UbicacionCliente && Itinerarios) {
      const ordenes: OrdenVenta[] = []
      const itinerarios = [...Itinerarios]
      let horas_incompleta = false

      itinerarios.sort((a, b) => {
        if (a.fecha === '' || b.fecha === '') {
          horas_incompleta = true
        }
        const fechaA = new Date(a.fecha)
        const fechaB = new Date(b.fecha)

        if (fechaA.getTime() < fechaB.getTime()) {
          return -1
        }
        return 1
      })

      itinerarios.forEach((iter) => {
        const orden = OrdenesRuteo.find((orden) => orden.id === iter.orden_id)
        if (orden) {
          ordenes.push(orden)
        }
      })

      const ubicaciones = ordenes.map((orden) => {
        const ubi = orden.punto_retiro.ubicacion.split(',')
        return [Number(ubi[0]), Number(ubi[1])]
      })

      ubicaciones.push([UbicacionCliente.lat, UbicacionCliente.lng])

      if (FechaRetiro === '' || horas_incompleta) {
        setPolylines(null)
      } else {
        setPolylines(ubicaciones)
      }
    }
  }, [Itinerarios])

  React.useEffect(() => {
    if (OrdenesRuteo && props.pedido && UbicacionCliente) {
      const ubicaciones = OrdenesRuteo.map((orden) => {
        const ubi = orden.punto_retiro.ubicacion.split(',')
        return [Number(ubi[0]), Number(ubi[1])]
      })

      ubicaciones.push([UbicacionCliente.lat, UbicacionCliente.lng])

      if (FechaRetiro === '') {
        setPolylines(null)
      } else {
        setPolylines(ubicaciones)
      }

      const center = {
        lat: ubicaciones.reduce((suma, loc) => suma + loc[0], 0) / ubicaciones.length,
        lng: ubicaciones.reduce((suma, loc) => suma + loc[1], 0) / ubicaciones.length,
      }
      setCenter(center)
    }
  }, [OrdenesRuteo, UbicacionCliente])

  const guardarHoja = () => {
    const fecha_entrega = moment(`${FechaRetiro} ${HoraEntrega}`, DATE_FORMAT.DATEHOURMINUTE)
    let invalid = false

    Itinerarios?.forEach((iter) => {
      if (fecha_entrega.isBefore(iter.fecha)) {
        invalid = true
        addToast('La hora de entrega debe ser posterior a las demás.')
      }
    })

    if (!invalid && !!OrdenesRuteo && !!Itinerarios) {
      const hoja: HojaRutaData = {
        pedido_id: props.pedido.id,
        fecha_entrega: fecha_entrega.format(DATE_FORMAT.ISO),
        itinerarios_retiro: [...Itinerarios],
      }
      rootStore.environment.api.ruteoManualStaff(hoja).then((result) => {
        apiToaster(result, 'Pedido ruteado correctamente', 'rutear')
        if (result.kind === 'ok') {
          props.setVisible(false)
          history.push(RUTAS.ADMIN_PEDIDO_DETAIL(props.pedido.id) + '?r=true')
        } else if (result.kind === 'bad-data') {
          addToast('Fecha/hora errónea.')
        }
      })
    }
  }

  const [mostrarDialogo, ConfirmDialog] = useConfirm((additionalData: any) => {})

  return (
    <IonModal
      isOpen={props.visible}
      onDidDismiss={props.setVisible}
      swipeToClose={true}
      backdropDismiss={true}
      cssClass={'modal-ruteo-pedido'}
    >
      {ConfirmDialog}
      <div className="content-container">
        <div className="container d-flex flex-column pt-3">
          <h3>Itinerario</h3>
          <div className="row my-2">
            <div className="col-4">
              <strong>Fecha</strong>
            </div>
            <AdminItemFieldRegistro
              deshabilitado={true}
              titulo={'dd/mm/aaaa'}
              tipo={'fecha'}
              campo={'fecha_retiro'}
              valor={FechaRetiro}
              error={''}
              set_dato={(campo: string, value: string) => setFechaRetiro(value)}
              columnas={6}
              columnas_sm={6}
            />
          </div>

          <IonContent style={{ flex: 1 }}>
            {OrdenesRuteo && OrdenesRuteo.length > 0 && (
              <div className="row">
                <div className="col-12">
                  <div className="card">
                    <div className="card-body">
                      <table className={'table'}>
                        <thead>
                          <tr>
                            <th>Paradas</th>
                            <th></th>
                          </tr>
                        </thead>
                        <tbody>
                          {!!Itinerarios &&
                            Itinerarios.length > 0 &&
                            OrdenesRuteo.map((orden, idx) => {
                              return (
                                <RuteoRow
                                  orden={orden}
                                  key={orden.id}
                                  setFecha={(fecha: string) => {
                                    const itinerarios = [...Itinerarios]
                                    itinerarios[idx].fecha = fecha
                                    setItinerarios(itinerarios)
                                  }}
                                  fecha={FechaRetiro}
                                />
                              )
                            })}
                          <tr>
                            <td>Cliente</td>
                            <td>
                              <div className="row">
                                <AdminItemFieldRegistro
                                  deshabilitado={true}
                                  titulo={'hh:mm'}
                                  tipo={'hora'}
                                  campo={'hora'}
                                  valor={HoraEntrega}
                                  error={''}
                                  set_dato={(campo: string, value: string) => setHoraEntrega(value)}
                                  columnas={6}
                                  columnas_sm={6}
                                />
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </IonContent>
          <div className="row justify-content-center">
            <Button className={'btn btn-light ml-3 mb-3'} onClick={props.setVisible}>
              Cancelar
            </Button>
            <Button className={'btn btn-primary ml-3 mb-3'} onClick={guardarHoja}>
              Guardar
            </Button>
          </div>
        </div>
        <div>
          {Ready && (
            <MapContainer
              zoom={13}
              center={Center}
              style={{ height: '100%', width: '100%' }}
              attributionControl={false}
            >
              <TileLayer
                attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.osm.org/{z}/{x}/{y}.png"
              />
              {!!Polylines && (
                <Polyline
                  // @ts-ignore
                  positions={Polylines}
                />
              )}

              {!!OrdenesRuteo &&
                OrdenesRuteo?.length > 0 &&
                OrdenesRuteo.map((orden) => (
                  <Marker
                    key={orden.id}
                    position={orden.punto_retiro.ubicacion.split(',') as any}
                    icon={ICONOS_ITINERARIO.fabricante}
                  >
                    <Popup className={'itinerario-popup'}>
                      <div className={'itinerario-columna-container'}>
                        <div className={'itinerario-columna1'}>
                          <span className={''}>{orden.fabricante}</span>
                        </div>
                        <div className={'itinerario-columna2'}>
                          <span>{orden.punto_retiro.nombre}</span>
                          <span>
                            {orden.punto_retiro.calle_principal} c/ {orden.punto_retiro.calle_secundaria}
                          </span>
                        </div>
                      </div>
                    </Popup>
                  </Marker>
                ))}

              {!!UbicacionCliente && !!props.pedido && (
                <Marker position={UbicacionCliente} icon={ICONOS_ITINERARIO.usuario}>
                  <Popup className={'itinerario-popup'}>
                    <div className={'itinerario-columna-container'}>
                      <div className={'itinerario-columna1'}>
                        <span className={''}>
                          {props.pedido.usuario.first_name} {props.pedido.usuario.last_name}
                        </span>
                      </div>
                      <div className={'itinerario-columna2'}>
                        <span>
                          {props.pedido.direccion.calle_principal} c/ {props.pedido.direccion.calle_secundaria}
                        </span>
                      </div>
                    </div>
                  </Popup>
                </Marker>
              )}
            </MapContainer>
          )}
        </div>
      </div>
    </IonModal>
  )
})

export default AdminModalRuteo
