import { IonAlert } from '@ionic/react'
import { Rating } from '@material-ui/lab'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { MdAddCircleOutline, MdRemoveCircleOutline } from 'react-icons/md'
import { useHistory } from 'react-router-dom'

import { RUTAS } from '../constants'
import { plata_fmt } from '../helpers'
import {formatTextoURL, useVerificarLoginEdad} from '../helpers-ts'
import { useStores } from '../models/root-store/root-store-context'

type ProductosListadoProps = {
  item: any
  index: number
  movil: boolean
  titulo: string
  extra?: any
  extra_css?: string
}

export const ProductosListado: React.FC<ProductosListadoProps> = observer((props) => {
  const history = useHistory()
  const rootStore = useStores()
  const { loginStore, carritoStore } = rootStore
  const anchoImagenes = !props.movil ? 200 : 120
  const minHeightItem = !props.movil ? 220 : 150
  const containerSyle = props.movil ? 'justify-content-flex-start mx-0 px-0' : 'mx-5 scroll-items-home'
  const extra_css = props.extra_css || 'my-5 '
  const [AlertBloqueo, setAlertBloqueo] = useState<boolean>(false)
  const [AlertBloqueoTitle, setAlertBloqueoTitle] = useState<string>('')
  const [AlertBloqueoMessage, setAlertBloqueoMessage] = useState<string>('')
  const [AlertBloqueoAction, setAlertBloqueoAction] = useState<any>(null)
  const [cantidadCarrito, setCantidadCarrito] = useState(1)
  const verificarEdadEstado = useVerificarLoginEdad()
  const producto: any = props.item

  const imagenProducto = producto.imagenes.length > 0 ? producto.imagenes[0].imagen : 'assets/abi-logo-producto.png'
  const addProductoCarrito = (producto: any) => {
    if (producto.no_apto_menores) {
      const estado = verificarEdadEstado()
      if (estado.case !== 10) {
        setAlertBloqueoTitle(estado.title)
        setAlertBloqueoMessage(estado.message)
        setAlertBloqueoAction(estado.action)
        setAlertBloqueo(true)
        return false
      }
    }
    let obj = carritoStore.productos.find((o) => o.id === producto.id)
    let productoCarrito
    if (obj) {
      carritoStore.sumarProducto(producto)
    } else {
      productoCarrito = {
        ...producto,
        cantidad: 1,
        fabricante: producto.fabricante.nombre,
        imagenes: producto.imagenes.length > 0 ? producto.imagenes[0].imagen : '',
        categoria: producto.categoria.nombre,
      }
      carritoStore.addProducto(productoCarrito)
    }
    if (loginStore.isLoggedIn) {
      rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null).then((result) => {
        if (result.kind === 'ok') {
        }
      })
    } else {
    }
  }

  const producto_detalle = (producto: any) => {
    history.push(RUTAS.PRODUCTO_DETAIL(producto.id, formatTextoURL(producto.nombre)))
  }

  return (
    <div className={''}>
      <IonAlert
        isOpen={AlertBloqueo}
        onDidDismiss={() => setAlertBloqueo(false)}
        header={'Acceso restringido'}
        message={AlertBloqueoMessage}
        buttons={
          AlertBloqueoAction
            ? [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
                {
                  text: AlertBloqueoTitle,
                  handler: AlertBloqueoAction,
                },
              ]
            : [
                {
                  text: 'Cancelar',
                  role: 'cancel',
                  cssClass: 'secondary',
                },
              ]
        }
      />

      <div className="container-tarjeta-producto">
        <div
          style={{
            display: 'flex',
            minHeight: minHeightItem,
            justifyContent: 'center',
            alignItems: 'center',
          }}
          onClick={() => producto_detalle(producto)}
        >
          <img className={'my-2'} src={imagenProducto} alt={'item'} style={{ height: 'auto', width: anchoImagenes }} />
        </div>
        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
          <div>
            <Rating name="read-only" size={'small'} value={producto.fabricante.rating} readOnly />
          </div>
          <span
            className={'sub-titulo-producto-inicio'}
            style={{
              whiteSpace: 'pre-wrap',
              display: '-webkit-box',
              WebkitBoxOrient: 'vertical',
              WebkitLineClamp: 2,
              overflow: 'hidden',
              textOverflow: 'ellipsis',
            }}
            onClick={() => producto_detalle(producto)}
          >
            {producto.nombre}
          </span>

          <p onClick={() => producto_detalle(producto)} className={'sub-titulo-producto-inicio-precio'}>
            {plata_fmt(producto.precio_mayorista)}
          </p>
        </div>
        <div style={{ display: 'flex', justifyContent: 'space-between', paddingLeft: 5, paddingRight: 5 }}>
          <div style={{ flex: 1, display: 'flex', justifyContent: 'flex-start' }}>
            <div onClick={() => addProductoCarrito(producto)}>
              <img src="assets/icon/corazon.png" className="icono-tarjeta-producto" />
            </div>
          </div>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <div onClick={() => setCantidadCarrito(cantidadCarrito + 1)}>
              <MdRemoveCircleOutline size={30} color={'var(--gris-claro-abi)'} />
            </div>

            <span style={{ marginLeft: 10, marginRight: 10 }}>{cantidadCarrito}</span>
            <div onClick={() => setCantidadCarrito(cantidadCarrito + 1)}>
              <MdAddCircleOutline size={30} color={'var(--gris-claro-abi)'} className="icono-add" />
            </div>
          </div>
          <div
            onClick={() => addProductoCarrito(producto)}
            style={{ flex: 1, display: 'flex', justifyContent: 'flex-end' }}
          >
            <img src="assets/icon/carrito-gray.png" className="icono-tarjeta-producto" />
          </div>
        </div>

        <br />
      </div>
    </div>
  )
})

export default ProductosListado
