import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { observer } from 'mobx-react-lite'
import { useStores } from '../../models/root-store/root-store-context'
import { useHistory } from 'react-router-dom'
import { IonAlert } from '@ionic/react'
import { RUTAS, RUTAS_APP_FABRICANTE, RUTAS_WEB_TRANSPORTISTA } from '../../constants'
import { useClearStore, useToaster } from '../../helpers-ts'

type AdminHeaderProps = {}

export const AdminHeader: React.FC<AdminHeaderProps> = observer(() => {
  const rootStore = useStores()
  const { adminStore, loginStore } = rootStore
  const history = useHistory()
  const [Alert, setAlert] = useState(false)
  const addToast = useToaster()
  const clearStore = useClearStore()

  // const toggleSidebarType = (event: any) => {
  //   adminStore.setSidebarType(adminStore.sidebarType === 'full' ? 'mini-sidebar' : 'full')
  // }

  const cerrarSesion = () => {
    addToast('Sesión finalizada', 'dark')
    clearStore()
    document.location.href = '/'
  }
  const verPerfil = () => {
    if (loginStore.fabricante) {
      // @ts-ignore
      history.push({
        pathname: '/admin/staff/detail',
        origen: 'header',
      })
    } else {
      history.push('/admin')
    }
  }

  const LinkLogo = (evt: any) => {
    evt.preventDefault()
    if (document.location.pathname == RUTAS.ADMIN) {
      document.location.reload()
    }
    if (loginStore.administrador) {
      history.push(RUTAS.ADMIN)
    } else if (loginStore.modo_uso_app === 20) {
      history.push(RUTAS_APP_FABRICANTE.PERFIL)
    } else if (loginStore.modo_uso_app === 10) {
      history.push(RUTAS.CLIENTE)
    } else {
      history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)
    }
  }

  return (
    <header className="topbar" style={{ zIndex: 500 }}>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={'Cerrar sesión'}
        message={'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => cerrarSesion(),
          },
        ]}
      />
      <nav className="navbar top-navbar navbar-expand-md navbar-light">
        <div className="navbar-header">
          <a href={RUTAS.ADMIN} className="navbar-brand" onClick={LinkLogo}>
            <b>
              <img src={'assets/abi-v.svg'} alt="Inicio" className="dark-logo" height="30px" />
              <img src={'assets/abi-v.svg'} alt="Inicio" className="light-logo" height="30px" />
            </b>
            <span className="logo-text">
              <img src={'assets/abi-v.svg'} alt="Inicio" className="dark-logo" height="50px" />
            </span>
            {/* <div className="nav-toggler waves-effect waves-light d-block d-md-none" onClick={toggleSidebarType}>
              <FIcon.Menu color={'#000'} />
            </div> */}
          </a>
        </div>
        <div className="navbar-collapse collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto float-left">
            {/* <li className="nav-item">
              <div
                className="nav-link sidebartoggler d-none d-md-block waves-effect waves-dark"
                style={{ cursor: 'pointer' }}
                onClick={toggleSidebarType}
              >
                <FIcon.Menu />
              </div>
            </li> */}
          </ul>
          <ul className="navbar-nav my-lg-0">
            {loginStore.modo_uso_app !== 30 && (
              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle waves-effect waves-dark"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                  onClick={verPerfil}
                >
                  <div className="profile-pic-container">
                    <FIcon.User className="profile-pic" />
                  </div>
                </a>
                <div className="dropdown-menu mailbox dropdown-menu-right animated bounceInDown">
                  <ul className="dropdown-user list-style-none">
                    <li className="user-list">
                      <a className="px-3 py-2" href="#">
                        <FIcon.User className="profile-pic" style={{ width: 18 }} />
                        <i data-feather="user" className="feather-icon" />
                        {loginStore.username}
                      </a>
                    </li>
                  </ul>
                </div>
              </li>
            )}
            <li className="nav-item dropdown">
              <a
                className="nav-link dropdown-toggle waves-effect waves-dark"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
                onClick={() => setAlert(true)}
                style={{ cursor: 'pointer' }}
              >
                <div>
                  <FIcon.LogOut className="profile-pic" />
                </div>
              </a>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  )
})

export default AdminHeader
