import { IonModal } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect } from 'react'
import * as FIcon from 'react-feather'
import { useHistory, useLocation } from 'react-router'
import { Pedido as PedidoApi } from '../../services/api'
import './ChatModal.css'
import ChatPedido from './ChatPedido'

const { WSS_BASE_URL } = require('../../config/env')

type ChatModalProps = {
  visible: boolean
  setVisible: any
  pedido: PedidoApi
}

export const ChatModal: React.FC<ChatModalProps> = observer((props) => {
  const history = useHistory()
  const { search } = useLocation()

  // useEffect(() => {
  //   if (props.visible) {
  //     let currentUrlParams = new URLSearchParams(search)
  //     const id =  props.pedido.id
  //     currentUrlParams.set('chat','1')
  //     currentUrlParams.set('pedido',String(id))
  //     history.push(window.location.pathname + '?' + currentUrlParams)
  //   }
  // }, [props.visible])

  return (
    <IonModal
      isOpen={props.visible}
      id={'modal-chat'}
      onDidDismiss={() => {
        props.setVisible(false)
        let currentUrlParams = new URLSearchParams(search)
        currentUrlParams.delete('chat')
        currentUrlParams.delete('pedido')
        history.push(window.location.pathname + '?' + currentUrlParams)
      }}
      swipeToClose={true}
      backdropDismiss={true}
      cssClass={'modal-chat'}
    >
      <ChatPedido
        botonExtra={
          <a onClick={() => props.setVisible(false)} className="ml-2">
            <FIcon.X color="white" size={30} />
          </a>
        }
        pedido={props.pedido}
        containerStyle={{ flex: 1 }}
      />
    </IonModal>
  )
})
export default ChatModal
