import React, { useState } from 'react'
import { IonAlert, IonIcon, IonModal } from '@ionic/react'
import * as FIcon from 'react-feather'
import { observer } from 'mobx-react-lite'
import { useStores } from '../models/root-store/root-store-context'
import WhatsAppIcon from '@material-ui/icons/WhatsApp'
import FacebookIcon from '@material-ui/icons/Facebook'
import TwitterIcon from '@material-ui/icons/Twitter'
import InstagramIcon from '@material-ui/icons/Instagram'
import { BiUpArrow } from 'react-icons/bi'
import { useHistory } from 'react-router'
import { cambiarModo } from '../helpers-ts'
import { RUTAS, RUTAS_APP_FABRICANTE, RUTAS_WEB_TRANSPORTISTA, storeOutline } from '../constants'
import { RELEASE } from '../index'
import './../pages/Cliente/Home.css'
import { Span } from '@sentry/tracing'

const moment = require('moment-timezone')

type FooterProps = {
  content?: any
  container?: any
}

export const Footer: React.FC<FooterProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [showModal, setShowModal] = useState(false)
  const [TituloAlert, setTituloAlert] = useState('')
  const [TextoAlert, setTextoAlert] = useState('')
  const [FuncionAlert, setFuncionAlert] = useState('')
  const [Alert, setAlert] = useState(false)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const icono: React.CSSProperties = { width: 20, height: 20, marginLeft: 10 }
  const history = useHistory()

  const scrollToTop = () => {
    props.content && props.content.current.scrollToTop(100)
    props.container.current.scrollTop = 0
  }

  const verPerfil = () => {
    setShowModal(false)
    if (loginStore.administrador) {
      history.push(RUTAS.ADMIN)
    } else if (loginStore.modo_uso_app === 20) {
      history.push(RUTAS_APP_FABRICANTE.PERFIL)
    } else if (loginStore.modo_uso_app === 10) {
      if (!loginStore.perfil_completo && !loginStore.administrador) {
        history.push(RUTAS.CLIENTE)
      }
    } else {
      history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)
    }
  }

  return (
    <>
      <IonModal
        isOpen={showModal}
        cssClass="modal-menu-estilo"
        swipeToClose={true}
        backdropDismiss={true}
        onDidDismiss={() => setShowModal(false)}
      >
        <div className={'d-flex flex-column h-100 overflow-auto'}>
          <div className={'row w-100 mx-0 pb-5'} style={{ overflowX: 'hidden' }}>
            <div className={'col-12 mt-3 px-0'}>
              <p
                className={'mb-0 d-flex align-items-center'}
                style={{ color: 'white', fontWeight: 'bolder', marginLeft: 10 }}
              >
                {!loginStore.imagen_perfil && <FIcon.User size={25} />}
                {loginStore.imagen_perfil && (
                  <img
                    style={{ borderRadius: '50%', width: 50, height: 50 }}
                    alt="Imagen de perfil"
                    className={'imagen-perfil'}
                    src={loginStore.imagen_perfil}
                    onError={(event: any) => (event.target.style.display = 'none')}
                  />
                )}
                <strong
                  style={{
                    lineHeight: 1,
                    marginTop: 5,
                    marginLeft: 10,
                  }}
                >
                  HOLA {loginStore.isLoggedIn && loginStore.first_name}!
                </strong>
              </p>
              {loginStore.isLoggedIn && (
                <small className={'d-flex pt-2 text-white'} style={{ marginLeft: 10 }}>
                  Cliente
                </small>
              )}
              <hr className={'mt-3 mb-1'} style={{ borderColor: 'white' }} />
            </div>
            <div className={'col-12 mt-3 px-0'}>
              <a
                onClick={() => {
                  history.push(RUTAS.ROOT)
                  setShowModal(false)
                }}
                style={{ color: 'white', fontWeight: 'bolder', marginLeft: 10 }}
              >
                Inicio
              </a>
            </div>
            <div className={'col-12 mt-3 px-0'}>
              {loginStore.isLoggedIn ? (
                <a onClick={verPerfil} style={{ color: 'white', fontWeight: 'bolder', marginLeft: 10 }}>
                  {!loginStore.administrador
                    ? !loginStore.perfil_completo
                      ? 'COMPLETAR PERFIL'
                      : 'Mi perfil'
                    : 'Panel'}
                </a>
              ) : null}
            </div>
            {loginStore.isLoggedIn && (
              <div className={'col-12 mt-3 px-0'}>
                <a
                  onClick={() => {
                    history.push('/pedido')
                    setShowModal(false)
                  }}
                  style={{ color: 'white', fontWeight: 'bolder', marginLeft: 10 }}
                >
                  Pedidos
                </a>
              </div>
            )}
            {loginStore.isLoggedIn && (
              <div className={'col-12 mt-3 px-0'}>
                <a href={'#'} style={{ color: 'white', fontWeight: 'bolder', marginLeft: 10 }}>
                  Comprar de nuevo
                </a>
                <hr className={'mt-3 mb-1'} style={{ borderColor: 'white' }} />
              </div>
            )}

            <div className={'col-12 mt-3 px-0'}>
              <a href={'#'} style={{ color: 'white', fontWeight: 'bolder', marginLeft: 10 }}>
                Perfil
              </a>
              <div className={'row my-3 d-flex justify-content-center'}>
                <div
                  className={'col-6 text-center icono'}
                  onClick={() => {
                    if (loginStore.fabricante) {
                      setTituloAlert('Cambiar a modo fabricante')
                      setTextoAlert('Confirma que desea cambiar a modo fabricante?')
                      setFuncionAlert('cambiar-fabricante')
                      setAlert(true)
                    } else {
                      setShowModal(false)
                      history.push(RUTAS.REGISTRO_FABRICANTE)
                    }
                  }}
                >
                  <IonIcon icon={storeOutline} color={'light'} className={'d-block mx-auto'} style={{ fontSize: 28 }} />
                  <span style={{ color: '#FFF', fontSize: 14 }}>Proveedor</span>
                </div>
                <div
                  className={'col-6 text-center'}
                  onClick={() => {
                    if (loginStore.transportista) {
                      setAlert(true)
                      setTituloAlert('Cambiar a modo driver')
                      setTextoAlert('Confirma que desea cambiar a modo driver?')
                      setFuncionAlert('cambiar-transportista')
                    } else {
                      setShowModal(false)
                      //
                      let currentUrlParams = new URLSearchParams(window.location.search)
                      currentUrlParams.set('registrot', '1')
                      history.push(window.location.pathname + '?' + currentUrlParams.toString())
                    }
                  }}
                >
                  <FIcon.Truck size={26} className={'d-block mx-auto'} style={{ color: '#ffffff' }} />
                  <span style={{ color: '#FFF', fontSize: 14 }}>Driver</span>
                </div>
              </div>
              <hr className={'mt-3 mb-1'} style={{ borderColor: 'white' }} />
            </div>
          </div>
        </div>
      </IonModal>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TituloAlert !== '' ? TituloAlert : 'Cerrar sesión'}
        message={TextoAlert !== '' ? TextoAlert : 'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => {
              if (FuncionAlert === 'cambiar-fabricante') {
                cambiarModo(rootStore, history, 20)
              } else if (FuncionAlert === 'cambiar-transportista') {
                cambiarModo(rootStore, history, 30)
              }
            },
          },
        ]}
      />
      {!Movil && (
        <>
          <div id={'footer'} style={{ backgroundColor: '#efefef' }}>
            <div
              className={'scroll-to-top'}
              onClick={scrollToTop}
              style={{ backgroundColor: '#efefef', color: 'var(--gris-oscuro-abi)' }}
            >
              <div className="d-flex flex-column  align-items-center pb-3 pt-3">
                <BiUpArrow />
                <span className={'ml-2'}>Ir al inicio de la página</span>
              </div>
            </div>
            <div className="d-flex justify-content-center">
              <div style={{ height: 1, width: '90%', backgroundColor: 'var(--gris-oscuro-abi)' }} />
            </div>
            <div className={'footer p-0'}>
              <div className={'top'}>
                <div className="container pt-4">
                  <div className={'row pt-2'}>
                    <div className={'col-lg-4 col-xl-3 col-12'}>
                      <p>
                        <a href="#">
                          <span>Quiénes somos</span>
                          <img alt={'Quiénes somos'} style={icono} src={'assets/abi-logo-gray.png'} />
                        </a>
                      </p>

                      <p>
                        <a href="https://app.abi.com.py/terminos/">
                          <span>Condiciones de uso</span>
                          <img alt={'Condiciones de uso'} style={icono} src={'assets/icon/documento.png'} />
                        </a>
                      </p>
                      <p>
                        <a href="https://app.abi.com.py/terminos/">
                          <span>Políticas de privacidad</span>
                          <img alt={'Políticas de privacidad'} style={icono} src={'assets/icon/documento.png'} />
                        </a>
                      </p>
                      <p>
                        <a href={RUTAS.PREGUNTAS_FRECUENTES}>
                          <span>Preguntas frecuentes</span>
                          <img alt={'Preguntas frecuentes'} style={icono} src={'assets/icon/info.png'} />
                        </a>
                      </p>
                      <p>
                        <a href="https://wa.me/595981840840" target={'_blank'}>
                          <span>Contactanos</span>
                          <img alt={'Contactanos'} style={icono} src={'assets/icon/atencion-cliente.png'} />
                        </a>
                      </p>
                      <p>
                        <a href="#">
                          <span>Comentarios y Sugerencias</span>
                        </a>
                      </p>
                    </div>
                    <div className={'col-lg-4 col-xl-3 col-12'}>
                      <p>
                        <a
                          onClick={() => {
                            if (loginStore.isLoggedIn && loginStore.transportista) {
                              setAlert(true)
                              setTituloAlert('Cambiar a modo driver')
                              setTextoAlert('Confirma que desea cambiar a modo driver?')
                              setFuncionAlert('cambiar-transportista')
                            } else {
                              setShowModal(false)
                              //
                              let currentUrlParams = new URLSearchParams(window.location.search)
                              currentUrlParams.set('registrot', '1')
                              history.push(window.location.pathname + '?' + currentUrlParams.toString())
                            }
                          }}
                        >
                          Drivers
                        </a>
                      </p>
                      <p>
                        <a
                          onClick={() => {
                            if (loginStore.isLoggedIn && loginStore.fabricante) {
                              setTituloAlert('Cambiar a modo fabricante')
                              setTextoAlert('Confirma que desea cambiar a modo fabricante?')
                              setFuncionAlert('cambiar-fabricante')
                              setAlert(true)
                            } else {
                              setShowModal(false)
                              //
                              let currentUrlParams = new URLSearchParams(window.location.search)
                              currentUrlParams.set('registrop', '1')
                              history.push(window.location.pathname + '?' + currentUrlParams.toString())
                            }
                          }}
                        >
                          Proveedores
                        </a>
                      </p>
                      <p>
                        <a href="#">Marcas adheridas</a>
                      </p>
                      <p>
                        <a href="#">Áreas de cobertura</a>
                      </p>
                      <p>
                        <a href="#">Información Útil</a>
                      </p>
                      <p>
                        <a href="mailto:directorio@abi.com.py?subject=Quiero trabajar en ABi">Trabajá con nosotros</a>
                      </p>
                    </div>
                    <div className={'col-lg-4 col-xl-6 col-12 text-lg-right'}>
                      <p>
                        <a target={'_blank'} href="https://wa.me/595981840840">
                          <WhatsAppIcon style={{ marginRight: 5, marginBottom: 3, fontSize: 40 }} />
                          (0981) 840-840
                        </a>
                      </p>
                      <p>
                        <a href={'https://www.facebook.com/ABiparaguay'} target={'_blank'}>
                          <FacebookIcon style={{ marginRight: 5, marginBottom: 3, fontSize: 40 }} />
                        </a>
                        <a href={'https://twitter.com'} target={'_blank'}>
                          <TwitterIcon style={{ marginRight: 5, marginBottom: 3, fontSize: 40 }} />
                        </a>
                        <a href={'https://instagram.com/abiparaguay/'} target={'_blank'}>
                          <InstagramIcon style={{ marginRight: 5, marginBottom: 3, fontSize: 40 }} />
                        </a>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className={'footer p-0'}>
              <div className="d-flex justify-content-center" style={{ backgroundColor: '#efefef' }}>
                <div style={{ height: 1, width: '90%', backgroundColor: 'var(--gris-oscuro-abi)' }} />
              </div>
              <div className={'top pb-4 pb-lg-4'}>
                <div className={'container '}>
                  <div className={'row pt-3 justify-content-center'}>
                    <div className={''}>
                      <p className={'mb-0'}>ABi. © {moment().format('YYYY')} TODOS LOS DERECHOS RESERVADOS</p>
                      <div id={'release-container'}>v{RELEASE.split('@')[1]}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
    </>
  )
})

export default Footer
