import React from 'react'
import { isMovil } from '../helpers-ts'

interface FooterFormularioProps {
  style?: React.CSSProperties
  movil?: boolean
  className?: string
}

export const FooterFormulario: React.FC<FooterFormularioProps> = (props) => {
  const Movil = props.movil ? props.movil : isMovil()
  return (
    <div
      style={
        props.style
          ? props.style
          : {
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              backgroundColor: Movil ? 'var(--gris-claro)' : '#fff',
              marginBottom: 'env(safe-area-inset-bottom)',
              flexDirection: "column",
            }
      }
      className={'footer-formulario py-3 px-3 ' + props.className}
    >
      {props.children}
    </div>
  )
}

export default FooterFormulario
