import './ModalTarjeta.css'

import React, { useState } from 'react'
import { observer } from 'mobx-react-lite'
import { IonModal } from '@ionic/react'
import * as FIcon from 'react-feather'
import { HeaderRegistro } from './HeaderRegistro'
import {useToaster} from "../helpers-ts";

type ModalTarjetaProps = {
  visible: boolean
  url: string|null
  successCallback: any
}

export const ModalTarjeta: React.FC<ModalTarjetaProps> = observer((props) => {
  const addToast = useToaster()
  const [Url, setUrl] = useState(props.url)
  const [Visible, setVisible] = useState(false)
  const [IframeHeight, setIframeHeight] = useState(350)
  const [InfoVisible, setInfoVisible] = useState(false)

  React.useEffect(() => {
    setUrl(props.url)
  }, [props.url])

  React.useEffect(() => {
    setVisible(props.visible)
  }, [props.visible])

  const handleMessage = (t: { data: { message: string; details: any; iframeHeight: number } }) => {
    if (t.data.message && ['add_new_card_fail', 'add_new_card_success'].indexOf(t.data.message) !== -1) {
      if (t.data.message === 'add_new_card_fail') {
        alert(t.data.details)
      } else {
        addToast('Tarjeta agregada exitosamente', 'success')
        props.successCallback()
      }
      setVisible(false)
      setUrl(null)
    }
    setIframeHeight(t.data.iframeHeight + 1)
  }

  React.useEffect(() => {
    window.addEventListener('message', (event) => {
      handleMessage(event)
    })

    return () => {
      window.removeEventListener('message', (event) => {
        handleMessage(event)
      })
    }
  }, [])

  if (!Visible) {
    return null
  }


  return (
    <>
      <IonModal isOpen={Visible} id={'modal-agregar-tarjeta'} swipeToClose={false} backdropDismiss={false}>
        <HeaderRegistro
          color={'var(--naranja-abi)'}
          botonIzquierdo={
            <span
              onClick={() => {
                setVisible(false)
                setUrl(null)
              }}
            >
              <FIcon.ChevronLeft size={30} color={'#ffffff'} />
            </span>
          }
          titulo={'Registrar tarjeta'}
        />
        <div className={'texto-info'}>
          Introducí todos los datos de tu tarjeta, ¡dale guardar y empezá a hacer tus compras!
          <img
            alt={'Recomendaciones'}
            onClick={() => setInfoVisible(true)}
            className="icono-info-registro"
            src={'assets/icon/info.png'}
          />
        </div>

        <div style={{ padding: 20 }}>
          {!!Url && <iframe src={Url} height="100%" width="100%" style={{ border: 0, height: IframeHeight }} />}
        </div>

        {/* Fix gráfico */}
        <div>&nbsp;</div>
      </IonModal>

      <IonModal
        isOpen={InfoVisible}
        id={'info-tarjeta'}
        cssClass={'modal-info-tarjeta'}
        swipeToClose={false}
        backdropDismiss={false}
      >
        <>
          <div className="heather-info-registro background-color-naranja-abi">
            <div>
              <span>Datos de las tarjetas</span>
            </div>

            <div onClick={() => setInfoVisible(false)}>
              <FIcon.XCircle style={{ width: 26, marginRight: 10, color: '#fff' }} />
            </div>
          </div>
          <div
            className="sub-titulo-login-abi"
            style={{ height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}
          >
            <img alt={'Información'} src={'assets/info-datos-tarjeta.png'} style={{ padding: '24px' }} />
          </div>
        </>
      </IonModal>
    </>
  )
})

export default ModalTarjeta
