import './CalificacionTransportista.css'

import { IonAlert, IonModal } from '@ionic/react'
import withStyles from '@material-ui/core/styles/withStyles'
import { Rating } from '@material-ui/lab'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'

import { useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { getSnapshot } from 'mobx-state-tree'

const { BASE_URL } = require('../../config/env')

type CalificacionTransportistaProps = {
  visible: boolean
  onAceptar: any
  onCerrar: any
  color: string
}

const StyledRating = withStyles({
  iconFilled: {
    color: 'var(--naranja-abi)',
  },
  iconHover: {
    color: 'var(--naranja-abi)',
  },
})(Rating)

const labels: { [index: string]: string } = {
  1: 'Muy malo',
  2: 'Malo',
  3: 'Bueno',
  4: 'Muy bueno',
  5: 'Excelente!',
}

export const CalificacionTransportista: React.FC<CalificacionTransportistaProps> = observer((props: any) => {
  const rootStore = useStores()
  const [Alert, setAlert] = useState(false)
  const [TextAlert, setTextAlert] = useState('')
  const [TitleAlert, setTitleAlert] = useState('')
  const [value, setValue] = useState<number>(3)
  const [Comentario, setComentario] = useState<string>('')
  const [Movil, setMovil] = useState(false)
  const apiToast = useApiToaster()

  useEffect(() => {
    setValue(3)
    setComentario('')
  }, [props.visible])

  const alertAccion = () => {
    let objetivoCalificar = ''
    const pedidoCalificar = rootStore.pedidoCalificar
    if (pedidoCalificar && pedidoCalificar.tipo_calificacion === 20) {
      objetivoCalificar = 'la experiencia'
    } else if (pedidoCalificar && pedidoCalificar.tipo_calificacion === 10) {
      objetivoCalificar = 'al cliente'
    } else {
      objetivoCalificar = 'al driver'
    }
    setAlert(true)
    setTitleAlert('Calificar ' + objetivoCalificar)
    setTextAlert('Confirma que desea calificar ' + objetivoCalificar + '?')
  }

  const alertEjecutarAccion = () => {
    const calificacion = rootStore.pedidoCalificar
    if (calificacion) {
      rootStore.environment.api
        .calificar(calificacion.id_pedido, calificacion.tipo_calificacion, calificacion.id_calificar, value, Comentario)
        .then((result: any) => {
          apiToast(result, 'Calificación enviada exitosamente!', 'calificar')
        })
    }
    props.onAceptar()
  }

  return (
    rootStore.pedidoCalificar &&
    props.visible && (
      <div>
        <IonModal
          isOpen={props.visible}
          cssClass={!Movil ? 'modal-calificacion-transportista' : ''}
          onDidPresent={() => {
            setMovil(window.innerWidth <= 500)
          }}
          onDidDismiss={() => props.onCerrar()}
        >
          <IonAlert
            isOpen={Alert}
            onDidDismiss={() => setAlert(false)}
            header={TitleAlert}
            message={TextAlert}
            buttons={[
              {
                text: 'Cancelar',
                role: 'cancel',
                cssClass: 'secondary',
              },
              {
                text: 'Confirmar',
                handler: () => alertEjecutarAccion(),
              },
            ]}
          />
          <div
            className="calificaicion-header d-flex justify-content-between p-2 btn"
            style={{ backgroundColor: props.color }}
          >
            <span className={'text-truncate'} style={{ maxWidth: 300, color: '#fff', fontWeight: 'bolder' }}>
              {rootStore.pedidoCalificar.tipo_calificacion === 20 && 'Calificar experiencia de compra'}
              {rootStore.pedidoCalificar.tipo_calificacion === 10 && 'Calificar al cliente'}
              {rootStore.pedidoCalificar.tipo_calificacion === 30 && 'Calificar al driver'}
            </span>
            <FIcon.XCircle size={30} onClick={() => props.onCerrar()} color={'white'} />
          </div>

          <div className={'calificacion-cliente-container text-center'} style={{ flex: 1 }}>
            <div>
              <div className={'text-center'}>
                <span className={'nombre-cliente'}>
                  ¿Cómo fue tu experiencia con {rootStore.pedidoCalificar.nombre}?
                </span>
              </div>
              <StyledRating
                className={'mx-auto mt-2'}
                name="size-large"
                value={value}
                size="large"
                onChange={(event, newValue) => {
                  if (typeof newValue === 'number') {
                    setValue(newValue)
                  }
                }}
              />
            </div>

            {/* <div className={'mt-1'}>{value !== null && <h3 className={'text-center'}>{labels[value]}</h3>}</div> */}
            <div>
              <h3 style={{ textAlign: 'center', color: 'var(--gris-oscuro)' }}>Comentarios</h3>
              <textarea
                rows={5}
                placeholder={'Deje un comentario'}
                value={Comentario}
                className={''}
                style={{
                  backgroundColor: '#fff',
                  width: '80%',
                  border: '3px solid var(--gris-claro)',
                  borderRadius: 4,
                }}
                onChange={(e) => {
                  setComentario(e.target.value)
                }}
              />
            </div>

            <div className="row justify-content-center">
              <div className="col-6">
                <div className={'boton-calificar w-100'} onClick={() => alertAccion()}>
                  Valorar
                </div>
              </div>
            </div>
          </div>
        </IonModal>
      </div>
    )
  )
})

export default CalificacionTransportista
