import './Header.css'

import { IonSpinner } from '@ionic/react'
import MenuIcon from '@material-ui/icons/Menu'
import { observer } from 'mobx-react-lite'
import moment from 'moment'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { useHistory } from 'react-router-dom'

import { HomeSidebar } from '..'
import { RUTAS, RUTAS_APP_FABRICANTE, RUTAS_WEB_TRANSPORTISTA } from '../../constants'
import { cambiarModo, useToaster } from '../../helpers-ts'
import { ICONOS } from '../../helpers-ts/iconos'
import { useStores } from '../../models/root-store/root-store-context'

type HeaderProps = {
  datos_busqueda?: any
  datos_filtros?: any
  obtener_datos?: any
}

export const FabricanteHeader: React.FC<HeaderProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [showModal, setShowModal] = useState(false)
  // Tristate de notificaciones: hide => oculto, null => cargando, array => cargado
  const [Notificaciones, setNotificaciones] = useState('hide' as any)
  const history = useHistory()
  const Movil = /Mobi/.test(navigator.userAgent)
  const addToast = useToaster()
  const [TextoBusqueda, setTextoBusqueda] = useState('')

  React.useEffect(() => {
    let currentUrlParams = new URLSearchParams(window.location.search)
    if (currentUrlParams.get('s')) {
      setTextoBusqueda(currentUrlParams.get('s') || '')
    }
  }, [])

  const verPerfil = () => {
    setShowModal(false)
    if (loginStore.administrador) {
      history.push(RUTAS.ADMIN)
    } else if (loginStore.modo_uso_app === 20) {
      history.push(RUTAS_APP_FABRICANTE.PERFIL)
    } else if (loginStore.modo_uso_app === 10) {
      history.push(RUTAS.CLIENTE)
    } else {
      history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)
    }
  }

  const searchKeyDown = (evt: any) => {
    if (evt.keyCode === 13) {
      doSearch(evt.target.value.trim())
    }
  }

  const doSearch = (texto: any) => {
    history.push(`${RUTAS_APP_FABRICANTE.CATALOGO}${texto ? '?s=' + encodeURIComponent(texto) : ''}`)
  }

  const obtenerNotificaciones = async () => {
    const result = await rootStore.environment.api.simple_get(`/notificacion/`)
    if (result.kind === 'ok') {
      setNotificaciones(result.data.results)
    } else {
      addToast('No se pudo obtener las notificaciones', 'error')
    }
  }

  return (
    <div style={{ height: Movil ? 136 : 'auto' }}>
      {!Movil && (
        <>
          <div className={'header header-fabricante'} style={{ zIndex: 999, width: '100%', backgroundColor: '#FFF' }}>
            <div
              className=""
              style={{ display: 'flex', justifyContent: 'center', backgroundColor: 'var(--azul-fabricante)' }}
            >
              <div className="container-lg top-cliente">
                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-start' }}>
                  <div
                    className="boton-header-cliente"
                    onClick={() => {
                      if (loginStore.isLoggedIn && loginStore.transportista) {
                        history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)
                      } else {
                        let currentUrlParams = new URLSearchParams(window.location.search)
                        currentUrlParams.set('registrot', '1')
                        history.push(window.location.pathname + '?' + currentUrlParams.toString())
                      }
                    }}
                  >
                    <span className="icn-driver mr-2">{ICONOS.driver}</span>
                    <span className="texto-header">Driver</span>
                  </div>
                  <div
                    className={'boton-header-cliente'}
                    onClick={() => {
                      if (!!loginStore) {
                        cambiarModo(rootStore, history, 10)
                      } else {
                        history.push(RUTAS.ROOT)
                      }
                    }}
                  >
                    <span className="icn-proveedor">{ICONOS.tienda}</span>
                    <span className="texto-header">Tienda ABi</span>
                  </div>
                </div>
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-end',
                  }}
                >
                  <a className="boton-header-cliente">
                    <div id={'icon-notificacion'} style={{ height: 30, width: 30, position: 'relative' }}>
                      <span
                        className="icn-notificacion"
                        onClick={() => {
                          const mostrar = Notificaciones !== 'hide' ? 'hide' : null
                          setNotificaciones(mostrar)
                          if (mostrar !== 'hide') {
                            obtenerNotificaciones()
                          }
                        }}
                      >
                        {ICONOS.notificacion}
                        {rootStore.notificaciones > 0 && <span className="cantidad">{rootStore.notificaciones}</span>}
                      </span>
                      {Notificaciones !== 'hide' && (
                        <div className="notificaciones-container">
                          <div className="titulo">Notificaciones</div>
                          <div className="lista-notificaciones">
                            {Notificaciones === null && (
                              <div className="loader-container">
                                <IonSpinner name="crescent" className={'loadingSpinner'} />
                              </div>
                            )}
                            {Notificaciones !== null &&
                              Notificaciones.map((notificacion: any) => (
                                <div
                                  className="notificacion-container"
                                  onClick={() => history.push(RUTAS_APP_FABRICANTE.NOTIFICACIONES)}
                                >
                                  <img className="img" src="assets/icon/perfil.png" />
                                  <div className="titulo-fecha">
                                    <div className="titulo-notificacion">{notificacion.titulo}</div>
                                    <div className="fecha">{moment(notificacion.fecha).fromNow()}</div>
                                  </div>
                                </div>
                              ))}
                          </div>
                          <div
                            className="btn-ver-mas"
                            onClick={() => history.push(RUTAS_APP_FABRICANTE.NOTIFICACIONES)}
                          >
                            Ver más notificaciones
                          </div>
                        </div>
                      )}
                    </div>
                  </a>
                  {loginStore.isLoggedIn && (
                    <>
                      <a
                        onClick={verPerfil}
                        className="boton-header-cliente"
                        style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}
                      >
                        <div className="nombre-usuario-header">
                          {loginStore.first_name} {loginStore.last_name}
                        </div>
                        <div style={{ fontWeight: 'bold', fontSize: 12 }}>id#{loginStore.id}</div>
                      </a>
                      <a
                        className="centrado-flex"
                        onClick={verPerfil}
                        style={{
                          height: 50,
                          width: 50,
                          backgroundColor: '#fff',
                          borderRadius: '100%',
                          overflow: 'hidden',
                        }}
                      >
                        <img
                          alt={'Imagen de perfil'}
                          className={'imagen-perfil'}
                          src={loginStore.imagen_perfil ? loginStore.imagen_perfil : 'assets/icon/person-line.png'}
                          width={loginStore.imagen_perfil ? 50 : 30}
                          height={loginStore.imagen_perfil ? 50 : 30}
                          style={loginStore.imagen_perfil ? {} : { position: 'relative', bottom: -5 }}
                        />
                      </a>
                    </>
                  )}
                  {!loginStore.isLoggedIn && (
                    <>
                      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', paddingRight: 10 }}>
                        <a
                          onClick={() => {
                            let currentUrlParams = new URLSearchParams(window.location.search)
                            currentUrlParams.set('login', '1')
                            history.push(window.location.pathname + '?' + currentUrlParams.toString())
                          }}
                          style={{ fontSize: 15, fontWeight: 'bold' }}
                        >
                          <span className="texto-header">Iniciar Sesión</span>
                        </a>
                        <a
                          onClick={() => {
                            let currentUrlParams = new URLSearchParams(window.location.search)
                            currentUrlParams.set('registro', '1')
                            history.push(window.location.pathname + '?' + currentUrlParams.toString())
                          }}
                          style={{ fontSize: 12 }}
                        >
                          <span className="texto-header">Crear cuenta</span>
                        </a>
                      </div>
                      <div
                        className="centrado-flex"
                        style={{
                          height: 50,
                          width: 50,
                          backgroundColor: '#fff',
                          borderRadius: '100%',
                          overflow: 'hidden',
                        }}
                      >
                        <img
                          alt={'Usuario'}
                          src={'assets/icon/person-line.png'}
                          width={30}
                          height={30}
                          style={{ position: 'relative', bottom: -5 }}
                        />
                      </div>
                    </>
                  )}
                </div>
              </div>
            </div>
            <div className="container-lg" style={{ background: '#FFF' }}>
              <div className="mid-header-container" style={{ height: 110 }}>
                <div style={{ display: 'flex', justifyContent: 'flex-start' }}>
                  <a
                    title={'Ir al inicio'}
                    href={RUTAS_APP_FABRICANTE.PEDIDOS}
                    onClick={(evt) => {
                      evt.preventDefault()
                      history.push(RUTAS_APP_FABRICANTE.PEDIDOS)
                    }}
                  >
                    <img src={'assets/abi-v.svg'} alt={'Logo de ABi'} style={{ width: 70, height: 70 }} />
                  </a>
                </div>

                <div style={{ flex: 2, justifyContent: 'flex-end', display: 'flex', alignItems: 'center' }}>
                  <div className="input-group" style={{ marginRight: 20 }}>
                    <input
                      type="search"
                      className="form-control"
                      placeholder={'Escriba el nombre del producto'}
                      value={TextoBusqueda}
                      onChange={(evt) => {
                        setTextoBusqueda(evt.target.value)
                      }}
                      onKeyDown={searchKeyDown}
                    />
                    <div className="input-group-append">
                      <a className="btn btn-outline-secondary" type="button" onClick={() => doSearch(TextoBusqueda)}>
                        <FIcon.Search size={20} />
                      </a>
                    </div>
                  </div>
                </div>
                <div
                  style={{
                    height: '100%',
                    flex: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'flex-end',
                  }}
                >
                  <span className="cpointer" onClick={() => history.push(RUTAS_APP_FABRICANTE.HORARIO_ATENCION)}>
                    <img src={`assets/${rootStore.abierto ? 'open' : 'close'}.png`} width={'70px'} />
                  </span>
                </div>
              </div>
            </div>
          </div>
        </>
      )}
      {/* Header modo app*/}
      {Movil && (
        <>
          <div className={'header header-fabricante w-100'} style={{ position: 'fixed', zIndex: 999 }}>
            <div className="top-cliente-movil " style={{ backgroundColor: 'var(--azul-fabricante)' }}>
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'flex-start',
                  alignItems: 'center',
                  width: window.innerWidth / 2 - 42,
                }}
              >
                <div onClick={() => setShowModal(true)} className="centrado-flex">
                  <MenuIcon style={{ fontSize: 30 }} />
                </div>
              </div>

              <div className="logo-no-direccion-header-movil">
                <a className="boton-header-cliente logo-abi-fabricante" onClick={() => history.push(RUTAS.ROOT)}>
                  {ICONOS.abi}
                </a>
              </div>

              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'flex-end',
                  alignItems: 'center',
                  width: window.innerWidth / 2 - 42,
                }}
              >
                <span
                  className="cpointer boton-header-cliente"
                  onClick={() => history.push(RUTAS_APP_FABRICANTE.HORARIO_ATENCION)}
                >
                  <img src={`assets/${rootStore.abierto ? 'open' : 'close'}.png`} width={'35px'} />
                </span>
                <span
                  className="cpointer boton-header-cliente"
                  style={{ height: 30, width: 30, position: 'relative' }}
                  onClick={() => history.push(RUTAS_APP_FABRICANTE.NOTIFICACIONES)}
                >
                  <span className="icn-notificacion">
                    {ICONOS.notificacion}
                    {rootStore.notificaciones > 0 && <span className="cantidad">{rootStore.notificaciones}</span>}
                  </span>
                </span>
              </div>
            </div>
            <div className={'top-cliente-movil '} style={{ backgroundColor: 'var(--azul-fabricante)' }}>
              <div onClick={() => history.goBack()}>
                <FIcon.ChevronLeft />
              </div>
              <div className="input-group">
                <input
                  type="search"
                  className="form-control"
                  placeholder={'Escriba el nombre del producto'}
                  value={TextoBusqueda}
                  style={{}}
                  onChange={(evt) => {
                    setTextoBusqueda(evt.target.value)
                  }}
                  onClick={() => {
                    setTextoBusqueda('')
                  }}
                  onKeyDown={searchKeyDown}
                />
                <div className="input-group-append">
                  <button
                    className="btn btn-outline-secondary"
                    style={{ height: 38, backgroundColor: '#fff' }}
                    type="button"
                    onClick={() => doSearch(TextoBusqueda)}
                  >
                    <FIcon.Search size={20} style={{ color: '#4e4e4eff', marginBottom: 2 }} />
                  </button>
                </div>
              </div>
            </div>
          </div>
          <HomeSidebar
            visible={showModal}
            set_visible={(set: boolean) => {
              setShowModal(set)
            }}
            fabricante={loginStore.fabricante ? loginStore.fabricante : null}
          />
        </>
      )}
    </div>
  )
})

export default FabricanteHeader
