import React, {useEffect, useRef, useState} from 'react'
import { observer } from 'mobx-react-lite'
import './ModalVerificarPinFirebase.css'
import { IonModal } from '@ionic/react'
import * as FIcon from 'react-feather'
import { useStores } from '../models/root-store/root-store-context'
import { HeaderRegistro } from './HeaderRegistro'
import { nroTelefonoLocal } from '../helpers'
import { FooterFormulario } from '.'

type ModalVerificarPinFirebaseProps = {
  visible: boolean | null
  callback: any
  color?: string
  close?: any
  class_boton_aceptar?: string
  animate?: boolean
}

export const ModalVerificarPinFirebase: React.FC<ModalVerificarPinFirebaseProps> = observer((props) => {
  const rootStore = useStores()
  const { registroStore } = rootStore
  const [Pin, setPin] = useState('')
  const color = props.color || '#303AB2'
  const class_boton_aceptar = props.class_boton_aceptar || 'btn-outline-primary'
  const [Timer, setTimer] = useState(20)
  //
  const [DotVisible, setDotVisible] = useState(0)
  const DotVisibleRef = useRef(0)
  const dotInterval = useRef(null as any)

  useEffect(() => {
    if (!props.animate) {
      return
    }
    dotInterval.current = setInterval(() => {
      setDotVisible(DotVisibleRef.current + 1)
      DotVisibleRef.current = DotVisibleRef.current + 1
      if (DotVisibleRef.current > 6) {
        clearInterval(dotInterval.current)
      }
    }, 100)
  }, [props.animate])

  useEffect(() => {
    if (Timer > 0) {
      const timerId = setTimeout(() => setTimer(Timer - 1), 1000)
      return () => clearTimeout(timerId)
    }
  }, [Timer])

  const omitirPaso = () => {
    props.callback('-1')
  }

  return (
    <IonModal
      isOpen={!!props.visible}
      id={'modal-carrito'}
      swipeToClose={false}
      backdropDismiss={false}
      cssClass={'modal-codigo-firebase'}
    >
      <HeaderRegistro
        color={color}
        botonIzquierdo={
          <span onClick={() => props.callback(null)}>
            <FIcon.ChevronLeft size={30} color={'#ffffff'} />
          </span>
        }
        titulo={'Verifique su número'}
      />
      <div className={'text-center titulo'}>
        <span>Se ha enviado el código de verificación al número {nroTelefonoLocal(registroStore.nro_telefono)}</span>
      </div>
      <div
        className={'text-center d-flex align-self-center'}
        style={{position: 'relative', width: 270, height: 35}}
      >
        {DotVisible < 1 && 
          <div className="pin-container">
            <input
              type="number"
              pattern="\d*"
              className="pin-input"
              maxLength={6}
              value={Pin}
              onChange={(e: any) => {
                const value = e.target.value
                setPin(value ? value.substring(0, 6) : '')
              }}
            />
            <div className="lineas">
              <div className="linea"></div>
              <div className="linea"></div>
              <div className="linea"></div>
              <div className="linea"></div>
              <div className="linea"></div>
              <div className="linea"></div>
            </div>
          </div>
        }
        {DotVisible >= 1 && <div className={'input-dot'}>•</div>}
        {DotVisible >= 1 && <div className={'input-dot'} style={{left: 55}}>
          {DotVisible >= 2 ? '•' : ''}
        </div>}
        {DotVisible >= 1 && <div className={'input-dot'} style={{left: 100}}>
          {DotVisible >= 3 ? '•' : ''}
        </div>}
        {DotVisible >= 1 && <div className={'input-dot'} style={{left: 145}}>
          {DotVisible >= 4 ? '•' : ''}
        </div>}
        {DotVisible >= 1 && <div className={'input-dot'} style={{left: 190}}>
          {DotVisible >= 5 ? '•' : ''}
        </div>}
        {DotVisible >= 1 && <div className={'input-dot'} style={{left: 235}}>
          {DotVisible >= 6 ? '•' : ''}
        </div>}
      </div>

      <div
        style={{
          height: 200,
          display: 'flex',
          flexDirection: 'column',
          alignContent: 'center',
          justifyContent: 'space-evenly',
        }}
      >
        {!Timer && (
          <div style={{ textAlign: 'center' }}>
            <div className="sub-titulo-login-abi mt-3">¿No recibiste el código?</div>
            <div className="sub-titulo-login-abi">
              <div className={'py-3 cpointer'} onClick={() => props.close()} style={{ color: color }}>
                Solicitá un <span style={{ fontWeight: 'bold' }}> nuevo código</span>
              </div>
            </div>
          </div>
        )}

        <p className='cpointer text-center' onClick={omitirPaso}>Omitir este paso</p>

        <h5 style={{ textAlign: 'center' }}>
          <a style={{ fontWeight: 'bold', width: 'fit-content', color: color }} href="https://wa.me/595981840840">
            Contactanos
          </a>
        </h5>
      </div>
      <div className={'text-center mb-auto mt-3'}></div>
      <FooterFormulario className="py-4 px-5 ">
        <button
          style={color ? { backgroundColor: color, color: '#FFF' } : {}}
          className={class_boton_aceptar + ' boton-continuar'}
          onClick={() => props.callback(Pin)}
        >
          Continuar
        </button>
        </FooterFormulario>
    </IonModal>
  )
})

export default ModalVerificarPinFirebase
