import React, {useRef, useState} from 'react'
import { observer } from 'mobx-react-lite'
import { IonContent, IonHeader, IonModal, IonTitle, IonToolbar } from '@ionic/react'
import './VideoLoaderModal.css'
import { Capacitor } from '@capacitor/core'
import { Camera as NativeCamera } from '@ionic-native/camera'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary'
import { ModalInfo } from './ModalInfo'
import {useToaster} from "../helpers-ts";
import {Filesystem} from "@capacitor/filesystem";

type VideoLoaderModalProps = {
  titulo_label: string
  titulo_modal: string
  titulo_boton?: string
  modal_content?: any
  on_select: any
  preview?: string
  preview_css_class?: string
  color?: string
  class_boton_aceptar?: string
  extra_css_class?: string
  infoTitulo?: string
  infoBody?: any
  disabled?: boolean
}

export const VideoLoaderModal: React.FC<VideoLoaderModalProps> = observer((props) => {
  const [Loading, setLoading] = useState(false)
  const [Visible, setVisible] = useState(false)
  const [pathTemp, setPathTemp] = useState(null as any)
  const [AlertInfoImagen, setAlertInfoImagen] = useState(false)
  const fileInputRef = useRef(null as any)
  const color = props.color || '#303AB2'
  const class_boton_aceptar = props.class_boton_aceptar || 'btn-outline-primary'
  const toaster = useToaster()

  React.useEffect(() => {
    if (props.preview) {
      setPathTemp(props.preview)
    }
  }, [])

  const webVideoPicked = (evt: any) => {
    if (!evt.target.files.length) {
      return
    }
    const file = evt.target.files[0]
    if (file.size > 10485760) {
      toaster('El video no puede pesar más de 10 megabytes')
      return
    }
    const reader = new FileReader()
    reader.onload = function (e: any) {
      setPathTemp(e.target.result)
      setLoading(false)
    }
    reader.readAsDataURL(file)
  }

  const loadFile = async () => {
    setLoading(true)
    if (Capacitor.getPlatform() === 'web') {
      fileInputRef.current.click()
      return
    }
    let video = await NativeCamera.getPicture({
      sourceType: 2, // photo library
      destinationType: 1, // File uri
      mediaType: NativeCamera.MediaType.VIDEO,
    })
    if (video) {
      const prefix = Capacitor.getPlatform() === 'ios' ? '' : 'file://'
      if (!video.startsWith(prefix)) {
        video = prefix + video
      }
      const contents = await Filesystem.readFile({
        path: video,
      })
      video = `data:video/mp4;base64,${contents.data}`
    }
    setPathTemp(video)
    setLoading(false)
  }

  const confirmDialog = () => {
    setVisible(false)
    props.on_select(pathTemp)
  }

  const cancelDialog = () => {
    setVisible(false)
  }

  const openDialog = () => {
    setVisible(true)
  }

  let content = props.modal_content ? (
    props.modal_content
  ) : (
    <div className={'photoload-content-container p-2 mt-auto'}>
      <div className={'helptext-div px-2'}>
        <p>Todos los videos que serán publicadas en ABi, deben cumplir los siguientes estándares:</p>
        <div className={'row'}>
          <div className={'col-12 col-lg-4 text-center'}>
            <img src={'assets/icon/mp4.svg'} style={{width: 40}} />
            <div className={'mt-2'} style={{textAlign: 'center'}}>Archivos MP4/ H.264</div>
            <div className={'mt-lg-2'} style={{color: '#ffa300', fontWeight: 'bold', textAlign: 'center'}}>FORMATO</div>
          </div>
          <div className={'col-12 col-lg-4 mt-2 mt-lg-0 text-center'}>
            <div style={{textAlign: 'center', fontSize: 14}}>Aspecto: 640 x 360 px<br/>(ancho x alto)</div>
            <div className={'mt-2'}>Proporción 16:9</div>
            <div className={'mt-lg-2'} style={{color: '#ffa300', fontWeight: 'bold'}}>TAMAÑO</div>
          </div>
          <div className={'col-12 col-lg-4  mt-2 mt-lg-0 text-center'}>
            <img src={'assets/icon/sync-file.svg'} style={{width: 40}} />
            <div className={'mt-1'}>Máx. 10MB</div>
            <div className={'mt-lg-2'} style={{color: '#ffa300', fontWeight: 'bold'}}>PESO</div>
          </div>
          <div className={'col-12 mt-4'}>
            Duración mínima: 3 seg<br/>
            Duración máxima: 1 min<br/>
            Relación o aspecto del video: 16:9<br/>
            Formato: MP4/H.264<br/>
            Subtítulo de video: opcional, texto centrado, evitar ocupar los márgenes del material.<br/>
            Sonido de video: voz en off, músicas con permisos de copyright.
          </div>
        </div>
      </div>

      <div className={'helptext-div col-12 mt-3'}>
        <input type={"file"} accept={"video/mp4"} ref={fileInputRef} onChange={webVideoPicked} style={{display: 'none'}} />
        <button className={'btn ' + class_boton_aceptar + ' col-12 mt-2'} onClick={loadFile}>
          ELEGIR DE LA GALERÍA
        </button>
      </div>
    </div>
  )

  return (
    <>
      <div className={'form-group'}>
        {!!props.infoTitulo && !!props.infoBody && (
          <ModalInfo
            color={color}
            isOpen={AlertInfoImagen}
            close={() => setAlertInfoImagen(false)}
            titulo={props.infoTitulo}
            cssClass="requisitos-foto-perfil"
          >
            {props.infoBody}
          </ModalInfo>
        )}
        <IonModal isOpen={Visible} backdropDismiss={false}>
          <IonHeader>
            <IonToolbar>
              <IonTitle>{props.titulo_modal}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>{content}</IonContent>
          <div style={{ padding: 10 }}>
            <div className={'row'}>
              <div className={'col-6'}>
                <button className={'btn btn-outline-secondary btn-block'} onClick={cancelDialog}>
                  CANCELAR
                </button>
              </div>
              <div className={'col-6'}>
                <button
                  className={'btn ' + class_boton_aceptar + ' btn-block'}
                  onClick={confirmDialog}
                  disabled={pathTemp === '' || Loading}
                >
                  ACEPTAR
                </button>
              </div>
            </div>
          </div>
        </IonModal>
      </div>
      <>
        <div>
          <div
            style={{ borderColor: color, width: 640, height: 360, maxWidth: '100%' }}
            className={props.extra_css_class}
          >
            <video
              controls
              style={{width: 640, height: 360, maxWidth: '100%'}}
              className={
                props.preview_css_class ? props.preview_css_class : props.preview !== '' ? 'con-imagen' : 'sin-imagen'
              }
              src={!!pathTemp ? pathTemp : ''}
            />
          </div>
        </div>
        <div className="input-group mb-3 justify-content-center" style={{flexDirection: 'column'}}>
          {!props.disabled && <div className={'boton-photo-loader-registro d-flex flex-row align-items-center justify-content-center'}>
            {!pathTemp && <VideoLibraryIcon onClick={openDialog} style={{ fontSize: 22, color: '#67757c' }} />}
            {pathTemp && <CheckCircleIcon style={{ fontSize: 22, color: '#28a745' }} />}
            <span className={'mx-2'} onClick={openDialog} style={{ color: color }}>
              {props.titulo_label}
            </span>
            {props.titulo_boton && <div style={{ color: props.color }}>{props.titulo_boton}</div>}
          </div>}
          {pathTemp && <div className={'cpointer text-center'} onClick={() => {
            props.on_select(null)
            setPathTemp(null)
          }}>
            <strong>Eliminar</strong>
          </div>}
        </div>
      </>
    </>
  )
})

export default VideoLoaderModal
