import React, {useImperativeHandle, useState} from 'react'
import {IonContent, IonFooter, IonModal} from "@ionic/react"
import "./UppyModal.css"
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import '@uppy/image-editor/dist/style.css'
import {SMap} from "./SMap"
import {LatLngExpression} from "leaflet"

type MapaSelectorModalProps = {
  ref: any
  // location: LatLngExpression | null
  lat: number | null
  lng: number | null
  onAccept?: any
  userdata?: any
  onCancel?: any
  color?: string
}

export const MapaSelectorModal: React.FC<MapaSelectorModalProps> = React.forwardRef((props, ref) => {
  const [Visible, setVisible] = useState(false)
  const [Ready, setReady] = useState(false)
  const [Lat, setLat] = useState<number | null>(props.lat)
  const [Lng, setLng] = useState<number | null>(props.lng)
  const [Zoom, setZoom] = useState<number>(14)
  const [Center, setCenter] = useState<LatLngExpression | null>({
    lng: props.lat || -57.6359100,
    lat: props.lng || -25.3006600
  })
  const [OnAccept, setOnAccept] = useState(props.onAccept ? [props.onAccept] : null)
  const [UserData, setUserData] = useState(props.userdata ? [props.userdata] : null)
  const [OnCancel, setOnCancel] = useState(props.onCancel ? [props.onCancel] : null)
  const color = props.color || '#303AB2'

  const didPresent = () => {
    setTimeout(() => {
      setReady(true)
    }, 0)
  }

  useImperativeHandle(ref, () => ({
    open(onAccept: any, oncancel?: any, userdata?: any) {
      if (onAccept)
        setOnAccept([onAccept])
      if (oncancel)
        setOnCancel([oncancel])
      if (userdata)
        setUserData([userdata])

      if (props.lat && props.lng) {
        setZoom(16)
      }

      setLat(props.lat)
      setLng(props.lng)

      setVisible(true)
    },
    close() {
      cancelDialog()
    }
  }));

  const seleccionarUbicacion = (lat: number, lng: number) => {
    setLat(lat)
    setLng(lng)
    setCenter({lat, lng})
  }

  const cancelDialog = () => {
    setReady(false)
    setVisible(false)
    if (OnCancel)
      OnCancel[0](UserData ? UserData[0] : null)
  }

  const acceptDialog = () => {
    setVisible(false)
    setReady(false)
    if (OnAccept && Lat && Lng)
      OnAccept[0](Lat, Lng, UserData ? UserData[0] : null)
  }

  return <IonModal isOpen={Visible} onDidDismiss={cancelDialog} onDidPresent={didPresent}>
    <IonContent>
      {Ready && <SMap
        lat={Lat}
        lng={Lng}
        zoom={Zoom}
        center={Center}
        ubicacion={seleccionarUbicacion}
        full_screen
        color={color}
      />}
    </IonContent>
    <IonFooter>
      <div className={'d-flex'}>
        <button className={'btn btn-secondary btn-block py-2'} style={{borderRadius: 0}} onClick={cancelDialog}>
          Cancelar
        </button>
        <button
          className={'btn btn-primary btn-block py-2 mt-0'}
          style={{borderRadius: 0, backgroundColor: color, borderColor: color}} onClick={acceptDialog}>
          Confirmar
        </button>
      </div>
    </IonFooter>
  </IonModal>
})

export default MapaSelectorModal



