import React, {useImperativeHandle, useState} from 'react';
import {Dashboard} from "@uppy/react";
import {Uppy} from "@uppy/core";
import {IonContent, IonHeader, IonModal, IonTitle, IonToolbar} from "@ionic/react";
import "./UppyModal.css"
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import '@uppy/image-editor/dist/style.css'

const ImageEditor = require('@uppy/image-editor')
const Espanol = require('@uppy/locales/lib/es_ES')

type UppyModalProps = {
  ref: any
  titulo_modal: string
  onFileSelected?: any
  facingMode?: string
  userdata?: any
  onCancel?: any
  allowedFileTypes?: string[]
};

export const UppyModal: React.FC<UppyModalProps> = React.forwardRef((props, ref) => {
  const [Visible, setVisible] = useState(false)
  const facingMode = props.facingMode || 'user'
  const [FileSelected, setFileSelected] = useState(props.onFileSelected ? [props.onFileSelected] : null)
  const [UserData, setUserData] = useState(props.userdata ? [props.userdata] : null)
  const [OnCancel, setOnCancel] = useState(props.onCancel ? [props.onCancel] : null)

  useImperativeHandle(ref, () => ({
    open(file_selected: any, oncancel?: any, userdata?: any) {
      uppy.reset()
      if(file_selected)
        setFileSelected([file_selected])
      if(oncancel)
        setOnCancel([oncancel])
      if(userdata)
        setUserData([userdata])
      setVisible(true)
    },
    close() {
      cancelDialog()
    }
  }));

  const cancelDialog = () => {
    setVisible(false)
    if(OnCancel)
      OnCancel[0](UserData ? UserData[0] : null)
  }

  // uppy ///////////////////////////////////////////////////////////////////////////////
  const uppy = new Uppy({
    allowMultipleUploads: false,
    locale: Espanol,
    restrictions: {allowedFileTypes: props.allowedFileTypes || ['image/*'], maxNumberOfFiles: 1},
  })

  uppy.use(ImageEditor, {
    id: 'ImageEditor',
    quality: 0.8,
    cropperOptions: {
      viewMode: 1,
      background: false,
      autoCropArea: 1,
      responsive: true,
    },
  })

  uppy.on('complete', (result: any) => {
    if (result.successful.length >= 1){
      let data = result.successful[0]
      const file = uppy.getFile(data.id)

      if(FileSelected && file)
        FileSelected[0](URL.createObjectURL(file.data), file.preview, UserData ? UserData[0] : null)
    }
    setVisible(false)
  })

  uppy.on('cancel-all', (result: any) => {
    if(OnCancel)
      OnCancel[0](UserData ? UserData[0] : null)
    setVisible(false)
  })

  // uppy /////////////////////////////////////////

  return <IonModal isOpen={Visible} backdropDismiss={false}>
    <IonHeader>
      <IonToolbar>
        <IonTitle>{props.titulo_modal}</IonTitle>
      </IonToolbar>
    </IonHeader>

    <IonContent className={'uppyContentContainer'}>
      <Dashboard
        id={'uppy-uploader'}
        uppy={uppy}
        plugins={['ImageEditor']}
        metaFields={[{id: 'name', name: 'Nombre', placeholder: 'Nombre del archivo'},]}
        height={'100%'}
      />
    </IonContent>
    <div style={{padding: 10}}>
      <div className={'row'}>
        <div className={'col-12'}>
          <button
              className={'btn btn-secondary btn-block'}
              onClick={cancelDialog}>
            Cancelar
          </button>
        </div>
      </div>
    </div>
  </IonModal>
});

export default UppyModal;
