import React from 'react';
import {observer} from "mobx-react-lite";
import Toast from "react-bootstrap/Toast";
import * as FIcon from "react-feather";

type ToastCustomProps = {
  titulo?: string
  texto?: string
  visible: boolean
  setVisible: any
  duracion?: number
  tipo?: string
};

export const ToastCustom: React.FC<ToastCustomProps> = observer((props) => {

  const duracion = props.duracion || 5000
  let titulo = props.titulo || ''
  const tipo = props.tipo || 'warning'
  const texto = props.texto || 'Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos'

  let bgColor = '#ffffff'
  let textColor = '#000000'
  let iconColor = '#000000'

  switch (tipo) {
    case 'success':
      if (titulo === ''){
        titulo = 'Exito'
      }
      bgColor = '#dfffef'
      textColor = '#005837'
      iconColor = '#1DBF73'
      break
    case 'warning':
      if (titulo === ''){
        titulo = 'Atención'
      }
      bgColor = '#fdffe5'
      textColor = '#935604'
      iconColor = '#ff9100'
      break
    case 'danger':
      if (titulo === ''){
        titulo = 'Atención'
      }
      bgColor = '#ffebe5'
      textColor = '#c50000'
      iconColor = '#c50000'
      break
    case 'info':
      bgColor = '#ffffff'
      textColor = '#54577b'
      iconColor = '#2633d0'
      break
  }

  return <Toast
    style={{position: 'absolute', top: 10, right: 10, backgroundColor: bgColor, zIndex: 99999}}
    onClose={() => props.setVisible(false)}
    show={props.visible}
    delay={duracion}
    autohide
  >
    <Toast.Header>
      <strong className="mr-auto">{titulo}</strong>
    </Toast.Header>
    <Toast.Body>
      <div className={'row'}>
        <div className={'col-2 m-0 p-0 d-flex'}>
          {tipo === 'success' && <FIcon.CheckCircle className={'mx-auto my-auto'} color={iconColor} size={25}/>}
          {tipo === 'warning' && <FIcon.AlertTriangle className={'mx-auto my-auto'} color={iconColor} size={25}/>}
          {tipo === 'danger' && <FIcon.XCircle className={'mx-auto my-auto'} color={iconColor} size={25}/>}
          {tipo === 'info' && <FIcon.AlertCircle className={'mx-auto my-auto'} color={iconColor} size={25}/>}
        </div>
        <div className={tipo ? 'col-10 pl-0' : 'col-10'}>
          <span style={{color: textColor}}>{texto}</span>
        </div>
      </div>
    </Toast.Body>
  </Toast>
});

export default ToastCustom;
