import React, { useState } from 'react'
import { observer } from 'mobx-react-lite'
import { IonContent, IonHeader, IonModal, IonTitle, IonToolbar } from '@ionic/react'
import './PhotoLoaderModalRegistro.css'
import { Capacitor } from '@capacitor/core'
import { Camera as NativeCamera } from '@ionic-native/camera'
import { Camera, CameraDirection, CameraResultType, CameraSource } from '@capacitor/camera'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import { ModalInfo } from './ModalInfo'

type PhotoLoaderModalRegistroProps = {
  children?: any
  titulo_label: string
  titulo_modal: string
  helptext?: any
  titulo_boton?: string
  modal_content?: any
  img_path?: string
  on_select: any
  userdata?: any
  source?: number
  camera_direction?: number
  editor?: boolean
  dontUseWebInput?: boolean
  preview?: string
  preview_css_class?: string

  color?: string
  perfil: boolean
  class_boton_aceptar?: string
  extra_css_class?: string
  infoTitulo?: string
  infoBody?: any
  modal_info_css_class?: string
  disable?: boolean
  open_modal?: boolean
}

// source: 0 - Galeria / 1 - Camara

export const PhotoLoaderModalRegistro: React.FC<PhotoLoaderModalRegistroProps> = observer((props) => {
  const [Visible, setVisible] = useState(false)
  const [imgPath, setImgPath] = useState('')
  const [imgPathTemp, setImgPathTemp] = useState('')
  const [AlertInfoImagen, setAlertInfoImagen] = useState(false)
  let camera_direction = props.camera_direction ? props.camera_direction : 0 // rear por default
  const Movil = /Mobi/.test(navigator.userAgent)
  const color = props.color || '#303AB2'
  const class_boton_aceptar = props.class_boton_aceptar || 'btn-outline-primary'

  if (Capacitor.getPlatform() === 'web') {
    camera_direction = 1 // front
  }

  const launchCam = async () => {
    try {
      let image = await NativeCamera.getPicture({
        quality: 90,
        sourceType: 1, // camera
        destinationType: 1, // File uri
        allowEdit: false,
        correctOrientation: false,
        cameraDirection: camera_direction,
      })
      setImgPathTemp(image || '')
    } catch (err) {
      console.log(err)
    }
  }

  const loadFile = async () => {
    try {
      if (Capacitor.getPlatform() === 'web') {
        // Camera de capacitor tiene problemas con algunos teléfonos
        const capturedPhoto = await Camera.getPhoto({
          resultType: CameraResultType.Uri,
          source: CameraSource.Prompt,
          direction: camera_direction === 0 ? CameraDirection.Rear : CameraDirection.Front,
          allowEditing: props.editor == undefined ? true : props.editor,
          quality: 100,
          webUseInput: true, // usar dialogo nativo del OS
        })
        const path = capturedPhoto.webPath || ''
        setImgPathTemp(path)
        if (props.open_modal === false) {
          setVisible(false)
          setImgPath(path)
          props.on_select(path)
        }
        return
      }
      //
      let image = await NativeCamera.getPicture({
        quality: 90,
        sourceType: 2, // photo library
        destinationType: 1, // File uri
        allowEdit: false,
        correctOrientation: false,
      })
      if (image) {
        const prefix = Capacitor.getPlatform() === 'ios' ? '' : 'file://'
        if (!image.startsWith(prefix)) {
          image = prefix + image
        }
      }
      setImgPathTemp(image || '')
    } catch (err) {
      console.log(err)
    }
  }

  const confirmDialog = () => {
    setVisible(false)
    if (imgPathTemp) {
      setImgPath(imgPathTemp)
      props.on_select(imgPathTemp, props.userdata)
    }
  }

  const cancelDialog = () => {
    setVisible(false)
  }

  const openDialog = () => {
    if (props.open_modal === false && Capacitor.getPlatform() === 'web') {
      loadFile()
      return
    }
    setImgPathTemp(imgPath)
    setVisible(true)
  }

  let content = props.modal_content ? (
    props.modal_content
  ) : (
    <div className={'photoload-content-container p-2 mt-auto'}>
      <div className={'helptext-div col-12'}>{props.helptext}</div>

      <div className={'helptext-img-div mt-2 col-12'}>
        {props.img_path && (
          <img
            src={!!imgPathTemp ? Capacitor.convertFileSrc(imgPathTemp) : props.img_path}
            className={!!imgPathTemp ? 'helptext-img-div-img' : 'helptext-img-div-img-smaller'}
            width={150}
          />
        )}
      </div>

      <div className={'helptext-div col-12 mt-3'}>
        {Capacitor.getPlatform() !== 'web' && (props.source === undefined || props.source === 1) && (
          <button className={'btn ' + class_boton_aceptar + ' col-12'} onClick={launchCam}>
            SACAR FOTO
          </button>
        )}
        <button className={'btn ' + class_boton_aceptar + ' col-12 mt-2'} onClick={loadFile}>
          ELEGIR DE LA GALERÍA
        </button>
      </div>
    </div>
  )
  return (
    <>
      {!!props.infoTitulo && !!props.infoBody && !!props.modal_info_css_class && (
        <ModalInfo
          color={color}
          isOpen={AlertInfoImagen}
          close={() => setAlertInfoImagen(false)}
          titulo={props.infoTitulo}
          cssClass={`modal-info-image-loader ${props.modal_info_css_class}`}
        >
          {props.infoBody}
        </ModalInfo>
      )}
      <IonModal isOpen={Visible} backdropDismiss={false}>
        <IonHeader>
          <IonToolbar>
            <IonTitle>{props.titulo_modal}</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>{content}</IonContent>
        <div style={{ padding: 10 }}>
          <div className={'row'}>
            <div className={'col-6'}>
              <button className={'btn btn-outline-secondary btn-block'} onClick={cancelDialog}>
                CANCELAR
              </button>
            </div>
            <div className={'col-6'}>
              <button
                className={'btn ' + class_boton_aceptar + ' btn-block'}
                onClick={confirmDialog}
                disabled={imgPathTemp === ''}
              >
                ACEPTAR
              </button>
            </div>
          </div>
        </div>
      </IonModal>
      {props.perfil && (
        <>
          <div className={'d-flex justify-content-center align-items-center pt-2'}>
            <div
              style={{ borderColor: color }}
              className={
                props.extra_css_class
                  ? 'cpointer ' + props.extra_css_class
                  : 'cpointer circulo-contenedor-imagen-registro'
              }
              onClick={props.disable ? () => {} : openDialog}
            >
              <img
                alt={'Imagen de perfil'}
                style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                className={
                  props.preview_css_class ? props.preview_css_class : props.preview !== '' ? 'con-imagen' : 'sin-imagen'
                }
                src={props.preview !== '' ? props.preview : 'assets/icon/perfil.png'}
              />
            </div>
          </div>
          {props.titulo_label && (
            <div className="input-group mb-3">
              <div className={'boton-photo-loader-registro d-flex flex-row align-items-center justify-content-center'}>
                {!props.preview && (
                  <img onClick={props.disable ? () => {} : openDialog} alt={''} src={'assets/icon/camara.png'} />
                )}
                {props.preview && <CheckCircleIcon style={{ fontSize: 22, color: '#28a745' }} />}
                <span className={'mx-2'} onClick={props.disable ? () => {} : openDialog} style={{ color: color }}>
                  {props.titulo_label}
                </span>
                {props.titulo_boton ? (
                  <div style={{ color: props.color }}>{props.titulo_boton}</div>
                ) : (
                  <img onClick={() => setAlertInfoImagen(true)} alt={''} src={'assets/icon/info.png'} />
                )}
              </div>
            </div>
          )}
        </>
      )}
      {!props.perfil && !!props.children && (
        <span
          className={'boton-photo-loader-registro-no-perfil text-gray font-weight-bold mr-2'}
          onClick={props.disable ? () => {} : openDialog}
        >
          {props.children}
        </span>
      )}
      {!props.perfil && !props.children && (
        <>
          <div className={'d-flex align-items-center mb-3'}>
            <span
              className={'boton-photo-loader-registro-no-perfil text-gray font-weight-bold mr-2'}
              onClick={props.disable ? () => {} : openDialog}
            >
              {props.titulo_label}
            </span>
            {imgPathTemp && <CheckCircleIcon style={{ fontSize: 22, color: '#28a745' }} />}
            {!imgPathTemp && (
              <img
                className={'info-image'}
                onClick={() => setAlertInfoImagen(true)}
                alt={''}
                src={'assets/icon/info.png'}
              />
            )}
          </div>
        </>
      )}
    </>
  )
})

export default PhotoLoaderModalRegistro
