import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {
  IonCheckbox,
  IonContent,
  IonFooter,
  IonHeader,
  IonItem,
  IonLabel,
  IonList,
  IonModal,
  IonSearchbar,
  IonTitle,
  IonToolbar,
} from '@ionic/react'

type SelectorModalProps = {
  titulo: string
  titulo_label: string
  listado_elementos: any
  listado_elementos_seleccionados: any
  datos_filtrados: any
  multiple: boolean
  llave_label_opcion: string
  ocultar_input?: boolean
  mostrar_modal?: boolean
  mostrar_modal_cambio_estado?: any
  boton_extra_text?: string
  boton_extra_funcion?: any
  deshabilitado?: boolean | null
  auto_aceptar?: boolean
}

export const SelectorModal: React.FC<SelectorModalProps> = observer((props) => {
  const [Visible, setVisible] = useState(false)
  const [ItemsSeleccionados, setItemsSeleccionados] = useState<number[]>([])
  const [ItemsSeleccionadosText, setItemsSeleccionadosText] = useState<string[]>([])
  const [ItemsSearch, setItemsSearch] = useState([])
  const [searchText, setSearchText] = useState('')
  const auto_aceptar = props.auto_aceptar || false

  let deshabilitado = false
  if (props.deshabilitado !== undefined && props.deshabilitado !== null && !props.deshabilitado) {
    deshabilitado = true
  }

  const filtrarDatos = (value: string | undefined) => {
    if (typeof props.listado_elementos === 'function') {
      props.listado_elementos(value).then((result: any) => {
        if (result.kind === 'ok') {
          setItemsSearch(result.data.results)
        }
      })
    } else {
      let items_filtrar = []
      if (props.listado_elementos !== null) {
        items_filtrar = props.listado_elementos.filter((item: any) => {
          return item[props.llave_label_opcion] && value && item[props.llave_label_opcion].toLowerCase().includes(value.toLowerCase())
        })
      }
      if (value !== '') {
        setItemsSearch(items_filtrar)
      } else {
        setItemsSearch(props.listado_elementos)
      }
    }
  }

  const seleccionarItem = (value: any) => {
    value = parseInt(value)
    let items_seleccionados = [...ItemsSeleccionados]
    let items_seleccionadost = [...ItemsSeleccionadosText]
    // @ts-ignore
    const nombre = ItemsSearch.find((item) => item.id === value)[props.llave_label_opcion]
    const idx = items_seleccionados.indexOf(value)
    if (items_seleccionados.includes(value)) {
      items_seleccionados.splice(idx, 1)
      items_seleccionadost.splice(idx, 1)
    } else if (props.multiple) {
      items_seleccionados.push(value)
      // @ts-ignore
      items_seleccionadost.push(nombre)
    } else {
      items_seleccionados = [value]
      items_seleccionadost = [nombre]
    }
    setItemsSeleccionados(items_seleccionados)
    setItemsSeleccionadosText(items_seleccionadost)
    if (auto_aceptar) {
      setVisible(false)
      props.datos_filtrados(items_seleccionados)
    }
  }

  useEffect(() => {
    if (typeof props.listado_elementos_seleccionados === 'string') {
      setItemsSeleccionados(props.listado_elementos_seleccionados.split(',').map((el) => parseInt(el)))
    } else if (typeof props.listado_elementos_seleccionados === 'number') {
      setItemsSeleccionados([props.listado_elementos_seleccionados])
      const item = props.listado_elementos.find((value: any) => {
        return value.id === props.listado_elementos_seleccionados
      })
      if (item) {
        setItemsSeleccionadosText([item.nombre])
      }
    } else if (Array.isArray(props.listado_elementos_seleccionados)) {
      if (props.listado_elementos_seleccionados.length && props.listado_elementos_seleccionados[0].id) {
        setItemsSeleccionados(props.listado_elementos_seleccionados.map((value) => value.id))
        setItemsSeleccionadosText(props.listado_elementos_seleccionados.map((value) => value[props.llave_label_opcion]))
        props.datos_filtrados(props.listado_elementos_seleccionados.map((value) => value.id))
      } else {
        setItemsSeleccionados([])
        setItemsSeleccionadosText([])
      }
    } else if (!!props.listado_elementos_seleccionados && typeof props.listado_elementos_seleccionados === 'object') {
      setItemsSeleccionados([props.listado_elementos_seleccionados.id])
      setItemsSeleccionadosText([props.listado_elementos_seleccionados[props.llave_label_opcion]])
    }
    filtrarDatos('')
  }, [props.listado_elementos_seleccionados])

  useEffect(() => {
    if (props.mostrar_modal) {
      setVisible(true)
    }
    if (props.mostrar_modal_cambio_estado && !Visible) {
      props.mostrar_modal_cambio_estado(Visible)
    }
  }, [props.mostrar_modal, Visible])

  return (
    <div className='selector-modal'>
      <IonModal
        isOpen={Visible}
        onDidDismiss={() => {
          setSearchText('')
          filtrarDatos('')
        }}
        backdropDismiss={false}
      >
        <IonHeader>
          <IonToolbar>
            <IonTitle slot="start">{props.titulo}</IonTitle>
            {props.boton_extra_text !== undefined && (
              <IonTitle slot="end">
                <div
                  className={'text-primary cpointer'}
                  title={'Agregar ' + props.boton_extra_text}
                  onClick={() => {
                    props.boton_extra_funcion()
                  }}
                >
                  <small>Agregar</small>
                </div>
              </IonTitle>
            )}
          </IonToolbar>
          <IonToolbar>
            <IonSearchbar
              showCancelButton="focus"
              placeholder={'Buscar'}
              value={searchText}
              onIonChange={(e) => {
                filtrarDatos(e.detail.value)
                setSearchText(e.detail.value!)
              }}
            />
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            {ItemsSearch &&
              ItemsSearch.length > 0 &&
              ItemsSearch.map((item: any) => {
                return (
                  <IonItem key={'id-' + item.id}>
                    <IonLabel>{item[props.llave_label_opcion]}</IonLabel>
                    <IonCheckbox
                      slot="end"
                      value={item.id}
                      // @ts-ignore
                      checked={ItemsSeleccionados.includes(item.id)}
                      onClick={(e) => {
                        // @ts-ignore
                        seleccionarItem(e.target.value)
                      }}
                    />
                  </IonItem>
                )
              })}
          </IonList>
        </IonContent>
        <IonFooter>
          <div className={'d-flex'}>
            <button className={'btn btn-secondary btn-block py-2'} style={{borderRadius: 0}} onClick={() => setVisible(false)}>
              Cancelar
            </button>
            <button className={'btn btn-primary btn-block py-2 mt-0'} style={{borderRadius: 0}} onClick={() => {
              setVisible(!Visible)
              props.datos_filtrados(ItemsSeleccionados)
            }}>
              Aceptar
            </button>
          </div>
        </IonFooter>
      </IonModal>
      {props.ocultar_input === undefined && (
        <label className={'font-weight-bold text-gray'}>{props.titulo_label}</label>
      )}
      {props.ocultar_input === undefined && (
        <div className="input-group mb-3">
          <input type="text" className="form-control" readOnly value={ItemsSeleccionadosText.join(', ')} />
          <div className="input-group-append">
            <button
              disabled={deshabilitado}
              className="btn btn-outline-secondary"
              type="button"
              onClick={() => (!deshabilitado ? setVisible(!Visible) : null)}
            >
              Seleccionar
            </button>
          </div>
        </div>
      )}
    </div>
  )
})

export default SelectorModal
