import './ModalInfo.css'
import { IonContent, IonModal, IonPopover } from '@ionic/react'
import React from 'react'
import * as FIcon from 'react-feather'
import { observer } from 'mobx-react-lite'
import { Modal } from 'react-bootstrap'

interface ModalInfoProps {
  titulo: string
  close: any
  color?: string
  isOpen: boolean
  cssClass?: string
}

export const ModalInfo: React.FC<ModalInfoProps> = observer((props) => {
  const cssClass = props.cssClass ? props.cssClass : ''
  return (
    <IonModal isOpen={props.isOpen} onDidDismiss={() => props.close()} cssClass={'modal-info ' + cssClass}>
      <div
        className="header-modal-info"
        style={{ backgroundColor: !!props.color ? props.color : 'var(--naranja-abi)' }}
      >
        <div>
          <span>{props.titulo}</span>
        </div>

        <div onClick={() => props.close()}>
          <FIcon.XCircle style={{ width: 26, marginRight: 5, color: '#fff' }} />
        </div>
      </div>

      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          height: 'fit-content',
          width: '100%',
          backgroundColor: '#fff',
          borderBottomLeftRadius: '0.3rem',
          borderBottomRightRadius: '0.3rem',
        }}
      >
        <div className="modal-info-childen-container">{props.children}</div>
      </div>
    </IonModal>
  )
})
