import './SelectorModo.css'

import { IonAlert, IonModal } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { useHistory } from 'react-router'

import { RUTAS, RUTAS_APP_FABRICANTE } from '../constants'
import { cambiarModo, useClearStore } from '../helpers-ts'
import { useStores } from '../models/root-store/root-store-context'

export const SelectorModo: React.FC = observer((props) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const history = useHistory()
  const [Alert, setAlert] = useState(false)
  const [TituloAlert, setTituloAlert] = useState('')
  const [TextoAlert, setTextoAlert] = useState('')
  const [FuncionAlert, setFuncionAlert] = useState('')

  const goModoTienda = () => {
    setAlert(true)
    setTituloAlert('Cambiar a modo compras')
    setTextoAlert('Confirma que desea cambiar a modo compras?')
    setFuncionAlert('cambiar-usuario')
  }

  const goModoProveedor = () => {
    if (loginStore.isLoggedIn && loginStore.fabricante) {
      setAlert(true)
      setTituloAlert('Cambiar a modo proveedor')
      setTextoAlert('Confirma que desea cambiar a modo proveedor?')
      setFuncionAlert('cambiar-fabricante')
    } else {
      history.push(RUTAS_APP_FABRICANTE.HOME)
    }
  }

  const goModoDriver = () => {
    if (loginStore.isLoggedIn && loginStore.transportista) {
      setAlert(true)
      setTituloAlert('Cambiar a modo driver')
      setTextoAlert('Confirma que desea cambiar a modo driver?')
      setFuncionAlert('cambiar-transportista')
    } else {
      let currentUrlParams = new URLSearchParams(window.location.search)
      currentUrlParams.set('registrot', '1')
      history.push(window.location.pathname + '?' + currentUrlParams.toString())
    }
  }

  const goBack = () => {
    let currentUrlParams = new URLSearchParams(window.location.search)
    currentUrlParams.delete('smodo')
    currentUrlParams.delete('i')
    history.push(window.location.pathname + '?' + currentUrlParams.toString())
  }

  return (
    <IonModal isOpen={true} id={'modal-selector-modo'} swipeToClose={false} backdropDismiss={false}>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TituloAlert !== '' ? TituloAlert : 'Cerrar sesión'}
        message={TextoAlert !== '' ? TextoAlert : 'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => {
              if (FuncionAlert === 'cambiar-usuario') {
                cambiarModo(rootStore, history, 10)
              } else if (FuncionAlert === 'cambiar-transportista') {
                cambiarModo(rootStore, history, 30)
              } else if (FuncionAlert === 'cambiar-fabricante') {
                cambiarModo(rootStore, history, 20)
              } else {
                history.replace(RUTAS.LOGOUT)
              }
            },
          },
        ]}
      />
      <div>
        <div className="btn-cerrar" onClick={goBack}>
          <FIcon.XCircle color="var(--gris-oscuro-abi)" size={35} />
        </div>
        <div className="titulo-container">
          <div className="titulo">MODO DE USUARIO</div>
          <div className="subtitulo">¿Cómo querés ver ABi?</div>
        </div>
        <div className="row modos">
          <a className="mb-3 col-12 col-md-4" onClick={goModoTienda}>
            <img alt={'Modo tienda'} src={'assets/registro-cliente.jpg'} />
          </a>
          <a className="mb-3 col-12 col-md-4" onClick={goModoProveedor}>
            <img alt={'Modo proveedor'} src={'assets/registro-proveedor.jpg'} />
          </a>
          <a className="mb-3 col-12 col-md-4" onClick={goModoDriver}>
            <img alt={'Modo driver'} src={'assets/registro-driver.jpg'} />
          </a>
        </div>
      </div>
    </IonModal>
  )
})

export default SelectorModo
