import './CheckoutCarrito.css'

import { IonCheckbox, IonContent, IonInput, IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import { getSnapshot, SnapshotOut } from 'mobx-state-tree'
import moment from 'moment'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { BiCheckCircle, BiCircle } from 'react-icons/bi'
import InputMask from 'react-input-mask'
import { useHistory } from 'react-router'

import { ModalDireccion, ModalTarjeta, SelectorModal } from '..'
import { ICONOS_DIRECCION, RUTAS } from '../../constants'
import { plata_fmt } from '../../helpers'
import {
  format_numero_telefono_input, format_numero_telefono_verificar,
  iconoTarjeta,
  useApiToaster,
  useDireccionDefault,
  useToaster
} from '../../helpers-ts'
import { MEDIOS_PAGO } from '../../models/carrito-store/carrito-store'
import { useStores } from '../../models/root-store/root-store-context'
import { Direccion as DireccionModel } from '../../models/types'
import { Tarjeta } from '../../models/types/Tarjeta'
import { DatoFacturacion } from '../../services/api'
import { ModalInfo } from '../ModalInfo'
import ModalSinglebuy from '../ModalSinglebuy'
import ModalFacturacion from "../ModalFacturacion";

const numeral = require('numeral')

export interface CheckoutCarritoProps {
  index: 'metodo-envio' | 'tarjeta-direcion'
  setIndexCallback(index: 'metodo-envio' | 'tarjeta-direcion'): any
}


export const CheckoutCarrito: React.FC<CheckoutCarritoProps> = observer(({ index, setIndexCallback }) => {
  const rootStore = useStores()
  const { loginStore, carritoStore, tarjetaStore } = rootStore
  const [ModalVisible, setModalVisible] = useState(false)
  const [Loading, setLoading] = useState(true)
  const [Ready, setReady] = useState(false)
  const direccionDefault = useDireccionDefault()
  const [Direccion, setDireccion] = useState<SnapshotOut<typeof DireccionModel>>()
  const [Facturacion, setFacturacion] = useState<DatoFacturacion>()
  const [ModalDatosFacturacion, setModalDatosFacturacion] = useState(false)
  const addToast = useToaster()
  const apiToaster = useApiToaster()
  const [FechaEstimadaExpress, setFechaEstimadaExpress] = useState('-')
  const [FechaEstimada, setFechaEstimada] = useState(null)
  const [NroTelefono, setNroTelefono] = useState(format_numero_telefono_input(loginStore.nro_telefono) as any)
  const [Movil, setMovil] = useState(false)
  const [Delivery, setDelivery] = useState<boolean>(true)
  const [ModalMediosPago, setModalMediosPago] = useState(false)
  const [MedioPago, setMedioPago] = useState<null | number>(null)
  const [TarjetaSeleccionada, setTarjetaSeleccionada] = useState<SnapshotOut<typeof Tarjeta> | null>()
  const [IconoTarjeta, setIconoTarjeta] = useState<null | string>()
  const [EnvioExpress, setEnvioExpress] = useState(carritoStore.envio_sin_hora)
  const [CostoEnvio, setCostoEnvio] = useState<number | null>(null)
  const [LimiteEnvioGratuito, setLimiteEnvioGratuito] = useState<any>(null)
  const history = useHistory()
  const FechaActual = moment()
  const [UrlModalTarjeta, setUrlModalTarjeta] = useState(null as any)
  const [ModalDireccionVisible, setModalDireccionVisible] = useState(false)
  const [ModalFacturacionVisible, setModalFacturacionVisible] = useState(false)
  const singleBuyRef = useRef(null as any)
  const [showModalProgramada, setShowModalProgramada] = useState(false)

  const [fechaInicio, setFechaInicio] = useState('')
  const [horaInicio, setHoraInicio] = useState('')
  const [horaFin, setHoraFin] = useState('')
  const [fechaHoraProgramada, setFechaHoraProgramada] = useState('')

  useEffect(() => {
    estimarFecha()
    if (carritoStore.total > 0) {
      obtenerCostoEnvio()
    }
  }, [loginStore.id])

  useEffect(() => {
    estimarFecha()
    setReady(true)
    setMovil(window.innerWidth <= 500)
    const defaultDatoFacturacion = loginStore.datos_facturacion.find((d) => d.default)
    if (!!defaultDatoFacturacion) {
      setFacturacion({
        id: defaultDatoFacturacion.id,
        default: defaultDatoFacturacion.default,
        razon_social: defaultDatoFacturacion.razon_social,
        ruc: defaultDatoFacturacion.ruc,
      })
    }
  }, [])

  useEffect(() => {
    if (EnvioExpress) {
      carritoStore.setEnvioSinHora()
      carritoStore.setCompraMejorada(false)
    } else {
      carritoStore.setEnvioConHora(
        carritoStore.entregaDesde ? carritoStore.entregaDesde : '',
        carritoStore.entregaHasta ? carritoStore.entregaHasta : '',
      )
      carritoStore.setCompraMejorada(false)
    }
  }, [EnvioExpress])

  useEffect(() => {
    if (carritoStore.total > 0) {
      obtenerCostoEnvio()
    }
  }, [carritoStore.total])

  useEffect(() => {
    setMedioPago(carritoStore.medioPago)
    if (carritoStore.medioPago === MEDIOS_PAGO.tarjeta_registrada) {
      setTarjetaSeleccionada(tarjetaStore.tarjetas.find((tarjeta) => tarjeta.card_id === carritoStore.tarjeta))
    }
  }, [carritoStore.medioPago, carritoStore.tarjeta])

  useEffect(() => {
    const direccion = direccionDefault()
    setDireccion(direccion)
  }, [])

  useEffect(() => {
    if (TarjetaSeleccionada?.card_brand) {
      setIconoTarjeta(iconoTarjeta(TarjetaSeleccionada?.card_brand))
    }
  }, [TarjetaSeleccionada])

  const seleccionarDireccion = (id: any) => {
    const direccion = loginStore.direcciones.find((dir) => dir.id === id)

    if (!!direccion) {
      setDireccion(getSnapshot(direccion))
    }
  }

  const dataSelectorModal = (data: any) => {
    seleccionarDireccion(data[0])
  }

  const cambiarModalDireccionesEstado = (data: boolean) => {
    setModalVisible(data)
  }

  const has_direccion = (): boolean => {
    return loginStore.direcciones.length > 0
  }

  const estimarFecha = () => {
    rootStore.environment.api.fechaEstimada().then((result) => {
      if (result.kind === 'ok') {
        const currentDate = moment()
        const deliveryDateExpress = moment.duration(currentDate.diff(moment(result.data.express.fecha))).humanize()
        setFechaEstimada(result.data)

        setFechaEstimadaExpress(deliveryDateExpress)
      } else {
        setFechaEstimada(null)
        setFechaEstimadaExpress('-')
      }
    })
  }

  const obtenerCostoEnvio = () => {
    rootStore.environment.api.costoEnvio(carritoStore.total).then((result: any) => {
      if (result.kind === 'ok') {
        console.log(carritoStore.total + result.data.costo_envio)
        setCostoEnvio(result.data.costo_envio)
        setLimiteEnvioGratuito(result.data.envio_gratuito_apartir)
        setLoading(false)
      } else {
        addToast('No se pudo obtener detalles del envío', 'warning')
      }
    })
  }

  const pagar = () => {
    if (!carritoStore.envio_sin_hora && (!carritoStore.entregaDesde || !carritoStore.entregaHasta) && Delivery) {
      addToast('Debe seleccionar el horario de entrega del pedido', 'warning')
      return false
    }
    const mejorada = carritoStore.compra_mejorada
    const entrega_programada = !carritoStore.envio_sin_hora
    const hora_entrega_inicio = entrega_programada ? moment(carritoStore.entregaDesde).format('HH:mm') : null
    const hora_entrega_fin = entrega_programada ? moment(carritoStore.entregaHasta).format('HH:mm') : null
    const Fecha_Entrega = carritoStore.envio_sin_hora
      ? null
      : moment(carritoStore.entregaDesde).format('YYYY-MM-DD HH:mm:ss')
    const idFacturacion = !!Facturacion ? Facturacion.id : null
    if (Direccion?.id === 0 && Delivery) {
      addToast('Debe seleccionar una dirección de entrega para realizar la compra', 'warning')
      return false
    }
    if (
      carritoStore.medioPago === MEDIOS_PAGO.tarjeta_no_registrada ||
      carritoStore.medioPago === MEDIOS_PAGO.zimple ||
      carritoStore.medioPago === MEDIOS_PAGO.tigo_money ||
      carritoStore.medioPago === MEDIOS_PAGO.billetera_personal
    ) {
      cargarSinCatastrar()
    } else {
      if (carritoStore.tarjeta || carritoStore.medioPago === MEDIOS_PAGO.test) {
        setLoading(true)
        const monto = numeral(carritoStore.total).value()
        const { api } = loginStore.environment
        api
          .crearPedido(
            Direccion,
            carritoStore.medioPago,
            carritoStore.productos,
            carritoStore.tarjeta,
            monto,
            mejorada,
            entrega_programada,
            hora_entrega_inicio,
            hora_entrega_fin,
            Delivery,
            Fecha_Entrega,
            idFacturacion,
          )
          .then((result) => {
            setLoading(false)
            if (result.kind === 'ok') {
              carritoStore.reset()
              addToast('Pago realizado satisfactoriamente', 'success')
              history.push(RUTAS.PEDIDOS)
            } else if (result.kind === 'bad-data') {
              // @ts-ignore
              if (result.errors.non_field_errors) {
                // @ts-ignore
                addToast(result.errors.non_field_errors[0], 'warning')
                return false
              }
              addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
            }
          })
      }
    }
  }

  const cargarSinCatastrar = () => {
    setLoading(true)
    const mejorada = carritoStore.compra_mejorada
    const entrega_programada = !carritoStore.envio_sin_hora
    const hora_entrega_inicio = carritoStore.envio_sin_hora ? '' : moment(carritoStore.entregaDesde).format('HH:mm')
    const hora_entrega_fin = carritoStore.envio_sin_hora ? '' : moment(carritoStore.entregaHasta).format('HH:mm')
    const Fecha_Entrega = carritoStore.envio_sin_hora
      ? moment().format('YYYY-MM-DD HH:mm:ss')
      : moment(carritoStore.entregaDesde).format('YYYY-MM-DD HH:mm:ss')

    const { api } = loginStore.environment
    const monto = numeral(carritoStore.total).value()
    const idFacturacion = !!Facturacion ? Facturacion.id : null

    api
      .singleBuy(
        Direccion,
        carritoStore.medioPago,
        carritoStore.productos,
        monto,
        mejorada,
        entrega_programada,
        hora_entrega_inicio,
        hora_entrega_fin,
        Delivery,
        format_numero_telefono_verificar(NroTelefono),
        Fecha_Entrega,
        idFacturacion,
      )
      .then((result) => {
        setLoading(false)
        if (result.kind === 'ok') {
          // Si es personal la respuesta ya viene acá
          if (carritoStore.medioPago === MEDIOS_PAGO.billetera_personal) {
            if (result.exitoso) {
              addToast('Pago registrado con éxito!', 'success')
              carritoStore.reset()
              history.replace(RUTAS.PEDIDOS)
            } else {
              addToast(result.mensaje_transaccion, 'error')
            }
          } else {
            singleBuyRef.current.iniciarPago(result.transaccion_id, result.url)
          }
        } else if (result.kind === 'bad-data') {
          // @ts-ignore
          if (result.errors.non_field_errors) {
            // @ts-ignore
            addToast(result.errors.non_field_errors[0], 'warning')
          }
        } else {
          addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
        }
      })
  }

  const agregarTarjeta = () => {
    setUrlModalTarjeta(null)
    setLoading(true)
    const { api } = loginStore.environment
    api.crearTarjeta().then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        const url_bancard = result.url
        const bancard_port = url_bancard.split('/')[2].split(':')[1]
        const process_id = url_bancard.split('process_id=')[1]
        setUrlModalTarjeta(
          `https://vpos.infonet.com.py:${bancard_port}/checkout/register_card/new?process_id=${process_id}`,
        )
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const tarjetaSelected = (tarjeta: any) => {
    carritoStore.setTarjeta(tarjeta.card_id)
  }

  const obtenerDatosCliente = () => {
    rootStore.environment.api.getCliente(loginStore.id).then((result) => {
      if (result.kind === 'ok') {
        const datos = { ...result.cliente } as any
        loginStore.setDirecciones(datos.direcciones)
        loginStore.setDatosFacturacion(datos.datos_facturacion)
      } else {
        addToast('Ocurrió un error al actualizar las direcciones', 'error')
      }
    })
  }

  const direccionCreada = () => {
    obtenerDatosCliente()
  }

  const datosFacturacionCreado = () => {
    obtenerDatosCliente()
  }

  const singleBuySuccess = () => {
    addToast('Pago registrado con éxito!', 'success')
    carritoStore.reset()
    history.replace(RUTAS.PEDIDOS)
  }

  const obtenerTarjetas = () => {
    setLoading(true)
    const { api } = loginStore.environment
    api.obtenerTarjetas().then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        tarjetaStore.setTarjetas(result.tarjetas)
      } else {
        apiToaster(result, '', 'obtener las tarjetas')
      }
    })
  }

  const tarjetaAgregada = () => {
    setUrlModalTarjeta(null)
    obtenerTarjetas()
  }

  const guardarFechaProgramada = () => {
    let validarFecha = new RegExp('^([01]?[0-9]|2[0-3]):[0-5][0-9]$')
    const esValido = (hora: any) => {
      return validarFecha.test(hora)
    }

    if (!fechaInicio) {
      addToast('Ingrese una fecha', 'warning')
      return
    }
    if (!esValido(horaInicio) || !esValido(horaFin)) {
      addToast('Ingrese una hora válida', 'warning')
      return
    }

    let fechaI = moment(fechaInicio).format('DD/MM/YYYY')
    setFechaInicio(fechaI.toString())

    // formatear fecha para mostrar como un texto
    let fechaFormateada = moment(fechaInicio).format('D \\d\\e MMMM')
    fechaFormateada = `${fechaFormateada} de ${horaInicio} a ${horaFin} hs`

    carritoStore.setEntregaDesde(fechaInicio + ' ' + horaInicio)
    carritoStore.setEntregaHasta(fechaInicio + ' ' + horaFin)

    setFechaHoraProgramada(fechaFormateada)
    setShowModalProgramada(false)
  }

  return (
    <>
      <ModalSinglebuy ref={singleBuyRef} successCallback={singleBuySuccess} />
      <ModalInfo
        close={() => {
          setShowModalProgramada(false)
        }}
        isOpen={showModalProgramada}
        color="var(--naranja-abi)"
        titulo="Fecha de compra programada"
        cssClass="modal-compra-programada"
      >
        <div className="mt-4">
          <span>Fecha:</span>
          <IonInput
            type="date"
            value={fechaInicio}
            onIonChange={(e) => setFechaInicio(e.detail.value!)}
            min={FechaActual.format('YYYY-MM-DD')}
            className={'form-control'}
          />
          <span>Desde las:</span>
          <InputMask
            placeholder={''}
            className={'form-control registro'}
            mask="99:99"
            onChange={(date: any) => setHoraInicio(date.target.value)}
            value={horaInicio}
          />
          <span>Hasta las:</span>
          <InputMask
            placeholder={''}
            className={'form-control registro'}
            mask="99:99"
            onChange={(date: any) => setHoraFin(date.target.value)}
            value={horaFin}
          />
          <div className="metodos-pago-container">
            <div style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
              <div
                style={{ cursor: 'pointer' }}
                className={''}
                onClick={() => {
                  guardarFechaProgramada()
                }}
              >
                <button disabled={Loading} className="btn btn-info mt-2" type="button">
                  Guardar
                </button>
              </div>
            </div>
          </div>
        </div>
      </ModalInfo>
      {ModalDireccionVisible && <ModalDireccion visible={true} successCallback={direccionCreada} />}
      {Ready && loginStore.direcciones.length > 0 && (
        <SelectorModal
          titulo={'Dirección'}
          titulo_label={'Seleccioná la dirección'}
          listado_elementos={loginStore.direcciones}
          listado_elementos_seleccionados={Direccion && Direccion.id !== 0 ? Direccion.id : []}
          datos_filtrados={dataSelectorModal}
          multiple={false}
          llave_label_opcion={'nombre'}
          ocultar_input={true}
          mostrar_modal={ModalVisible}
          mostrar_modal_cambio_estado={cambiarModalDireccionesEstado}
          boton_extra_text={'dirección'}
          boton_extra_funcion={() => {
            setModalDireccionVisible(false)
            setTimeout(() => setModalDireccionVisible(true), 100)
          }}
        />
      )}
      {UrlModalTarjeta && <ModalTarjeta visible={true} url={UrlModalTarjeta} successCallback={tarjetaAgregada} />}
      {ModalFacturacionVisible && <ModalFacturacion visible={true} successCallback={datosFacturacionCreado} />}
      <ModalInfo
        close={() => setModalDatosFacturacion(false)}
        isOpen={ModalDatosFacturacion}
        color="var(--naranja-abi)"
        titulo="Datos de facturación"
        cssClass="modal-info-metodo-pago"
      >
        <div className="metodos-pago-container">
          <IonContent style={{ flexGrow: 3 }}>
            {loginStore.datos_facturacion.map((dato, index) => {
              return (
                <div className={'item-medio-pago'} key={index}>
                  <div className={'checkbox'} onClick={() => setFacturacion(dato)}>
                    <IonCheckbox
                      disabled={Loading}
                      slot="end"
                      color="primary"
                      // @ts-ignore
                      checked={Facturacion?.id === dato.id}
                    />
                  </div>
                  <div>
                    <div>
                      <strong style={Facturacion?.id === dato.id ? { color: 'var(--verde-claro)' } : {}}>
                        {dato.razon_social}
                      </strong>
                    </div>
                    <p className="help">{dato.ruc}</p>
                  </div>
                </div>
              )
            })}
          </IonContent>
          <div style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <div
                style={{ cursor: 'pointer' }}
                className={''}
                onClick={() => {
                  setModalFacturacionVisible(false)
                  setTimeout(() => setModalFacturacionVisible(true), 100)
                }}
            >
              <button disabled={Loading} className="btn btn-info mt-2" type="button">
                Añadir datos de facturación
              </button>
            </div>
          </div>
        </div>
      </ModalInfo>
      <ModalInfo
        close={() => setModalMediosPago(false)}
        isOpen={ModalMediosPago}
        color="var(--naranja-abi)"
        titulo="Método de Pago"
        cssClass="modal-info-metodo-pago"
      >
        <div className="metodos-pago-container">
          <IonContent style={{ flexGrow: 3 }}>
            {tarjetaStore.tarjetas.map((tarjeta, index) => {
              const iconSrc = tarjeta.card_brand ? iconoTarjeta(tarjeta.card_brand) : null

              return (
                <div className={'item-medio-pago'} key={index}>
                  <div className={'checkbox'} onClick={() => !Loading && tarjetaSelected(tarjeta)}>
                    <IonCheckbox
                      disabled={Loading}
                      slot="end"
                      color="primary"
                      // @ts-ignore
                      checked={carritoStore.tarjeta === tarjeta.card_id}
                    />
                  </div>
                  <div>
                    <div>
                      <strong style={carritoStore.tarjeta === tarjeta.card_id ? { color: 'var(--verde-claro)' } : {}}>
                        Tarjeta {index + 1}{' '}
                      </strong>
                      {iconSrc && <img src={iconSrc} height={18} />}
                    </div>
                    <p className="help">{tarjeta.card_masked_number}</p>
                  </div>
                </div>
              )
            })}

            <div className={'item-medio-pago'}>
              <div
                className={'checkbox'}
                onClick={() => !Loading && carritoStore.setMedioPago(MEDIOS_PAGO.tarjeta_no_registrada)}
              >
                <IonCheckbox
                  disabled={Loading}
                  slot="end"
                  color="primary"
                  checked={carritoStore.medioPago === MEDIOS_PAGO.tarjeta_no_registrada}
                />
              </div>

              <div>
                <strong
                  style={
                    carritoStore.medioPago === MEDIOS_PAGO.tarjeta_no_registrada ? { color: 'var(--verde-claro)' } : {}
                  }
                >
                  Tarjeta de débito o crédito ocasional
                </strong>
                <p className={'help'}>
                  De esta forma se ingresa los datos de la tarjeta todas las veces al momento de pagar.
                </p>
              </div>
            </div>

            <div className={'item-medio-pago'}>
              <div className={''} onClick={() => !Loading && carritoStore.setMedioPago(MEDIOS_PAGO.zimple)}>
                <div className={'checkbox'}>
                  <IonCheckbox
                    disabled={Loading}
                    slot="end"
                    color="primary"
                    checked={carritoStore.medioPago === MEDIOS_PAGO.zimple}
                  />
                </div>
              </div>
              <div>
                <strong style={carritoStore.medioPago === MEDIOS_PAGO.zimple ? { color: 'var(--verde-claro)' } : {}}>
                  Billetera Zimple Bancard
                </strong>
                <p className={'help'}>Billetera: {loginStore.nro_telefono}</p>
              </div>
            </div>
            <div className={'item-medio-pago'}>
              <div className={'checkbox'} onClick={() => !Loading && carritoStore.setMedioPago(MEDIOS_PAGO.tigo_money)}>
                <IonCheckbox
                  disabled={Loading}
                  slot="end"
                  color="primary"
                  checked={carritoStore.medioPago === MEDIOS_PAGO.tigo_money}
                />
              </div>
              <div>
                <strong
                  style={carritoStore.medioPago === MEDIOS_PAGO.tigo_money ? { color: 'var(--verde-claro)' } : {}}
                >
                  Tigo Money
                </strong>
                <InputMask
                    placeholder={'Billetera'}
                    className={'form-control  registro-phone-input'}
                    mask="0\999-999-999"
                    onChange={(event) => {
                      setNroTelefono(event.target.value)
                    }}
                    value={NroTelefono}
                />
                {/* <p className={'help'}>Billetera: {loginStore.nro_telefono}</p> */}
              </div>
            </div>
            <div className={'item-medio-pago'}>
              <div
                className={'checkbox'}
                onClick={() => !Loading && carritoStore.setMedioPago(MEDIOS_PAGO.billetera_personal)}
              >
                <IonCheckbox
                  disabled={Loading}
                  slot="end"
                  color="primary"
                  checked={carritoStore.medioPago === MEDIOS_PAGO.billetera_personal}
                />
              </div>
              <div>
                <strong
                  style={
                    carritoStore.medioPago === MEDIOS_PAGO.billetera_personal ? { color: 'var(--verde-claro)' } : {}
                  }
                >
                  Billetera Personal
                </strong>
                <InputMask
                    placeholder={'Billetera'}
                    className={'form-control registro-phone-input'}
                    mask="0\999-999-999"
                    onChange={(event) => {
                      setNroTelefono(event.target.value)
                    }}
                    value={NroTelefono}
                />
              </div>
            </div>
            {rootStore.isDebug && (
              <div className={'item-medio-pago'}>
                <div className={'checkbox'} onClick={() => !Loading && carritoStore.setMedioPago(MEDIOS_PAGO.test)}>
                  <IonCheckbox
                    disabled={Loading}
                    slot="end"
                    color="primary"
                    checked={carritoStore.medioPago === MEDIOS_PAGO.test}
                  />
                </div>
                <div>
                  <strong style={carritoStore.medioPago === MEDIOS_PAGO.test ? { color: 'var(--verde-claro)' } : {}}>
                    Test
                  </strong>
                  <p className={'help'}>Medio de pago para pruebas.</p>
                </div>
              </div>
            )}
          </IonContent>
          <div style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <div
              style={{ cursor: 'pointer' }}
              className={''}
              onClick={() => {
                !Loading && agregarTarjeta()
              }}
            >
              <button disabled={Loading} className="btn btn-info mt-2" type="button">
                Añadir metódo de pago
              </button>
            </div>
          </div>
        </div>
      </ModalInfo>
      {index === 'metodo-envio' && (
        <div className="datos-carrito-container">
          {!Movil && (
            <div className="d-flex" style={{ gap: 10 }}>
              <a onClick={() => history.goBack()}>
                <FIcon.ArrowLeft size={Movil ? 15 : 20} />
              </a>

              <div>
                <span>Seguir Comprando</span>
              </div>
            </div>
          )}
          <div className="detalle-envio-checkout-container">
            <span className="titulo-checkout-carrito"> Tipo de compra</span>
            <div
              className="container-tipo-delivery cpointer py-1"
              onClick={() => {
                setEnvioExpress(true)
              }}
            >
              <div className={EnvioExpress ? 'seleccionado-checkout' : 'no-seleccionado-checkout'}>
                <div>
                  {EnvioExpress && <BiCheckCircle color={'var(--verde-claro)'} />}
                  {!EnvioExpress && <BiCircle />}
                </div>
                <div>
                  <span>Express</span>
                </div>
              </div>
            </div>
            <div
              className="container-tipo-delivery cpointer py-1"
              style={{ flexDirection: 'column' }}
              onClick={() => {
                setShowModalProgramada(true)
                setEnvioExpress(false)
              }}
            >
              <div className={!EnvioExpress ? 'seleccionado-checkout' : 'no-seleccionado-checkout'}>
                <div>
                  {!EnvioExpress && <BiCheckCircle color={'var(--verde-claro)'} />}
                  {EnvioExpress && <BiCircle />}
                </div>
                <div>
                  <span>Programada</span>
                </div>
              </div>
              {!EnvioExpress && (
                <div
                  className="set-horatio-entrega-container"
                  style={{
                    width: '90%',
                    display: 'block',
                    fontSize: 14,
                    textAlign: 'center',
                    paddingTop: 8,
                    borderRadius: 8,
                  }}
                >
                  {!carritoStore.envio_sin_hora && <div>{fechaHoraProgramada}</div>}
                </div>
              )}
            </div>
          </div>
          {Loading && (
            <div className="detalle-envio-checkout-container">
              <IonSpinner name={'crescent'} color={'primary'} />
            </div>
          )}
          {!Loading && (
            <div className="detalle-envio-checkout-container">
              <div>
                <span className="titulo-checkout-carrito">Subtotal</span>
              </div>
              <div>
                <strong className="precio-checkout-carrito">{plata_fmt(carritoStore.total)}</strong>
                <br />
                <span>
                  Delivery gratis con compras a partir de {plata_fmt(LimiteEnvioGratuito)} <br /> Válido para Asunción y
                  Gran Asunción.
                </span>
              </div>
              {LimiteEnvioGratuito && carritoStore.total < LimiteEnvioGratuito && (
                <div>
                  <span>
                    Faltan {plata_fmt(LimiteEnvioGratuito - carritoStore.total)} para que tu delivery sea gratuito.
                  </span>
                </div>
              )}
              <div>
                <span className="titulo-checkout-carrito">Tiempo estimado de entrega</span>
                <br />
                <strong className="precio-checkout-carrito">{EnvioExpress ? FechaEstimadaExpress : '-'}</strong>
              </div>
            </div>
          )}
          <div className="finalizar-compra-container">
            <button
              className="btn btn-info px-5 py-2 my-2"
              type="button"
              onClick={() => {
                if (!loginStore.isLoggedIn) {
                  addToast('Inicie sesión para finalizar su compra')
                  //
                  let currentUrlParams = new URLSearchParams(window.location.search)
                  currentUrlParams.set('login', '1')
                  currentUrlParams.set('checkout', '1')
                  history.push(window.location.pathname + '?' + currentUrlParams.toString())
                  //
                  return
                } else if (!loginStore.perfil_completo) {
                  history.replace(`${RUTAS.CLIENTE}?checkout=1`)
                  return
                } else if (
                  !carritoStore.envio_sin_hora &&
                  (!carritoStore.entregaDesde || !carritoStore.entregaHasta) &&
                  Delivery
                ) {
                  addToast('Debe seleccionar el horario de entrega del pedido', 'warning')
                  return
                }
                setIndexCallback('tarjeta-direcion')
              }}
            >
              Finalizar compra
            </button>
          </div>
        </div>
      )}
      {index === 'tarjeta-direcion' && (
        <div className="container-checkout-carrito" style={{ display: 'flex' }}>
          <div
            className=" container-tarjeta-direccion-checkout datos-carrito-container "
            style={{ justifyContent: 'flex-start' }}
          >
            <div className="detalle-envio-checkout-container">
              <div className="titulo-checkout-carrito"> Dirección de entrega</div>
              <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                {!!Direccion && (
                  <>
                    <div>
                      <div className="d-flex">
                        <img
                          src={
                            !!Direccion.icono
                              ? !!ICONOS_DIRECCION[Direccion.icono]
                                ? ICONOS_DIRECCION[Direccion.icono]
                                : 'assets/icon/ubicacion-gray.png'
                              : 'assets/icon/ubicacion-gray.png'
                          }
                          className="icono-direccion-checkout"
                        />
                        {!!Direccion.nombre && (
                          <>
                            <span style={{ color: 'var(--verde-claro)', fontWeight: 'bolder' }}>
                              {Direccion.nombre}{' '}
                            </span>
                            <br />
                          </>
                        )}
                      </div>
                      <div>
                        {Direccion.calle_principal} {Direccion.numero}
                      </div>
                    </div>
                    <div
                      className={'cpointer'}
                      onClick={() => {
                        setModalVisible(true)
                      }}
                      style={{ color: 'var(--naranja-abi)', fontWeight: 'bold', fontSize: '0.875rem' }}
                    >
                      Cambiar
                    </div>
                  </>
                )}
                {!has_direccion() && (
                  <div style={{ display: 'flex', justifyContent: 'space-between', flex: 1, alignItems: 'center' }}>
                    <div className="icono-direccion-tarjeta-container" />
                    <div className="pl-lg-4 icono-direccion-tarjeta-container">
                      <div
                        className={'cpointer'}
                        style={{ color: 'var(--naranja-abi)', fontWeight: 'bold', fontSize: '0.875rem' }}
                        onClick={() => {
                          setModalDireccionVisible(false)
                          setTimeout(() => setModalDireccionVisible(true), 100)
                        }}
                      >
                        Agregar dirección
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
            {loginStore.datos_facturacion.length > 0 && (
              <div className="detalle-envio-checkout-container">
                <div className="titulo-checkout-carrito"> Datos de facturación</div>
                <div className="container-opciones-pago-checkout">
                  <div style={{ display: 'flex', justifyContent: 'space-between', flex: 1 }}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', flex: 1, alignItems: 'center' }}>
                      <div>
                        <div style={{ color: 'var(--verde-claro)', fontWeight: 'bolder' }}>
                          {Facturacion
                            ? Facturacion.razon_social
                            : loginStore.defaultDatoFacturacion
                            ? loginStore.defaultDatoFacturacion.razon_social
                            : ''}
                        </div>
                        <div>
                          {Facturacion
                            ? Facturacion.ruc
                            : loginStore.defaultDatoFacturacion
                            ? loginStore.defaultDatoFacturacion.ruc
                            : ''}{' '}
                        </div>
                      </div>

                      <div
                        className={'cpointer'}
                        style={{ color: 'var(--naranja-abi)', fontWeight: 'bold', fontSize: '0.875rem' }}
                        onClick={() => {
                          setModalDatosFacturacion(true)
                        }}
                      >
                        {!!Facturacion ? 'Cambiar' : 'Elegir'}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}
            <div className="detalle-envio-checkout-container">
              <div className="titulo-checkout-carrito"> Método de pago</div>
              <div className="container-opciones-pago-checkout">
                <div style={{ display: 'flex', justifyContent: 'space-between', flex: 1 }}>
                  <div style={{ display: 'flex', justifyContent: 'space-between', flex: 1, alignItems: 'center' }}>
                    {MedioPago === MEDIOS_PAGO.tarjeta_no_registrada && (
                      <div className={'d-flex'}>
                        <img src={'assets/icon/tarjeta-verde.png'} className="icono-direccion-checkout" />
                        <div className="direccion-checkout-carrito">
                          <div style={{ color: 'var(--verde-claro)', fontWeight: 'bolder' }}>Tarjeta Ocasional</div>
                        </div>
                      </div>
                    )}
                    {MedioPago === MEDIOS_PAGO.test && (
                      <div className="direccion-checkout-carrito">
                        <span style={{ color: 'var(--verde-claro)', fontWeight: 'bold' }}> Test </span>
                        <br />
                      </div>
                    )}
                    {MedioPago === MEDIOS_PAGO.zimple && (
                      <div className="">
                        <span style={{ color: 'var(--verde-claro)', fontWeight: 'bold' }}> Zimple </span>
                        <br />
                      </div>
                    )}
                    {MedioPago === MEDIOS_PAGO.billetera_personal && (
                      <div className="">
                        <span style={{ color: 'var(--verde-claro)', fontWeight: 'bold' }}> Billetera Personal</span>
                        <br />
                      </div>
                    )}
                    {MedioPago === MEDIOS_PAGO.tigo_money && (
                      <div className="">
                        <span style={{ color: 'var(--verde-claro)', fontWeight: 'bold' }}> Tigo Money</span>
                        <br />
                      </div>
                    )}
                    {MedioPago === MEDIOS_PAGO.tarjeta_registrada && TarjetaSeleccionada && (
                      <div className={'d-flex'}>
                        <img src={'assets/icon/tarjeta-verde.png'} className="icono-direccion-checkout" />

                        <div className="direccion-checkout-carrito">
                          <div>
                            <span style={{ color: 'var(--verde-claro)', fontWeight: 'bolder' }}>
                              Tarjeta {IconoTarjeta ? <img src={IconoTarjeta} height={18} /> : null}
                            </span>
                            <br />
                            <span>{TarjetaSeleccionada.card_masked_number}</span>
                          </div>
                        </div>

                        <br />
                      </div>
                    )}
                    <div
                      className={'cpointer'}
                      style={{ color: 'var(--naranja-abi)', fontWeight: 'bold', fontSize: '0.875rem' }}
                      onClick={() => {
                        setModalMediosPago(true)
                      }}
                    >
                      Cambiar
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="datos-carrito-container p-4 costos-checkout-container">
            <div style={{ justifyContent: 'flex-start' }}>
              <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                <div>
                  <span> Mi pedido</span>
                  <br />
                  <div
                    className={'cpointer'}
                    style={{ color: 'var(--naranja-abi)', fontSize: '0.875rem' }}
                    onClick={() => setIndexCallback('metodo-envio')}
                  >
                    Ver detalles
                  </div>
                </div>
                <div>
                  <strong>{plata_fmt(carritoStore.total)}</strong>
                </div>
              </div>

              <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                <div>
                  <span> Envío</span>
                </div>
                <div>{CostoEnvio ? <strong>{plata_fmt(CostoEnvio)}</strong> : <strong>{plata_fmt(0)}</strong>}</div>
              </div>
              <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                <div>
                  <strong> Total</strong>
                  <br />
                  <span style={{ fontSize: '0.875rem' }}>Incluye IVA</span>
                </div>
                <div>{CostoEnvio ? <strong>{plata_fmt(carritoStore.total + CostoEnvio)}</strong> : <strong>{plata_fmt(carritoStore.total)}</strong>}</div>
              </div>
            </div>

            <div className="finalizar-compra-container" style={{ flex: 2 }}>
              <button
                onClick={() => {
                  pagar()
                }}
                disabled={Loading}
                className="btn btn-info px-5 py-2 mt-2"
                type="button"
              >
                Confirmar Compra
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  )
})

export default CheckoutCarrito
