import { IonSpinner } from '@ionic/react'
import React, { useEffect, useImperativeHandle, useRef, useState } from 'react'
import DataTable from 'react-data-table-component'
import * as FIcon from 'react-feather'

import { useApiToaster } from '../helpers-ts'

type SDataTableProps = {
  columns: any
  obtenerDatos: any
  ref: any
  subHeader?: any
  selectableRows?: boolean
  selectedRows?: any
  selectableRowSelected?: []
  disablePagination?: boolean
}

export const SDataTable: React.FC<SDataTableProps> = React.forwardRef((props, ref) => {
  const [Loading, setLoading] = useState(false)
  const [Data, setData] = useState([])
  const [TotalRows, setTotalRows] = useState(0)
  const Page = useRef(1)
  const [RowsPerPage, setRowsPerPage] = useState(10)
  const [Order, setOrder] = useState('')
  const [Search, setSearch] = useState('')
  const [Clear, setClear] = useState(false)
  const [SelectedRows, setSelectedRows] = useState<number>(0)

  const apiToaster = useApiToaster()

  useImperativeHandle(ref, () => ({
    refresh() {
      obtenerDatosWrapper(RowsPerPage, Page.current, Order)
    },
    clearSelected() {
      setClear(!Clear)
    },
    selectRow(row: number) {
      setSelectedRows(row)
    },
  }))

  useEffect(() => {
    obtenerDatosWrapper(RowsPerPage, 1, Order)
  }, [])

  const obtenerDatosWrapper = (perPage: number, page: number, order: string) => {
    setLoading(true)
    props.obtenerDatos(perPage, page, order, Search).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setTotalRows(result.data.count)
        setData(result.data.results)
      } else {
        apiToaster(result, '', 'listar')
      }
    })
  }

  const handlePerRowsChange = (perPage: number, page: number) => {
    setRowsPerPage(perPage)
    obtenerDatosWrapper(perPage, page, Order)
  }

  const handlePageChange = (page: number) => {
    Page.current = page
    obtenerDatosWrapper(RowsPerPage, page, Order)
  }

  const handleSort = (column: any) => {
    const direction = Order.endsWith(column.selector) && !Order.startsWith('-') ? '-' : ''
    const order = direction + column.selector
    setOrder(order)
    obtenerDatosWrapper(RowsPerPage, Page.current, order)
  }

  const doSearch = () => {
    Page.current = 1
    obtenerDatosWrapper(RowsPerPage, Page.current, Order)
  }

  return (
    <div className="table-responsive">
      <DataTable
        {...props}
        data={Data}
        noHeader={true}
        subHeaderComponent={
          <div className={'input-group'} style={{ maxWidth: 300 }}>
            <input
              type={'text'}
              className={'form-control'}
              placeholder={'Escriba el texto a buscar'}
              value={Search}
              onChange={(evt) => setSearch(evt.target.value)}
              onKeyPress={(evt) => {
                if (evt.key === 'Enter') {
                  doSearch()
                }
              }}
            />
            <div className="input-group-append">
              <button className={'btn btn-light'} onClick={() => doSearch()}>
                <FIcon.Search size={14} />
              </button>
            </div>
          </div>
        }
        progressPending={Loading}
        pagination={!props.disablePagination}
        paginationServer={!props.disablePagination}
        paginationTotalRows={props.disablePagination ? undefined : TotalRows}
        paginationRowsPerPageOptions={props.disablePagination ? undefined : [10, 30, 50, 70, 100]}
        onChangeRowsPerPage={props.disablePagination ? undefined : handlePerRowsChange}
        onChangePage={props.disablePagination ? undefined : handlePageChange}
        onSort={props.disablePagination ? undefined : handleSort}
        paginationPerPage={props.disablePagination ? undefined : RowsPerPage}
        sortServer
        noDataComponent={<div className={'mb-5'}>No hay registros para mostrar</div>}
        paginationComponentOptions={{
          rowsPerPageText: 'Filas por página:',
          rangeSeparatorText: 'de',
          selectAllRowsItemText: 'Todos',
        }}
        progressComponent={
          <div>
            <IonSpinner color={'dark'} name="crescent" />
          </div>
        }
        selectableRows={props.selectableRows ? props.selectableRows : false}
        onSelectedRowsChange={(row) => props.selectedRows([...row.selectedRows])}
        clearSelectedRows={Clear}
        selectableRowSelected={(row) => {
          if (SelectedRows) {
            // @ts-ignore
            return SelectedRows == row.id
          } else {
            return false
          }
        }}
      />
    </div>
  )
})

export default SDataTable
