import React, { useEffect, useState } from 'react'
import { observer } from 'mobx-react-lite'
import { IonContent, IonHeader, IonModal, IonSpinner, IonTitle, IonToolbar } from '@ionic/react'
import { Feather as Ficon } from 'react-feather'
import './PhotoLoaderModalRegistro.css'
import { Capacitor } from '@capacitor/core'
import { Camera as NativeCamera } from '@ionic-native/camera'
import { Camera, CameraDirection, CameraResultType, CameraSource } from '@capacitor/camera'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import { propTypes } from 'react-bootstrap/esm/Image'
import * as FIcon from 'react-feather'
import { ModalInfo } from './ModalInfo'

type ImageItem = {
  titulo: string
  preview?: string | null
  userDataLabel: string
}

interface UploadItemProps {
  itemImage: ImageItem
  on_select: any
  source?: number
  camera_direction?: number
  editor?: boolean
  dontUseWebInput?: boolean
  preview_css_class?: string
  color?: string
  class_boton_aceptar?: string
  extra_css_class?: string
  infoTitulo?: string
  infoBody?: any
}

export const UploadItem: React.FC<UploadItemProps> = (props) => {
  const itemImage = props.itemImage
  const preview = itemImage.preview ? itemImage.preview : null
  const [imgPathTemp, setImgPathTemp] = useState<any>(null)
  let camera_direction = props.camera_direction ? props.camera_direction : 0 // rear por default
  const color = props.color || '#303AB2'
  const class_boton_aceptar = props.class_boton_aceptar || 'btn-outline-primary'

  if (Capacitor.getPlatform() === 'web') {
    camera_direction = 1 // front
  }

  useEffect(() => {
    if (!!imgPathTemp) {
      props.on_select(imgPathTemp, itemImage.userDataLabel)
    }
  }, [imgPathTemp])

  const launchCam = async () => {
    try {
      let image = await NativeCamera.getPicture({
        quality: 90,
        sourceType: 1, // camera
        destinationType: 1, // File uri
        allowEdit: false,
        correctOrientation: false,
        cameraDirection: camera_direction,
      })
      setImgPathTemp(image || '')
    } catch (err) {
      console.log(err)
    }
  }

  const loadFile = async () => {
    try {
      if (Capacitor.getPlatform() === 'web') {
        // Camera de capacitor tiene problemas con algunos teléfonos
        const capturedPhoto = await Camera.getPhoto({
          resultType: CameraResultType.Uri,
          source: CameraSource.Prompt,
          direction: camera_direction === 0 ? CameraDirection.Rear : CameraDirection.Front,
          allowEditing: props.editor == undefined ? true : props.editor,
          quality: 100,
          webUseInput: true, // usar dialogo nativo del OS
        })
        setImgPathTemp(capturedPhoto.webPath || '')
        return
      }
      //
      let image = await NativeCamera.getPicture({
        quality: 90,
        sourceType: 2, // photo library
        destinationType: 1, // File uri
        allowEdit: false,
        correctOrientation: false,
      })
      if (image) {
        const prefix = Capacitor.getPlatform() === 'ios' ? '' : 'file://'
        if (!image.startsWith(prefix)) {
          image = prefix + image
        }
      }
      setImgPathTemp(image || '')
    } catch (err) {
      console.log(err)
    }
  }

  return (
    <>
      <div className={'helptext-div col-12 mt-3'}>
        <h4 style={{ textAlign: 'center', color: 'var(--gris-oscuro)', fontWeight: 'bolder' }}>{itemImage.titulo}</h4>

        <div style={{ display: 'flex', justifyContent: 'center' }}>
          {!preview && !!imgPathTemp && (
            <img className="preview-imagen-item" src={Capacitor.convertFileSrc(imgPathTemp)} />
          )}
          {preview && (
            <img className="preview-imagen-item" src={imgPathTemp ? Capacitor.convertFileSrc(imgPathTemp) : preview} />
          )}
        </div>
        <div style={{ display: 'flex', gap: 20 }}>
          {Capacitor.getPlatform() !== 'web' && (props.source === undefined || props.source === 1) && (
            <button style={{ flex: 1 }} className={'btn ' + class_boton_aceptar + ' col-12  mt-2'} onClick={launchCam}>
              Sacar foto
            </button>
          )}

          <button style={{ flex: 1 }} className={'btn ' + class_boton_aceptar + ' col-12 mt-2'} onClick={loadFile}>
            Seleccionar desde la galería
          </button>
        </div>
      </div>
    </>
  )
}

type PhotoLoaderModalRegistroMultipleProps = {
  titulo_label: any
  titulo_modal: string
  helptext?: any
  titulo_boton?: string
  custom_boton?: any
  modal_content?: any
  bloqueado?: boolean
  img_path?: string
  on_select: any
  on_accept?: any
  imageItems: ImageItem[]
  source?: number
  camera_direction?: number
  editor?: boolean
  dontUseWebInput?: boolean
  preview?: string
  preview_css_class?: string
  color?: string
  perfil?: boolean
  class_boton_aceptar?: string
  extra_css_class?: string
  infoTitulo?: string
  infoBody?: any
  modalInfoCssClass?: string
  extra_component?: any
}

// source: 0 - Galeria / 1 - Camara

export const PhotoLoaderModalRegistroMultiple: React.FC<PhotoLoaderModalRegistroMultipleProps> = observer((props) => {
  const [Visible, setVisible] = useState(false)
  const [imgPath, setImgPath] = useState('')
  const [imgPathTemp, setImgPathTemp] = useState<any>()
  const [AlertInfoImagen, setAlertInfoImagen] = useState(false)
  let camera_direction = props.camera_direction ? props.camera_direction : 0 // rear por default
  const color = props.color || '#303AB2'
  const class_boton_aceptar = props.class_boton_aceptar || 'btn-outline-primary'

  if (Capacitor.getPlatform() === 'web') {
    camera_direction = 1 // front
  }

  const launchCam = async () => {
    let image = await NativeCamera.getPicture({
      quality: 90,
      sourceType: 1, // camera
      destinationType: 1, // File uri
      allowEdit: false,
      correctOrientation: false,
      cameraDirection: camera_direction,
    })
    setImgPathTemp(image || '')
  }

  const loadFile = async () => {
    try {
      if (Capacitor.getPlatform() === 'web') {
        // Camera de capacitor tiene problemas con algunos teléfonos
        const capturedPhoto = await Camera.getPhoto({
          resultType: CameraResultType.Uri,
          source: CameraSource.Prompt,
          direction: camera_direction === 0 ? CameraDirection.Rear : CameraDirection.Front,
          allowEditing: props.editor == undefined ? true : props.editor,
          quality: 100,
          webUseInput: true, // usar dialogo nativo del OS
        })
        setImgPathTemp(capturedPhoto.webPath || '')
        return
      }
      //
      let image = await NativeCamera.getPicture({
        quality: 90,
        sourceType: 2, // photo library
        destinationType: 1, // File uri
        allowEdit: false,
        correctOrientation: false,
      })
      if (image) {
        const prefix = Capacitor.getPlatform() === 'ios' ? '' : 'file://'
        if (!image.startsWith(prefix)) {
          image = prefix + image
        }
      }
      setImgPathTemp(image || '')
    } catch (err) {
      console.log(err)
    }
  }

  const confirmDialog = () => {
    setVisible(false)
    if (imgPathTemp) {
      setImgPath(imgPathTemp)
      // props.on_select(imgPathTemp, props.userdata)
    }
  }

  const cancelDialog = () => {
    setVisible(false)
  }

  const openDialog = () => {
    setImgPathTemp(imgPath)
    setVisible(true)
  }

  return (
    <>
      <div className={'form-group'}>
        {!!props.infoTitulo && !!props.infoBody && (
          <ModalInfo
            color={color}
            isOpen={AlertInfoImagen}
            close={() => setAlertInfoImagen(false)}
            titulo={props.infoTitulo}
            cssClass={props.modalInfoCssClass ? props.modalInfoCssClass : 'requisitos-foto-perfil'}
          >
            <p>{props.infoBody}</p>
          </ModalInfo>
        )}
        <IonModal isOpen={Visible} backdropDismiss={false}>
          <IonHeader>
            <IonToolbar>
              <IonTitle>{props.titulo_modal}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            {props.imageItems.map((item, index) => (
              <UploadItem
                key={index}
                itemImage={item}
                class_boton_aceptar={class_boton_aceptar}
                on_select={props.on_select}
              />
            ))}
            {props.extra_component ? props.extra_component : null}
          </IonContent>

          <div style={{ padding: 10 }}>
            <div className={'row'}>
              <div className={'col-6'}>
                <button className={'btn btn-outline-secondary btn-block'} onClick={cancelDialog}>
                  CANCELAR
                </button>
              </div>
              <div className={'col-6'}>
                <button
                  className={'btn ' + class_boton_aceptar + ' btn-block'}
                  onClick={() => {
                    if (!!props.on_accept) {
                      props.on_accept()
                    }
                    setVisible(false)
                  }}
                >
                  <span>ACEPTAR</span>
                </button>
              </div>
            </div>
          </div>
        </IonModal>
      </div>
      {!props.custom_boton && !!props.perfil && (
        <>
          <div className={'d-flex justify-content-center align-items-center'}>
            <div
              style={{ borderColor: color }}
              className={
                props.extra_css_class
                  ? 'cpointer ' + props.extra_css_class
                  : 'cpointer circulo-contenedor-imagen-registro'
              }
              onClick={!props.bloqueado ? openDialog : () => {}}
            >
              <img
                alt={''}
                className={
                  props.preview_css_class ? props.preview_css_class : props.preview !== '' ? 'con-imagen' : 'sin-imagen'
                }
                src={props.preview !== '' ? props.preview : 'assets/icon/perfil.png'}
              />
            </div>
          </div>
          <div className="input-group mb-3">
            <div
              className={
                'boton-photo-loader-registro btn btn-outline-orange btn-font-sm d-flex flex-row align-items-center justify-content-center'
              }
            >
              {!props.preview && (
                <img onClick={!props.bloqueado ? openDialog : () => {}} alt={''} src={'assets/icon/camara.png'} />
              )}
              {props.preview && <CheckCircleIcon style={{ fontSize: 22, color: '#28a745' }} />}
              <span className={'mx-2'} onClick={!props.bloqueado ? openDialog : () => {}} style={{ color: color }}>
                {props.titulo_label}
              </span>
              {props.titulo_boton ? (
                <div style={{ color: props.color }}>{props.titulo_boton}</div>
              ) : (
                <img onClick={() => setAlertInfoImagen(true)} alt={''} src={'assets/icon/info.png'} />
              )}
            </div>
          </div>
        </>
      )}
      {!props.custom_boton && !props.perfil && (
        <>
          <div className={'d-flex align-items-center mb-3'}>
            <div
              className={'boton-photo-loader-registro-no-perfil btn btn-outline-orange btn-font-sm mr-2'}
              onClick={!props.bloqueado ? openDialog : () => {}}
            >
              {props.titulo_label}
            </div>
            {imgPathTemp && <CheckCircleIcon style={{ fontSize: 22, color: '#28a745' }} />}
            {!imgPathTemp && (
              <img
                className={'info-image'}
                onClick={() => setAlertInfoImagen(true)}
                alt={''}
                src={'assets/icon/info.png'}
              />
            )}
          </div>
        </>
      )}
      {!!props.custom_boton && (
        <>
          <div onClick={!props.bloqueado ? openDialog : () => {}}>{props.custom_boton}</div>
        </>
      )}
    </>
  )
})

export default PhotoLoaderModalRegistroMultiple
