import React, { useImperativeHandle, useState } from 'react'
import '../UppyModal.css'
import '@uppy/core/dist/style.css'
import '@uppy/dashboard/dist/style.css'
import '@uppy/image-editor/dist/style.css'
import { SMap } from '../SMap'
import { LatLngExpression } from 'leaflet'
import { useToaster } from '../../helpers-ts'
import { FooterFormulario } from '..'
import { useStores } from '../../models/root-store/root-store-context'
import { RUTAS_APP } from '../../constants'
import { useHistory } from 'react-router'

type MapaSelectorModalProps = {
  ref: any
  // location: LatLngExpression | null
  lat: number | null
  lng: number | null
  onAccept?: any
  userdata?: any
  onCancel?: any
  color?: string
  cssButtom?: string
  Movil?: boolean
}

export const MapaSelector: React.FC<MapaSelectorModalProps> = React.forwardRef((props, ref) => {
  const rootStore = useStores()
  const history = useHistory()
  const addToast = useToaster()
  const [Ready, setReady] = useState(false)
  const [Lat, setLat] = useState<number | null>(props.lat)
  const [Lng, setLng] = useState<number | null>(props.lng)
  const [Zoom, setZoom] = useState<number>(14)
  const [Center, setCenter] = useState<LatLngExpression | null>({
    lng: props.lat || -57.63591,
    lat: props.lng || -25.30066,
  })
  const [OnAccept, setOnAccept] = useState(props.onAccept ? [props.onAccept] : null)
  const [UserData, setUserData] = useState(props.userdata ? [props.userdata] : null)
  const [OnCancel, setOnCancel] = useState(props.onCancel ? [props.onCancel] : null)
  const color = props.color || '#303AB2'
  const Movil = props.Movil ? props.Movil : window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  const setup = async () => {
    let hasPermission = await rootStore.environment.locationService.checkGPSPermission()
    if (!hasPermission) {
      let hasPermission = await rootStore.environment.locationService.askPermissionAndOrTurnOnGPS()
      rootStore.setHasLocationPermission(hasPermission)
    }
  }

  React.useEffect(() => {
    setup()
    setTimeout(() => {
      setReady(true)
    }, 700)
  }, [])

  React.useEffect(() => {
    if (!props.lat) {
      return
    }
    if (!Lat) {
      setLat(props.lat)
    }
  }, [props.lat])

  React.useEffect(() => {
    if (!props.lng) {
      return
    }
    if (!Lng) {
      setLng(props.lng)
    }
  }, [props.lng])

  useImperativeHandle(ref, () => ({
    open(onAccept: any, oncancel?: any, userdata?: any) {
      if (onAccept) setOnAccept([onAccept])
      if (oncancel) setOnCancel([oncancel])
      if (userdata) setUserData([userdata])

      if (props.lat && props.lng) {
        setZoom(16)
      }

      setLat(props.lat)
      setLng(props.lng)
    },
    close() {
      cancelDialog()
    },
  }))

  const seleccionarUbicacion = (lat: number, lng: number) => {
    setLat(lat)
    setLng(lng)
    setCenter({ lat, lng })
  }

  const cancelDialog = () => {
    setReady(false)
    if (OnCancel) OnCancel[0](UserData ? UserData[0] : null)
  }

  const acceptDialog = () => {
    if (OnAccept && Lat && Lng) {
      OnAccept[0](Lat, Lng, UserData ? UserData[0] : null)
    } else {
      addToast('Seleccione una ubicación para continuar', 'warning')
      history.push(RUTAS_APP.VEHICULO)
    }
  }

  return (
    <div style={{ display: 'flex', flexDirection: 'column', width: '100%', height: '100%' }}>
      {!Movil ? (
        <div style={{ flexGrow: 1, width: '100%' }}>
          {Ready && (
            <SMap
              lat={Lat}
              lng={Lng}
              zoom={Zoom}
              center={Center}
              ubicacion={seleccionarUbicacion}
              full_screen={true}
              color={color}
            />
          )}
        </div>
      ) : (
        <div style={{ height: '100%', width: '100%' }}>
          {Ready && (
            <SMap
              lat={Lat}
              lng={Lng}
              zoom={Zoom}
              center={Center}
              ubicacion={(lat: number, lng: number) => {
                seleccionarUbicacion(lat, lng)
              }}
              full_screen={true}
              color={color}
            />
          )}
        </div>
      )}

      {!Movil ? (
        <div
          style={{
            position: 'absolute',
            bottom: 30,
            left: '50%',
            textAlign: 'center',
            zIndex: 9999,
            marginLeft: -94,
          }}
        >
          <button className={props.cssButtom ? props.cssButtom : 'btn-principal-registro-abi'} onClick={acceptDialog}>
            Seleccionar
          </button>
        </div>
      ) : (
        <FooterFormulario>
          <button
            className={props.cssButtom ? props.cssButtom : 'btn-principal-registro-abi my-2'}
            onClick={acceptDialog}
          >
            Seleccionar
          </button>
        </FooterFormulario>
      )}
    </div>
  )
})

export default MapaSelector
