import './AdminItemFieldRegistro.css';

import { CKEditor } from 'ckeditor4-react';
import es from 'date-fns/locale/es';
import { observer } from 'mobx-react-lite';
import React, { useEffect, useRef, useState } from 'react';
import { registerLocale } from 'react-datepicker';
import InputMask from 'react-input-mask';
import Select from 'react-select';
import AsyncSelect from 'react-select/async';

import { useDebounce } from '../../helpers-ts';
import { useStores } from '../../models/root-store/root-store-context';
import { ModalInfo } from '../ModalInfo';

registerLocale('es', es)
const numeral = require('numeral')

type AdminItemFieldRegistroProps = {
  titulo_externo?: string
  titulo: string
  tipo:
    | 'select2'
    | 'telefono-simple'
    | 'select2async'
    | 'texto'
    | 'email'
    | 'numero'
    | 'boolean'
    | 'decimales'
    | 'fecha'
    | 'hora'
    | 'telefono'
    | 'password'
    | 'ckeditor-mini'
    | 'ckeditor-full'
    | 'textarea'
  campo: string
  valor: any
  error?: any
  set_dato: any
  columnas: number
  columnas_sm?: number
  restriccion?: boolean | null
  deshabilitado?: boolean | null
  horizontal?: boolean | null
  textAreaRows?: number
  help_text?: string
  info_internal_icon?: any
  opciones_select?: any
  select_placeholder?: string
  select_no_options_text?: string
  select_multiple?: boolean
  select_onchange?: any
  numero_minimo?: number
  extra_css?: string
  set_dato_blur?: any
  campo_blur?: boolean
  verificar_firebase_habilitado?: boolean
  verificar_firebase_autenticado?: boolean
  verificar_firebase_func?: any
  verificar_firebase_error?: string
  verificar_firebase_reset?: any
  verificar_boton_clase?: string
  set_dato_registro_store?: boolean
  prepare_dato_registro_store?: any // permite preparar los datos para guardar en el store

  infoTitulo?: string
  infoBody?: any
  modal_info_css_class?: string
  color?: string
}

// Tipos de formatos disponibles - solo algunos están disponibles en formato horizontal
// tipo: texto - sin formato especifico
// tipo: numero - le agrega separador de miles
// tipo: decimales - le agrega separador de decimales
// tipo: fecha - agrega un datepicker
// tipo: telefono - agrega un phoneinput
// tipo: boolean - agrega un checkbox
// tipo: textarea - agrega un text area y se le puede pasar una cantidad de rows con la propiedad textAreaRows
// tipo: boolean - agrega un checkbox
// tipo: email - formato email
// tipo: password - formato pasword
// tipo: ckeditor-mini - ckeditor con 3 propiedades
// tipo: ckeditor-full - ckeditor con todas las propiedades por defecto

//numero_minimo: Es el valor mínimo aceptado por el input

// campo_blur: Campos que disparan el evento onBlur
// set_dato_blur: Función que se dispara al hacer onBlur sobre el campo marcado

export const AdminItemFieldRegistro: React.FC<AdminItemFieldRegistroProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore, registroStore } = rootStore
  const textAreaRows = props.textAreaRows || 2
  const extra_css = props.extra_css || ''
  const set_dato_registro_store = props.set_dato_registro_store || false
  const verificar_boton_clase = props.verificar_boton_clase || 'btn-outline-primary'
  const columnas_sm = props.columnas_sm || props.columnas
  const [ShowFirebaseMessageButton, setShowFirebaseMessageButton] = useState(false)
  const [FirebaseAutenticado, setFirebaseAutenticado] = useState(false)
  const [AlertInfo, setAlertInfo] = useState(false)
  // select async
  const [Value, setValue] = useState(null as any)
  const [SelectedValue, setSelectedValue] = useState(null as any)
  const callbackRef = useRef(null as any)
  const selectRef = useRef(null as any)
  const debouncedValue = useDebounce(Value, 500)
  const selectPlaceHolder = props.select_placeholder
    ? props.select_placeholder
    : 'Seleccionar ' + props.titulo.toLocaleLowerCase()

  const customStyles = {
    control: (state: any) => ({
      ...state,
      border: 0,
      borderBottom: '1px solid #afafaf',
      color: '#929292',
      fontWeight: 'bolder',
    }),
    singleValue: (provided: any) => {
      const color = '#929292'
      return { ...provided, color }
    },
    valueContainer: (state: any) => ({
      ...state,
      paddingLeft: 0,
    }),
  }

  if (props.restriccion === false) {
    return null
  }

  let deshabilitado = false
  if (props.deshabilitado !== undefined && props.deshabilitado !== null && !props.deshabilitado) {
    deshabilitado = true
  }

  const setDato = (campo: string, valor: any, extra_data?: any) => {
    props.set_dato(campo, valor, extra_data)
  }

  const setDatoBlur = (campo: string) => {
    props.set_dato_blur(campo)
  }

  const setDatoRegistroStore = (campo: string, valor: any) => {
    if (props.prepare_dato_registro_store) {
      valor = props.prepare_dato_registro_store(valor)
    }
    if (!loginStore.isLoggedIn) {
      registroStore.setDatoRegistroStore(campo, valor)
    }
  }

  const selectOnChange = (val: any) => {
    if (val) {
      setSelectedValue(val.value)
      //
      setDato(props.campo, val.value)
      if (set_dato_registro_store) {
        setDatoRegistroStore(props.campo, val.value)
      }
    } else {
      setDato(props.campo, null)
      if (set_dato_registro_store) {
        setDatoRegistroStore(props.campo, null)
      }
    }

    if (props.select_onchange) {
      props.select_onchange(val)
    }
  }

  useEffect(() => {
    setFirebaseAutenticado(props.verificar_firebase_autenticado === true)
  }, [props.verificar_firebase_autenticado])

  useEffect(() => {
    if (props.tipo === 'select2async' && props.valor && props.valor.value) {
      setSelectedValue(props.valor.value)
    }
    setValue('') // Para mostrar el componente
  }, [props.valor])

  useEffect(() => {
    // select async
    if (props.opciones_select && callbackRef.current) {
      props.opciones_select(debouncedValue).then((opciones: any) => {
        callbackRef.current(opciones)
      })
    }
  }, [debouncedValue])

  return (
    <div className={'col-' + props.columnas + ' col-lg-' + columnas_sm + ' ' + extra_css}>
      {!!props.infoTitulo && !!props.infoBody && !!props.modal_info_css_class && (
        <ModalInfo
          color={props.color ? props.color : 'var(--naranja-abi)'}
          isOpen={AlertInfo}
          close={() => setAlertInfo(false)}
          titulo={props.infoTitulo}
          cssClass={`modal-info-image-loader ${props.modal_info_css_class}`}
        >
          {props.infoBody}
        </ModalInfo>
      )}
      <div className={'form-group'}>
        {props.titulo_externo && <span className="titulo-externo-item-field"> {props.titulo_externo}</span>}

        <div
          style={
            props.info_internal_icon || !!props.infoBody
              ? {
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center',
                  borderBottom: '1px solid #afafaf',
                }
              : {}
          }
          className={
            !!props.info_internal_icon || !!props.infoBody
              ? 'icono_interno form-control-container'
              : 'form-control-container'
          }
        >
          {props.tipo === 'numero' && (
            <input
              autoComplete={'off'}
              placeholder={props.titulo}
              disabled={deshabilitado}
              type={'text'}
              className={'form-control registro'}
              id={props.campo}
              value={props.valor !== '' ? numeral(props.valor).format('0,0') : ''}
              onBlur={(evt) => {
                let numero = numeral(evt.target.value).value()
                if (props.numero_minimo !== undefined && props.numero_minimo > numero) {
                  numero = props.numero_minimo
                  setDato(props.campo, numero)
                }
                if (set_dato_registro_store) {
                  let numero = numeral(evt.target.value).value()
                  setDatoRegistroStore(props.campo, numero)
                }
              }}
              onChange={(evt) => {
                let numero = numeral(evt.target.value).value()
                setDato(props.campo, numero)
              }}
            />
          )}
          {props.tipo === 'decimales' && (
            <input
              autoComplete={'off'}
              placeholder={props.titulo}
              disabled={deshabilitado}
              type={'text'}
              className={'form-control registro'}
              id={props.campo}
              value={props.valor}
              onBlur={(evt) => {
                setDato(props.campo, numeral(evt.target.value.replace('.', ',')).format('0.00'))
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, numeral(evt.target.value.replace('.', ',')).format('0.00'))
                }
              }}
              onChange={(evt) => {
                setDato(props.campo, evt.target.value)
              }}
            />
          )}
          {(props.tipo === 'texto' || props.tipo === 'email' || props.tipo === 'password') && (
            <input
              autoComplete={'off'}
              placeholder={props.titulo}
              disabled={deshabilitado}
              type={props.tipo}
              className={'form-control registro'}
              id={props.campo}
              value={props.valor !== undefined && props.valor !== null ? props.valor + '' : ''}
              onChange={(evt) => {
                setDato(props.campo, evt.target.value)
              }}
              onBlur={(evt) => {
                if (props.campo_blur !== undefined && props.campo_blur) {
                  setDatoBlur(props.campo)
                }
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, evt.target.value)
                }
              }}
            />
          )}
          {props.tipo === 'textarea' && (
            <textarea
              placeholder={props.titulo}
              disabled={deshabilitado}
              className={'form-control registro'}
              rows={textAreaRows}
              id={props.campo}
              onChange={(evt) => setDato(props.campo, evt.target.value)}
              value={props.valor !== undefined ? props.valor + '' : ''}
              onBlur={(evt) => {
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, evt.target.value)
                }
              }}
            />
          )}
          {props.tipo === 'ckeditor-mini' && (
            <CKEditor
              initData={props.valor}
              onChange={(evt: any) => {
                setDato(props.campo, evt.editor.getData())
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, evt.target.value)
                }
              }}
              config={{
                toolbar: [['Bold', 'Italic', 'Strike', 'Underline', '-', 'Subscript', 'Superscript']],
                placeholder: '',
              }}
            />
          )}
          {props.tipo === 'ckeditor-full' && (
            <CKEditor
              initData={props.valor}
              onChange={(evt: any) => {
                setDato(props.campo, evt.editor.getData())
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, evt.target.value)
                }
              }}
            />
          )}
          {props.tipo === 'fecha' && (
            <InputMask
              placeholder={props.titulo}
              disabled={deshabilitado}
              className={'form-control registro'}
              mask="99/99/9999"
              onChange={(date: any) => setDato(props.campo, date.target.value)}
              value={props.valor}
              onBlur={(date: any) => {
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, date.target.value)
                }
              }}
            />
          )}
          {props.tipo === 'hora' && (
            <InputMask
              placeholder={props.titulo}
              disabled={deshabilitado}
              className={'form-control registro'}
              mask="99:99"
              onChange={(date: any) => setDato(props.campo, date.target.value)}
              value={props.valor}
              onBlur={(date: any) => {
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, date.target.value)
                }
              }}
            />
          )}
          {props.tipo === 'telefono-simple' && (
            <InputMask
              placeholder={props.titulo}
              disabled={deshabilitado || FirebaseAutenticado}
              className={'form-control registro-phone-input'}
              mask="0\999-999-999"
              onChange={(date: any) => {
                setDato(props.campo, date.target.value)
              }}
              value={props.valor}
              onBlur={(date: any) => {
                if (set_dato_registro_store) {
                  setDatoRegistroStore(props.campo, date.target.value)
                }
              }}
            />
          )}
          {props.tipo === 'telefono' && (
            <div>
              <div className={'d-flex'}>
                <InputMask
                  placeholder={props.titulo}
                  disabled={deshabilitado}
                  className={'form-control registro-phone-input'}
                  mask="0\999-999-999"
                  onChange={(date: any) => {
                    setDato(props.campo, date.target.value)
                  }}
                  value={props.valor}
                  onBlur={(date: any) => {
                    if (set_dato_registro_store) {
                      setDatoRegistroStore(props.campo, date.target.value)
                    }
                  }}
                />
                {!FirebaseAutenticado && (
                  <div className={'d-flex justify-content-start align-items-center pl-0'}>
                    <button
                      className={
                        'd-flex btn justify-content-center ml-2 ' +
                        verificar_boton_clase +
                        ' py-1' +
                        (props.verificar_firebase_habilitado ? '' : ' disabled')
                      }
                      title={'Verificar número de teléfono'}
                      onClick={() => {
                        if (props.verificar_firebase_habilitado) {
                          setShowFirebaseMessageButton(false)
                          props.verificar_firebase_func()
                        } else {
                          setShowFirebaseMessageButton(true)
                        }
                      }}
                    >
                      <span>Verificar</span>
                    </button>
                  </div>
                )}
                {FirebaseAutenticado && (
                  <div className={'d-flex justify-content-start align-items-center pl-0'}>
                    <button
                      className={'d-flex btn justify-content-center  ' + verificar_boton_clase + ' py-1 ml-2'}
                      title={'Cambiar número de teléfono'}
                      onClick={() => props.verificar_firebase_reset()}
                    >
                      <span>Cambiar</span>
                    </button>
                  </div>
                )}
              </div>
              {(ShowFirebaseMessageButton || props.verificar_firebase_error) && (
                <div className={'text-danger mt-2'}>
                  {props.verificar_firebase_error
                    ? props.verificar_firebase_error
                    : 'Debe ingresar un número válido para poder verificar'}
                </div>
              )}
            </div>
          )}
          {props.tipo === 'select2' && props.opciones_select !== undefined && (
            <div>
              <Select
                isDisabled={deshabilitado}
                value={props.opciones_select.filter((opc: any) => opc.value === props.valor)[0]}
                onChange={selectOnChange}
                options={props.opciones_select}
                placeholder={
                  props.select_placeholder !== undefined
                    ? props.select_placeholder
                    : 'Seleccionar ' + props.titulo.toLocaleLowerCase()
                }
                isClearable
                isMulti={props.select_multiple}
                noOptionsMessage={
                  props.select_no_options_text ? (obj) => props.select_no_options_text || null : undefined
                }
                theme={(theme) => ({
                  ...theme,
                  colors: {
                    ...theme.colors,
                    primary: '#303AB2',
                    neutral50: 'black',
                  },
                })}
              />
            </div>
          )}
          {props.tipo === 'select2async' && props.opciones_select !== undefined && Value !== null && (
            <div className={'asyncselect'}>
              <AsyncSelect
                ref={selectRef}
                //@ts-ignore
                styles={customStyles}
                value={props.valor && props.valor.value ? props.valor.value : ''}
                onChange={selectOnChange}
                loadingMessage={() => 'Cargando..'}
                loadOptions={(val: string, callback: any) => {
                  setValue(val)
                  callbackRef.current = callback
                }}
                defaultOptions={props.opciones_select(null)}
                placeholder={SelectedValue || selectPlaceHolder}
                isClearable
                noOptionsMessage={
                  props.select_no_options_text ? (obj) => props.select_no_options_text || null : undefined
                }
                theme={(theme) => ({
                  ...theme,
                  colors: {
                    ...theme.colors,
                    primary: '#929292',
                    neutral50: '#929292',
                  },
                })}
                onBlur={() => {
                  if (props.campo_blur !== undefined && props.campo_blur) {
                    setDatoBlur(props.campo)
                  }
                }}
              />
            </div>
          )}
          {props.tipo === 'boolean' && (
            <div>
              <input
                autoComplete={'off'}
                disabled={deshabilitado}
                type={'checkbox'}
                id={props.campo}
                checked={props.valor === true}
                onClick={() => {
                  setDato(props.campo, !props.valor)
                  if (set_dato_registro_store) {
                    setDatoRegistroStore(props.campo, !props.valor)
                  }
                }}
              />
              <label
                id={props.campo}
                onClick={() => {
                  setDato(props.campo, !props.valor)
                }}
              >
                {props.titulo}
              </label>
            </div>
          )}
          {props.info_internal_icon && <>{props.info_internal_icon}</>}
          {!!props.infoTitulo && !!props.infoBody && (
            <a onClick={() => setAlertInfo(true)}>
              <img style={{ height: '30px', width: '30px' }} src="assets/icon/info.png" />
            </a>
          )}
        </div>

        {props.help_text !== undefined && <small>{props.help_text}</small>}
        {props.error && <div className={'text-danger'}>{props.error}</div>}
      </div>
    </div>
  )
})

export default AdminItemFieldRegistro
