import { IonModal, IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { PEDIDO_ESTADOS, TIPO_CHAT, TIPO_ITINERARIO } from '../../constants'
import { useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { ItinerarioApi, Mensaje as MensajeApi, Pedido as PedidoApi } from '../../services/api'
import Chat from './Chat'
import './ChatModal.css'
import Header from './Header'

const { WSS_BASE_URL } = require('../../config/env')

type ChatModalProps = {
  idPedido?: number
  pedido?: PedidoApi
  className?: string
  containerStyle?: React.CSSProperties
  botonExtra?: any
  color?: string
  ocultarHeader?: boolean
}

export const ChatPedido: React.FC<ChatModalProps> = observer((props) => {
  const [Pedido, setPedido] = useState<PedidoApi>()
  const [Titulo, setTitulo] = useState('')
  const [Loading, setLoading] = useState(true)
  const [subTitulo, setSubTitulo] = useState('')
  const [Telefono, setTelefono] = useState<string | null>('')
  const [ImagenPerfil, setImagenPerfil] = useState('assets/person.png')
  const rootStore = useStores()
  const [WSUrl, setWSUrl] = useState<string | null>()
  const { loginStore, transportistaStore } = rootStore
  const [Mensajes, setMensajes] = useState<MensajeApi[]>([])
  const [Color, setColor] = useState('')
  const [ItinerarioActivo, setItinerarioActivo] = useState<ItinerarioApi | null>()
  const addToast = useToaster()

  const obtenerPedido = (idPedido: any) => {
    setLoading(true)
    rootStore.environment.api.getPedido(idPedido).then((result) => {
      if (result.kind === 'ok') {
        setPedido(result.pedido)
      }
    })
  }

  useEffect(() => {
    if (!!props.pedido) {
      setPedido(props.pedido)
      return
    }
    obtenerPedido(props.idPedido)
  }, [])

  useEffect(() => {
    setItinerarioActivo(transportistaStore.itinerarioActivo)
  }, [transportistaStore.itinerarioActivo])

  useEffect(() => {
    if (!Pedido) {
      return
    }

    const idFabricanteActual = loginStore.fabricante && loginStore.fabricante.id
    const esElTransportista = transportistaStore.hojaRutaActiva?.pedido?.id === Pedido.id
    const esElFabricante =
      (!!Pedido.fabricante_id && idFabricanteActual === Pedido.fabricante_id) ||
      (!!Pedido.fabricante?.id && idFabricanteActual === Pedido.fabricante.id)
    const esElCliente = Pedido.usuario.id === loginStore.id

    if (esElTransportista) {
      setColor('#ff6f33')
    } else if (esElFabricante) {
      setColor('#303AB2')
    } else if (esElCliente) {
      setColor('#ff6f33')
    } else {
      addToast('Ocurrió un error al configurar el chat. Código: 100', 'warning')
    }

    setSubTitulo('Id#' + Pedido.id)

    if (esElTransportista && ItinerarioActivo) {
      if (ItinerarioActivo.tipo === TIPO_ITINERARIO.ENTREGA) {
        recuperarMensajes(Pedido.id, TIPO_CHAT.CLIENTE_DRIVER, 0)
        setTitulo(Pedido.usuario?.first_name + ' ' + Pedido.usuario?.last_name)
        setImagenPerfil(Pedido.usuario.imagen_perfil ? Pedido.usuario.imagen_perfil : ImagenPerfil)
        setWSUrl(`${WSS_BASE_URL}/chat/cliente-driver/${Pedido.id}/?token=${loginStore.token}`)
        setTelefono(ItinerarioActivo.telefono_itinerario ? ItinerarioActivo.telefono_itinerario : null)
        return
      } else if (
        ItinerarioActivo.tipo === TIPO_ITINERARIO.RETIRO &&
        ItinerarioActivo.fabricante &&
        ItinerarioActivo.fabricante_id
      ) {
        recuperarMensajes(Pedido.id, TIPO_CHAT.FABRICANTE_DRIVER, ItinerarioActivo.fabricante_id)
        setImagenPerfil(
          ItinerarioActivo.fabricante_imagen_perfil ? ItinerarioActivo.fabricante_imagen_perfil : ImagenPerfil,
        )
        setTitulo(ItinerarioActivo.fabricante)
        setTelefono(ItinerarioActivo.telefono_itinerario ? ItinerarioActivo.telefono_itinerario : null)
        setWSUrl(
          `${WSS_BASE_URL}/chat/fabricante-driver/${ItinerarioActivo.fabricante_id}/${Pedido.id}/?token=${loginStore.token}`,
        )

        return
      }
    } else if (esElFabricante && loginStore.fabricante_id) {
      recuperarMensajes(Pedido.id, TIPO_CHAT.FABRICANTE_DRIVER, loginStore.fabricante_id)
      setTitulo(Pedido.transportista?.usuario.first_name + ' ' + Pedido.transportista?.usuario.last_name)
      setImagenPerfil(
        Pedido.transportista?.usuario.imagen_perfil ? Pedido.transportista?.usuario.imagen_perfil : ImagenPerfil,
      )
      setTelefono(Pedido.transportista?.usuario.nro_telefono)
      setWSUrl(
        `${WSS_BASE_URL}/chat/fabricante-driver/${loginStore.fabricante_id}/${Pedido.id}/?token=${loginStore.token}`,
      )
      return
    } else if (esElCliente) {
      recuperarMensajes(Pedido.id, TIPO_CHAT.CLIENTE_DRIVER, 0)
      setTitulo(Pedido.transportista?.usuario.first_name + ' ' + Pedido.transportista?.usuario.last_name)
      setImagenPerfil(
        Pedido.transportista?.usuario.imagen_perfil ? Pedido.transportista?.usuario.imagen_perfil : ImagenPerfil,
      )
      setTelefono(Pedido.transportista?.usuario.nro_telefono)
      setWSUrl(`${WSS_BASE_URL}/chat/cliente-driver/${Pedido.id}/?token=${loginStore.token}`)
      return
    } else {
      addToast('Ocurrió un error al configurar el chat', 'warning')
    }
    setLoading(false)
  }, [Pedido, ItinerarioActivo])

  const recuperarMensajes = (idPedido: number, tipoChat: number, fabricanteId: number) => {
    setLoading(true)
    rootStore.environment.api.getMensajesChat(idPedido, tipoChat, fabricanteId).then((result) => {
      if (result.kind == 'ok') {
        setLoading(false)
        const mensajes = [...result.data]
        setMensajes(mensajes)
      }
    })
  }

  return (
    <div
      className={props.className ? 'd-flex flex-column ' + props.className : 'd-flex flex-column'}
      style={props.containerStyle ? props.containerStyle : {}}
    >
      {!props.ocultarHeader && (
        <Header
          titulo={Titulo}
          sub_titulo={subTitulo}
          mostrarBarra={false}
          color={props.color ? props.color : Color}
          imagenPerfil={
            <div className={'foto-perfil-container-chat-modal'}>
              <img id={'foto-perfil-modal'} alt="" src={ImagenPerfil} />
            </div>
          }
          botonDerecho={
            <div>
              {props.botonExtra ? props.botonExtra : null}
            </div>
          }
        />
      )}

      {(Loading || !WSUrl) && (
        <div className="d-flex flex-grow-1 align-self-stretch justify-content-center align-items-center">
          <IonSpinner />
        </div>
      )}
      {!Loading && WSUrl && (
        <Chat
          wsUrl={WSUrl}
          mensajes={Mensajes}
          finalizado={Pedido?.estado === PEDIDO_ESTADOS.entregado || Pedido?.estado === PEDIDO_ESTADOS.incidente}
        />
      )}
    </div>
  )
})
export default ChatPedido
