import { IonContent, IonHeader, IonItem, IonModal, IonSpinner, IonTitle, IonToolbar } from '@ionic/react'
import React, { useEffect, useState } from 'react'
import { observer } from 'mobx-react-lite'
import { useStores } from '../../models/root-store/root-store-context'
import './HomeTransportistaFooter.css'
import '../../pages/Transportista/OportunidadesTransportista.css'
import * as FIcon from 'react-feather'
import { HojaRutaApi, ItinerarioApi } from '../../services/api'
import { RUTAS_APP, TIPO_ITINERARIO, TIPO_ITINERARIO_STR, TIPO_ITINERARIO_VERBO_STR } from '../../constants'
import { VerMapaIcono } from './IconosTransportista'
import { useHistory } from 'react-router'

type HomeTransportistaFooterProps = {
  loading: boolean
  onEjecutarItinerarioClicked: any
  onAbandonarItinerarioCliked: any
  buscandoOportudidad?: boolean
  oportunidades?: HojaRutaApi[]
}

export const HomeTransportistaFooter: React.FC<HomeTransportistaFooterProps> = observer((props) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Online, setOnline] = useState(true)
  const [FooterOpen, setFooterOpen] = useState(false)
  const [HojaRuta, setHojaRuta] = useState<HojaRutaApi | null>()
  const [Itinerario, setItinerario] = useState<ItinerarioApi | null>()
  const [ModalInfo, setModalInfo] = useState(false)
  const [Oportunidades, setOportunidades] = useState([] as HojaRutaApi[])
  const [Loading, setLoading] = useState(false)
  const history = useHistory()

  useEffect(() => {
    setItinerario(transportistaStore.itinerarioActivo)
  }, [transportistaStore.itinerarioActivo])

  useEffect(() => {
    setHojaRuta(transportistaStore.hojaRutaActiva)
  }, [transportistaStore.hojaRutaActiva])

  // const changeOnline = (newValue: boolean) => {
  //   setOnline(newValue)
  //   setFooterOpen(false)
  //   props.onFooterOpened(false)
  // }

  const obtenerOportunidades = () => {
    setLoading(true)
    rootStore.environment.api.oportunidades().then((result) => {
      if (result.kind === 'ok') {
        setLoading(false)
        setOportunidades(result.data)
      }
    })
  }

  const getFooterContent = () => {
    if (props.loading) {
      return (
        <div className="d-flex justify-content-center align-items-center">
          <IonSpinner name="crescent" className={'loadingSpinner'} />
        </div>
      )
    }
    if (!FooterOpen && !Itinerario) {
      return (
        <>
          <div
            className="col buscando-oportunidad justify-content-start d-flex align-items-center"
            style={{ gap: '5vw' }}
            onClick={() => {
              setFooterOpen(true)
              obtenerOportunidades()
            }}
          >
            <div>
              <FIcon.ChevronUp size={40} />
            </div>{' '}
            {props.buscandoOportudidad && <strong>Buscando oportunidades</strong>}
          </div>
        </>
      )
    }
    if (FooterOpen) {
      // FOOTER OPEN CONTENT
      return (
        <>
          <div className="d-flex flex-column justify-content-start" style={{ width: '100%' }}>
            <div className="row" style={{ width: '100%' }}>
              <div
                className="col-12 buscando-oportunidad justify-content-start d-flex align-items-center"
                style={{ gap: '5vw', width: '100%' }}
                onClick={() => setFooterOpen(false)}
              >
                <div>
                  <FIcon.ChevronDown size={32} />
                </div>{' '}
                {props.buscandoOportudidad && <strong>Buscando oportunidades</strong>}
              </div>
            </div>
            {Loading && (
              <div className="d-flex align-items-center justify-content-center" style={{ height: '100%' }}>
                <IonSpinner />
              </div>
            )}
            {!Loading && !!Oportunidades && Oportunidades?.length > 0 && (
              <IonContent>
                {Oportunidades.map((item: HojaRutaApi) => {
                  return (
                    <div className={'oportunidades-list-item col-12 pt-2'} key={'oportunidad-' + item.id}>
                      <div className="datos-empresa" style={{ lineHeight: '0.8rem' }}>
                        <div className={'mb-1'} style={{ fontWeight: 'bold', fontSize: '0.8rem' }}>
                          {item.inicio.fabricante}
                          {item.inicio.direccion_itinerario?.nombre && ', '}
                          {item.inicio.direccion_itinerario?.nombre}
                        </div>
                        <p>
                          {item.inicio.direccion_itinerario?.calle_principal} N॰{' '}
                          {item.inicio.direccion_itinerario?.numero}
                        </p>
                        <div className="datos-punto-itinerario">
                          <img src={'assets/icon/oportunidades.png'} />
                          <strong>Punto de retiro: </strong>
                          <span>
                            {item.inicio.tiempo_en_transito + item.inicio.tiempo_parada_calculado} min -{' '}
                            {item.inicio.distancia} km
                          </span>
                          <br />
                          <img src={'assets/icon/arrival_flag.png'} />
                          <strong>Punto de entrega: </strong>
                          <span>
                            {item.fin.tiempo_en_transito + item.fin.tiempo_parada_calculado} min - {item.fin.distancia}{' '}
                            km
                          </span>
                        </div>
                      </div>
                      <div>
                        <div
                          onClick={() => {
                            let currentUrlParams = new URLSearchParams(window.location.search)
                            currentUrlParams.set('oportunidad', String(item.id))
                            history.push(RUTAS_APP.HOME + '?' + currentUrlParams.toString())
                            setFooterOpen(false)
                          }}
                        >
                          <VerMapaIcono />
                        </div>
                      </div>
                    </div>
                  )
                })}
              </IonContent>
            )}

            {!Loading && !!Oportunidades && Oportunidades?.length === 0 && (
              <div className="d-flex justify-content-center align-content-center">
                <p className="text-center">Todavía no se encuentran oportunidades.</p>
              </div>
            )}
          </div>
        </>
      )
    } else {
      // FOOTER CLOSED CONTENT
      return (
        <div>
          {HojaRuta && Itinerario && (
            <>
              <div className="container flex-fill">
                <div className="row align-items-start">
                  <div className="col-6">
                    <button
                      className="btn-principal-transportista"
                      style={{ backgroundColor: 'var(--danger)' }}
                      onClick={() => props.onAbandonarItinerarioCliked(Itinerario)}
                    >
                      Abandonar
                    </button>
                  </div>
                  <div className="col-6">
                    <button
                      className="btn-principal-transportista "
                      style={{ backgroundColor: 'var(--success)' }}
                      onClick={() => props.onEjecutarItinerarioClicked(Itinerario)}
                    >
                      Llegué
                    </button>
                  </div>
                </div>
              </div>
            </>
          )}
        </div>
      )
    }
  }

  return (
    <div className={FooterOpen ? 'footer-home-transportista open' : 'footer-home-transportista closed'}>
      {Itinerario && (
        <IonModal
          isOpen={ModalInfo}
          onDidDismiss={() => setModalInfo(false)}
          swipeToClose={true}
          backdropDismiss={true}
        >
          <IonHeader>
            <IonToolbar>
              <IonTitle>{Itinerario.tipo === TIPO_ITINERARIO.ENTREGA ? 'Entrega' : 'Retiro'}</IonTitle>
            </IonToolbar>
          </IonHeader>
          <IonContent>
            <IonItem>
              <span className={'py-3'}>
                <strong>Ciudad: </strong>
                {Itinerario.direccion_itinerario && Itinerario.direccion_itinerario.ciudad_nombre}
              </span>
            </IonItem>
            <IonItem>
              <span className={'py-3'}>
                <strong>Calle principal: </strong>
                {Itinerario.direccion_itinerario && Itinerario.direccion_itinerario.calle_principal}
              </span>
            </IonItem>
            <IonItem>
              <span className={'py-3'}>
                <strong>Calle secundaria: </strong>
                {Itinerario.direccion_itinerario && Itinerario.direccion_itinerario.calle_secundaria}
              </span>
            </IonItem>
            <IonItem>
              <span className={'py-3'}>
                <strong>Número: </strong>
                {Itinerario.direccion_itinerario && Itinerario.direccion_itinerario.numero}
              </span>
            </IonItem>
            <IonItem>
              <span className={'py-3'}>
                <strong>Referencia: </strong>
                {Itinerario.direccion_itinerario && Itinerario.direccion_itinerario.referencia}
              </span>
            </IonItem>
          </IonContent>
          <div style={{ padding: 10 }}>
            <div className={'row'}>
              <div className={'col-12'}>
                <button className={'btn btn-outline-orange btn-block'} onClick={() => setModalInfo(false)}>
                  Cerrar
                </button>
              </div>
            </div>
          </div>
        </IonModal>
      )}
      {getFooterContent()}
    </div>
  )
})

export default HomeTransportistaFooter
