import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'

import { useToaster } from '../helpers-ts'
import { useStores } from '../models/root-store/root-store-context'
import { ModalDireccion } from './ModalDireccion'
import { SelectorModal } from './SelectorModal'

interface ModalDireccionPredeterminadaProps {
  visible: boolean
  onDismiss: any
  setDireccionPredeterminada: any
}

export const ModalDireccionPredeterminada: React.FC<ModalDireccionPredeterminadaProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Direccion, setDireccion] = useState(null as any)
  const [ModalDireccionVisible, setModalDireccionVisible] = useState(false)
  const addToast = useToaster()

  useEffect(() => {
    setDireccion(loginStore.direcciones.find((e) => e.default))
  }, [])

  const seleccionarDireccion = (id: any) => {
    const direccionDefecto = loginStore.direcciones.filter((direccion) => {
      return direccion.id === id
    })[0]
    setDireccion(direccionDefecto)
  }

  const dataSelectorModal = (data: any) => {
    loginStore.direcciones.forEach((direccion) => {
      if (data[0] === direccion.id) {
        direccion.toggleDefault()
      } else if (direccion.default) {
        direccion.toggleDefault()
      }
    })
    guardar()
    seleccionarDireccion(data[0])
    props.setDireccionPredeterminada('a')
  }

  const guardar = () => {
    loginStore.direcciones.forEach((direccion) => {
      const Direcccion = rootStore.environment.api.guardarDireccion(direccion).then((result: any) => {
        if (result.kind === 'ok') {
        } else if (result.kind === 'bad-data') {
          console.log(result.errors)
        }
      })
    })
  }

  const obtenerDatosCliente = () => {
    rootStore.environment.api.getCliente(loginStore.id).then((result) => {
      if (result.kind === 'ok') {
        const datos = { ...result.cliente } as any
        loginStore.setDirecciones(datos.direcciones)
        loginStore.setDatosFacturacion(datos.datos_facturacion)
      } else {
        addToast('Ocurrió un error al actualizar las direcciones', 'error')
      }
    })
  }

  const direccionCreada = () => {
    obtenerDatosCliente()
  }

  return (
    <>
      {ModalDireccionVisible && <ModalDireccion visible={true} successCallback={direccionCreada} />}
      <SelectorModal
        titulo={'Dirección'}
        titulo_label={'Seleccioná la dirección'}
        listado_elementos={loginStore.direcciones}
        listado_elementos_seleccionados={Direccion ? Direccion : []}
        datos_filtrados={dataSelectorModal}
        multiple={false}
        llave_label_opcion={'nombre'}
        ocultar_input={true}
        mostrar_modal={props.visible}
        mostrar_modal_cambio_estado={(visible: any) => props.onDismiss(visible)}
        boton_extra_text={'dirección'}
        boton_extra_funcion={() => {
          setModalDireccionVisible(false)
          setTimeout(() => setModalDireccionVisible(true), 100)
        }}
      />
    </>
  )
})
export default ModalDireccionPredeterminada
