import { Camera, CameraResultType, CameraSource } from '@capacitor/camera'
import { Capacitor } from '@capacitor/core'
import { IonButton, IonContent, IonHeader, IonModal, IonSpinner, IonTitle, IonToolbar } from '@ionic/react'
import AttachFileIcon from '@material-ui/icons/AttachFile'
import ImageIcon from '@material-ui/icons/Image'
import PhotoCameraIcon from '@material-ui/icons/PhotoCamera'
import RoomIcon from '@material-ui/icons/Room'
import SendIcon from '@material-ui/icons/Send'
import { observer } from 'mobx-react-lite'
import { SnapshotOut } from 'mobx-state-tree'
import React, { useEffect, useRef, useState } from 'react'
import useWebSocket from 'react-use-websocket'
import { SMap } from '../../components'
import { MODO_USO_APP, TIPO_CHAT, TIPO_ITINERARIO } from '../../constants'
import { comprimirImagen } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

import { Mensaje as MensajeApi } from '../../services/api'
import './Chat.css'

const moment = require('moment-timezone')
const audioChat = new Audio('assets/chat.mp3')

type ChatProps = { wsUrl: string; mensajes?: MensajeApi[]; finalizado?: boolean }

export const Chat: React.FC<ChatProps> = observer((props, { router }) => {
  const rootStore = useStores()
  const { loginStore, transportistaStore, chatStore } = rootStore
  const [MenuAdjuntar, setMenuAdjuntar] = useState(false)
  const [ModalUbicacion, setModalUbicacion] = useState(false)
  const [UbicacionEnviar, setUbicacionEnviar] = useState(null as any)
  const [LoadingMensajes, setLoadingMensajes] = useState(false)

  const inputMensajeRef = useRef(null)
  const [Mensajes, setMensajes] = useState<MensajeApi[]>()
  const [PorcentajeCompresion, setPorcentajeCompresion] = useState(0)
  const [InputComprimiendo, setInputComprimiendo] = useState('')
  const Content = useRef<HTMLIonContentElement | null>(null)
  let ultimaFecha: any = null as any

  useEffect(() => {
    if (props.mensajes) {
      setMensajes([...props.mensajes])
      setTimeout(() => Content.current?.scrollToBottom(100), 1000)
    }
  }, [props.mensajes])

  const comprimiendoImagen = (porcentaje: number) => {
    setPorcentajeCompresion(porcentaje)
    if (porcentaje === 100) {
      setTimeout(() => {
        setPorcentajeCompresion(0)
        setInputComprimiendo('')
      }, 500)
    } else {
    }
  }

  const mandarFotoCamara = async () => {
    try {
      let image = await Camera.getPhoto({
        quality: 90,
        allowEditing: false,
        source: CameraSource.Camera,
        resultType: CameraResultType.DataUrl,
      })

      const dataUrl: any = await image.dataUrl
      const data = await fetch(dataUrl)

      const blob = await data.blob()
      const imagenComprimida = await comprimirImagen(blob, 'imagen', comprimiendoImagen)

      const result = await new Promise((callback) => {
        const reader = new FileReader()
        reader.onload = () => {
          callback(reader.result)
        }
        reader.readAsDataURL(imagenComprimida)
      })
      const imagenComprimidaBase64 = await toBase64(result)
      const msg = await {
        fecha: moment().toISOString(),
        texto: '',
        usuario: loginStore.id,
        media: result,
      }
      await sendJsonMessage(msg)
    } catch (err) {
      console.log(err)
    }
  }

  const toBase64 = (data: any) => {
    data = `data:image/jpeg;base64,${data}`
  }

  const mandarFotoGaleria = async () => {
    try {
      let image = await Camera.getPhoto({
        quality: 90,
        allowEditing: false,
        source: CameraSource.Photos,
        resultType: CameraResultType.DataUrl,
      })

      const dataUrl: any = await image.dataUrl
      const data = await fetch(dataUrl)

      const blob = await data.blob()
      const imagenComprimida = await comprimirImagen(blob, 'imagen', comprimiendoImagen)

      const result = await new Promise((callback) => {
        const reader = new FileReader()
        reader.onload = () => {
          callback(reader.result)
        }
        reader.readAsDataURL(imagenComprimida)
      })
      const imagenComprimidaBase64 = await toBase64(result)
      const msg = await {
        fecha: moment().toISOString(),
        texto: '',
        usuario: loginStore.id,
        media: result,
      }
      await sendJsonMessage(msg)
    } catch (err) {
      console.log(err)
    }
  }

  const enviarUbicacion = async () => {
    setModalUbicacion(false)
    if (!UbicacionEnviar) {
      return
    }
    const msg = {
      fecha: moment().toISOString(),
      texto: `location;${UbicacionEnviar.lat};${UbicacionEnviar.lng}`,
      usuario: loginStore.id,
    }
    sendJsonMessage(msg)
  }

  const InputRespuesta: any = () => {
    if (loginStore.modo_uso_app === MODO_USO_APP.transportista) {
      return (
        <div className="predeterminados-container-trasportista">
          {chatStore.mensajesPredeterminadosDriver.map((message, index) => {
            return (
              <div className="mensaje-predeterminado-container" key={index}>
                <div
                  className="mensaje-predeterminado-trasportista"
                  onClick={() => enviarMensajePredeterminado(message.mensaje)}
                >
                  {message.mensaje}
                </div>
              </div>
            )
          })}
        </div>
      )
    }
    return (
      <>
        <div id="cliente-predeterminados-container">
          {chatStore.getMensajesPredeterminadoCliente().map((mensaje) => (
            <div
              key={mensaje.id}
              className="mensaje-predeterminado-cliente"
              onClick={() => enviarMensajePredeterminado(mensaje.mensaje)}
            >
              {mensaje.mensaje}
            </div>
          ))}
        </div>

        <div id="respuesta-container">
          <div className="adjunto" onClick={() => setMenuAdjuntar(!MenuAdjuntar)}>
            <AttachFileIcon />
          </div>
          <div id="menu-adjuntar" className={MenuAdjuntar ? 'active' : ''}>
            <div id="adjuntar-imagen" onClick={mandarFotoGaleria}>
              <ImageIcon fontSize={'medium'} />
            </div>
            {Capacitor.getPlatform() !== 'web' && (
              <div id="adjuntar-foto" onClick={mandarFotoCamara}>
                <PhotoCameraIcon fontSize={'medium'} />
              </div>
            )}
            <div
              id="adjuntar-ubicacion"
              onClick={() => {
                setMenuAdjuntar(false)
                setModalUbicacion(true)
              }}
            >
              <RoomIcon fontSize={'medium'} />
            </div>
          </div>
          <input
            ref={inputMensajeRef}
            type={'text'}
            className="input-texto"
            placeholder={'Escriba su mensaje'}
            onKeyPress={(evt) => {
              if (evt.key === 'Enter') {
                enviarMensaje()
              }
            }}
          />
          <div className={'enviar'} onClick={enviarMensaje}>
            <SendIcon style={{ color: '#96CE4E', marginLeft: 3 }} />
          </div>
        </div>
      </>
    )
  }

  const { sendMessage, sendJsonMessage, lastMessage, lastJsonMessage, readyState, getWebSocket } = useWebSocket(
    props.wsUrl,
    {
      onOpen: () => console.log('opened'),
      onMessage: (evt) => {
        const msg = JSON.parse(evt.data)
        if (msg.usuario !== loginStore.id) {
          audioChat.play()
        }
        setTimeout(() => Content.current?.scrollToBottom(100), 300)
      },
      //Will attempt to reconnect on all close events, such as server shutting down
      shouldReconnect: (closeEvent) => true,
    },
  )

  useEffect(() => {
    if (!lastMessage || !lastMessage.data) {
      return
    }
    try {
      const mensajes = !!Mensajes ? [...Mensajes] : ([] as MensajeApi[])
      const data = JSON.parse(lastMessage.data)

      // const msg = { mensaje: data.mensaje, fecha: data.fecha, emisor: data.emisor, imagen: data.imagen }
      const msg: MensajeApi = {
        texto: data.texto,
        fecha: data.fecha,
        usuario: data.usuario,
        media: data.media,
        tipo: data.tipo,
      }
      mensajes.push(msg)
      setMensajes(mensajes)
    } catch (err) {
      console.log('err', err)
    }
  }, [lastMessage])

  useEffect(() => {
    console.log('readyState', readyState)
  }, [readyState])

  const enviarMensaje = () => {
    // @ts-ignore
    const textoEnviar = inputMensajeRef.current.value
    if (!textoEnviar || !textoEnviar.trim()) {
      return
    }
    const msg = { fecha: moment().toISOString(), texto: textoEnviar, usuario: loginStore.id }
    sendJsonMessage(msg)
    // @ts-ignore
    setTimeout(() => inputMensajeRef.current.focus(), 500)
  }

  const enviarMensajePredeterminado = (mensaje: any) => {
    const msg = { fecha: moment().toISOString(), texto: mensaje, usuario: loginStore.id }
    sendJsonMessage(msg)
  }

  const FechaCorte = (msg: MensajeApi) => {
    const lastDate = moment(ultimaFecha)
    const currentDate = moment(msg.fecha)
    ultimaFecha = msg.fecha
    if (ultimaFecha !== null) {
      if (moment(lastDate).isBefore(currentDate, 'day')) {
        return (
          <div key={msg.id} className={'corte-fecha'}>
            {currentDate.format('DD/MM/YYYY')}
          </div>
        )
      }
    }

    return null
  }

  const renderMensaje = (msg: MensajeApi) => {
    if (!msg) {
      return null
    }

    const fecha = FechaCorte(msg)
    let tipo = 'texto'
    let mensaje: any = !!msg.texto ? msg.texto : ''
    let imagen = null
    if (mensaje.match(/^location;[-0-9\.]+;[-0-9\.]+$/gm)) {
      tipo = 'ubicacion'
      const lat = mensaje.split(';')[1]
      const lng = mensaje.split(';')[2]

      mensaje = (
        <a href={`https://www.google.com/maps/search/?api=1&query=${lat},${lng}`} target={'_blank'}>
          <SMap
            lat={lat}
            lng={lng}
            bloquear_interaccion
            center={{ lat, lng }}
            zoom={15}
            style={{ height: 100, width: '100%', borderRadius: 10 }}
          />
        </a>
      )
    }
    if (msg.media) {
      imagen = (
        <a href={msg.media}>
          <img src={msg.media} alt={'imagen'} style={{ width: 230, height: 'auto', borderRadius: 10 }} />
        </a>
      )
    }

    if (fecha) {
      return fecha
    }

    return (
      <div className={`mensaje ${tipo}` + (msg.usuario === loginStore.id ? ' propio' : '')} key={'msg' + msg.fecha}>
        {imagen}
        {mensaje}
        <span className={'hora'}>{moment(msg.fecha).format('HH:mm')}</span>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 23.64 14.6">
          <g id="Layer_2" data-name="Layer 2">
            <g id="Capa_1" data-name="Capa 1">
              <polyline className="cls-1" points="16.26 0.15 0.54 14.39 23.64 14.39" />
            </g>
          </g>
        </svg>
      </div>
    )
  }

  return (
    <div className={'chat-container'}>
      <IonModal
        isOpen={ModalUbicacion}
        id={'modal-ubicacion'}
        onDidDismiss={() => setModalUbicacion(false)}
        swipeToClose={true}
        backdropDismiss={true}
        cssClass={'modal-ubicacion'}
        presentingElement={router || undefined}
      >
        <IonHeader translucent={true}>
          <IonToolbar>
            <IonTitle>Enviar ubicación</IonTitle>
            <IonButton onClick={() => setModalUbicacion(false)} slot="end">
              Cancelar
            </IonButton>
          </IonToolbar>
        </IonHeader>
        <IonContent fullscreen>
          <SMap
            lat={rootStore.latitud || -25.30066}
            lng={rootStore.longitud || -57.63591}
            zoom={14}
            full_screen
            ubicacion={(lat: any, lng: any) => {
              setUbicacionEnviar({ lat, lng })
            }}
          />
          <IonButton onClick={() => enviarUbicacion()} id={'enviar-ubicacion'}>
            Enviar
          </IonButton>
        </IonContent>
      </IonModal>

      <div className="scroll-container" style={{ flex: 1 }}>
        <IonContent id="mensajes-container" scrollEvents={true} ref={(ref) => (Content.current = ref)}>
          <div className="py-3">
            {LoadingMensajes && <IonSpinner name="crescent" id="chat-loading-spinner" />}
            {!LoadingMensajes && !!Mensajes && Mensajes.length > 0 && (
              <div id="mensajes">{Mensajes.map((msg: MensajeApi, idx) => renderMensaje(msg))}</div>
            )}
          </div>
        </IonContent>
      </div>
      {!props.finalizado && <InputRespuesta />}
    </div>
  )
})

export default Chat
