import React from 'react';
import {observer} from "mobx-react-lite";
import {IonSpinner} from "@ionic/react";

type LoaderAdminProps = {
  visible: any
  max_height?: string
};

export const LoaderAdmin: React.FC<LoaderAdminProps> = observer((props) => {
  const visible = props.visible
  const max_height = props.max_height || "100vh"
  return visible && <div className="LoaderAdmin-wrapper" style={{display: "flex",  height: max_height, overflowY: "scroll"}}>
    <div className="container-fluid">
      <div className={'d-flex justify-content-center loader'}>
        <IonSpinner  name="crescent" className={'loadingSpinner'}/>
      </div>
    </div>
  </div>
});

export default LoaderAdmin;
