import React from 'react'
import { observer } from 'mobx-react-lite'
import { useHistory } from 'react-router-dom'
import * as FIcon from 'react-feather'
import './HeaderRegistro.css'
import { useStores } from '../models/root-store/root-store-context'
import { RUTAS, RUTAS_APP, RUTAS_APP_FABRICANTE, RUTAS_WEB_TRANSPORTISTA } from '../constants'
import { Capacitor } from '@capacitor/core'
import { ICONOS } from '../helpers-ts/iconos'

type HeaderRegistroProps = {
  botonIzquierdo?: any
  botonDerecho?: any
  color?: string
  titulo?: string
  sub_titulo?: string
  accion_boton_centro?: any
  accion_boton_izquierdo?: any
}

export const HeaderRegistro: React.FC<HeaderRegistroProps> = observer((props) => {
  const history = useHistory()
  const rootStore = useStores()
  const { loginStore } = rootStore
  const accionBotonIzquierdo = props.accion_boton_izquierdo ? props.accion_boton_izquierdo : () => history.goBack()
  const botonIzquierdo = props.botonIzquierdo || (
    <div style={{ padding: 5, paddingLeft: 10 }} onClick={() => accionBotonIzquierdo()}>
      <FIcon.ChevronLeft size={26} style={{ color: '#FFF' }} />
    </div>
  )
  const botonDerecho = props.botonDerecho || <div style={{ width: 46 }} />
  const color = props.color || 'var(--azul-fabricante)'
  const titulo = props.titulo || ''
  const sub_titulo = props.sub_titulo || ''

  return (
    <div style={{ height: 100, minHeight: 100 }}>
      <div className={'registro-cliente-header-main-container'} style={{ backgroundColor: color }}>
        <div className={'row mx-0 px-0'}>
          <div className={'col-2 text-left px-1 cpointer'}>{botonIzquierdo}</div>
          <div className={'col-8 text-center '}>
            <a
              className="logo-abi-fabricante cpointer"
              onClick={() => {
                if (loginStore.isLoggedIn && loginStore.modo_uso_app === 30) {
                  if (Capacitor.getPlatform() === 'android' || Capacitor.getPlatform() === 'ios') {
                    history.push(RUTAS_APP.HOME)
                  } else {
                    history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)
                  }
                } else if (loginStore.isLoggedIn && loginStore.modo_uso_app === 20) {
                  history.push(RUTAS_APP_FABRICANTE.PEDIDOS_PENDIENTES)
                } else {
                  history.push(RUTAS.ROOT)
                }
              }}
            >
              {ICONOS.abi}
            </a>
          </div>
          <div className={'col-2 text-right px-1 pt-2 cpointer'}>{botonDerecho}</div>
        </div>
        <div className={'text-center '}>
          <div className={'text-white'}>
            <strong>{titulo}</strong>
          </div>
          {!loginStore.isLoggedIn && (
            <div className={'text-white'} onClick={() => {
              if (props.accion_boton_centro) {
                props.accion_boton_centro()
              }
            }}>
              <span style={{fontSize: '0.875rem', fontWeight: 'bold'}}>{sub_titulo}</span>
            </div>
          )}
        </div>
      </div>
    </div>
  )
})

export default HeaderRegistro
