import {IonAlert, IonModal} from '@ionic/react';
import React, {useEffect, useState} from 'react';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './Calificacion.css';
import * as FIcon from "react-feather";
import {Rating} from '@material-ui/lab';
import withStyles from "@material-ui/core/styles/withStyles";
import {useApiToaster} from "../../helpers-ts";

const { BASE_URL } = require("../../config/env")

type CalificacionTransportistaProps = {
  visible: boolean
  onAceptar: any
  onCerrar: any
}

const StyledRating = withStyles({
  iconFilled: {
    color: '#303AB2',
  },
  iconHover: {
    color: '#303AB2',
  },
})(Rating);

const labels: { [index: string]: string } = {
  1: 'Muy malo',
  2: 'Malo',
  3: 'Bueno',
  4: 'Muy bueno',
  5: 'Excelente!',
};


// @ts-ignore
export const Calificacion: React.FC<CalificacionTransportistaProps> = observer((props) => {

  const rootStore = useStores()
  const [Alert, setAlert] = useState(false)
  const [TextAlert, setTextAlert] = useState('')
  const [TitleAlert, setTitleAlert] = useState('')
  const [value, setValue] = useState<number>(3)
  const [Comentario, setComentario] = useState<string>('')
  const [Movil, setMovil] = useState(false);
  const apiToast = useApiToaster()

  useEffect(() => {
    setValue(3)
    setComentario('')
  }, [props.visible]);


  const alertAccion = () => {
    let objetivoCalificar = ''
    const pedidoCalificar = rootStore.pedidoCalificar
    if (pedidoCalificar && pedidoCalificar.tipo_calificacion === 20) {
      objetivoCalificar = 'la experiencia'
    } else if (pedidoCalificar && pedidoCalificar.tipo_calificacion === 10) {
      objetivoCalificar = 'al cliente'
    } else {
      objetivoCalificar = 'al transportista'
    }
    setAlert(true)
    setTitleAlert('Calificar ' + objetivoCalificar)
    setTextAlert('Confirma que desea calificar ' + objetivoCalificar + '?')
  }

  const alertEjecutarAccion = () => {
    const calificacion = rootStore.pedidoCalificar
    if (calificacion) {
      rootStore.environment.api.calificar(calificacion.id_pedido, calificacion.tipo_calificacion, calificacion.id_calificar, value, Comentario).then((result: any) => {
        apiToast(result, 'Calificado correctamente', 'calificar')
      })
    }
    props.onAceptar()
  }


  return (rootStore.pedidoCalificar && props.visible) && <div>
    <IonModal
      isOpen={props.visible}
      cssClass={!Movil ? 'modal-calificacion' : ''}
      onDidPresent={() => {
        setMovil(window.innerWidth <= 500)
      }}
      onDidDismiss={() => props.onCerrar()}
    >
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TitleAlert}
        message={TextAlert}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => alertEjecutarAccion()
          }
        ]}
      />
      <div className={'transportista-header'}>
        <FIcon.X size={30} onClick={() => props.onCerrar()} color={'white'}/>
        <span>
          {rootStore.pedidoCalificar.tipo_calificacion === 20 && 'Calificar experiencia de compra'}
          {rootStore.pedidoCalificar.tipo_calificacion === 10 && 'Calificar al cliente'}
          {rootStore.pedidoCalificar.tipo_calificacion === 30 && 'Calificar al transportista'}
        </span>
      </div>
      <div className={'text-center'}>
        <div className={'imagen-perfil-container mx-auto d-flex mb-4 ml-0'}>
          {!rootStore.pedidoCalificar.imagen ? <span> <br/> </span> :
            <img alt="imagen de perfil" src={BASE_URL + rootStore.pedidoCalificar.imagen}/>}
        </div>
        <span
          className={'nombre-cliente'}>{rootStore.pedidoCalificar.nombre}</span>
      </div>
      <div className={'calificacion-cliente-container text-center'}>
        <StyledRating
          className={'mx-auto mt-5'}
          name="size-large"
          value={value}
          size="large"
          onChange={(event, newValue) => {
            if (typeof newValue === 'number') {
              setValue(newValue);
            }
          }}
        />
        <div className={'mt-3'}>
          {value !== null && <h3 className={'text-center'}>{labels[value]}</h3>}
        </div>
        <textarea rows={5} placeholder={'Deje un comentario'} value={Comentario} className={'mx-4 mt-2'}
                  onChange={e => {
                    setComentario(e.target.value)
                  }}/>
        <div className={'boton-calificar my-5 mx-5'} onClick={() => alertAccion()}>
          CALIFICAR
        </div>
      </div>
    </IonModal>
  </div>
});

export default Calificacion;
