import { IonContent, IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { IoChatbubbleEllipsesOutline } from 'react-icons/io5'
import { useHistory, useLocation } from 'react-router'
import { ICONOS } from '../../helpers-ts/iconos'
import { useStores } from '../../models/root-store/root-store-context'
import { Pedido as PedidoApi } from '../../services/api'
import './ChatInterno.css'
import ChatModal from './ChatModal'
import ChatPedido from './ChatPedido'

const { WSS_BASE_URL } = require('../../config/env')

type ChatModalProps = {}

export const ChatInterno: React.FC<ChatModalProps> = observer((props) => {
  const rootStore = useStores()
  const [PedidoSeleccionado, setPedidoSeleccionado] = useState<PedidoApi | null>()
  const [Pedidos, setPedidos] = useState<PedidoApi[] | []>()
  const [PedidoId, setPedidoId] = useState<number | null>(null)
  const [PedidosFiltrados, setPedidosFiltrados] = useState<PedidoApi[] | []>()
  const [TextoBusqueda, setTextoBusqueda] = useState('')
  const [MostrarListado, setMostrarListado] = useState(false)
  const [MostrarChat, setMostrarChat] = useState(false)
  const [MostrarMensajes, setMostrarMensajes] = useState(false)
  const [ChatModalVisible, setChatModalVisible] = useState(false)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const { search } = useLocation()
  const history = useHistory()

  const [Loading, setLoading] = useState(false)

  useEffect(() => {
    if (MostrarListado) {
      setPedidos([])
      obtenerPedidos()
    }
  }, [MostrarListado])

  useEffect(() => {
    let currentUrlParams = new URLSearchParams(search)
    const chatSelected = currentUrlParams.get('chat') === '1'
    const pedido = Number(currentUrlParams.get('pedido'))
    if (chatSelected && !!pedido) {
      setPedidoId(pedido)
      obtenerPedidos()
    } else if (!pedido) {
      setPedidoId(null)
    }
  }, [search])

  useEffect(() => {
    if (PedidoId && Pedidos) {
      setPedidoSeleccionado(Pedidos.find((p: any) => p.id === PedidoId))
      if (!Movil) {
        setMostrarChat(true)
        setMostrarMensajes(true)
        setChatModalVisible(false)
      } else {
        setChatModalVisible(true)
      }
    }
  }, [Pedidos, PedidoId])

  useEffect(() => {
    if (!!TextoBusqueda && Pedidos && Pedidos.length > 0) {
      const pedidos = [...Pedidos]
      const busqueda = TextoBusqueda.toLowerCase()
      setPedidosFiltrados(
        pedidos.filter(
          (pedido) =>
            pedido.transportista.usuario.last_name.toLowerCase().includes(busqueda) ||
            pedido.transportista.usuario.first_name.toLowerCase().includes(busqueda) ||
            String(pedido.id).includes(busqueda),
        ),
      )
    } else if (TextoBusqueda === '') {
      setPedidosFiltrados(Pedidos)
    }
  }, [TextoBusqueda, Pedidos])

  useEffect(() => {
    if (!MostrarChat) {
      let currentUrlParams = new URLSearchParams(search)
      currentUrlParams.delete('chat')
      currentUrlParams.delete('pedido')
      history.push(window.location.pathname + '?' + currentUrlParams)
    } else if (PedidoSeleccionado) {
      let currentUrlParams = new URLSearchParams(search)
      currentUrlParams.set('chat', '1')
      currentUrlParams.set('pedido', String(PedidoSeleccionado.id))
      history.push(window.location.pathname + '?' + currentUrlParams)
    }
  }, [MostrarChat, PedidoSeleccionado])

  const obtenerPedidos = () => {
    setLoading(true)
    rootStore.environment.api.pedidosChat().then((result: any) => {
      if (result.kind === 'ok') {
        setPedidos(result.data)
      }
      setLoading(false)
    })
  }

  return (
    <>
      {Movil && !!PedidoSeleccionado && (
        <ChatModal
          visible={ChatModalVisible}
          setVisible={() => {
            setChatModalVisible(false)
          }}
          pedido={PedidoSeleccionado}
        />
      )}
      {!Movil && (
        <div id="chat-interno-container" className="col-12 col-lg-3 col-xl-2">
          <div className="listado-container">
            <div className="d-flex titulo cpointer px-1 py-3" onClick={() => setMostrarListado(!MostrarListado)}>
              <div className="d-flex align-items-center justify-content-center" style={{ flex: 1 }}>
                <IoChatbubbleEllipsesOutline
                  size={20}
                  style={{ transform: 'rotateY(180deg)', marginRight: '0.2rem' }}
                />
                <div className="d-flex">
                  <h4>Chat interno</h4>
                  {rootStore.mensajes > 0 && (
                    <div className=" ml-1 txt-dot">
                      <span className="dot"></span>
                    </div>
                  )}
                </div>
              </div>
              <div>
                {!MostrarListado && <FIcon.ChevronUp />}
                {MostrarListado && <FIcon.ChevronDown />}
              </div>
            </div>

            {MostrarListado && (
              <IonContent className="lista">
                <div>
                  <div className="input-group align-items-center">
                    <input
                      type="search"
                      className="form-control"
                      placeholder={'¿A quién estás buscando?'}
                      value={TextoBusqueda}
                      onChange={(evt) => {
                        setTextoBusqueda(evt.target.value)
                      }}
                    />
                    <div className="input-group-append">
                      <div className="search-container">
                        <FIcon.Search size={20} />
                      </div>
                    </div>
                  </div>
                  <a className="chat-link px-2 py-2" href="https://wa.me/595981840840" target={'_blank'}>
                    <div className="icono">{ICONOS.atencion_cliente}</div>
                    <div className="nombre ">
                      <span style={{ color: 'var(--naranja-abi)' }}>Staff ABi</span>
                    </div>
                  </a>
                  {Loading && (
                    <div className="d-flex justify-content-center align-items-center py-3">
                      <IonSpinner />
                    </div>
                  )}
                  {!Loading &&
                    !!PedidosFiltrados &&
                    PedidosFiltrados?.length > 0 &&
                    PedidosFiltrados.map((pedido) => {
                      const transportista = pedido.transportista
                      return (
                        <>
                          {!!pedido.transportista && (
                            <a
                              key={pedido.id}
                              className="chat-link px-1 py-2"
                              onClick={() => {
                                setPedidoSeleccionado(null)
                                setTimeout(() => {
                                  setPedidoSeleccionado(pedido)
                                  setMostrarMensajes(true)
                                  setMostrarListado(false)
                                }, 10)
                                setMostrarChat(true)
                              }}
                            >
                              <div className="img-perfil-container">
                                <img src={transportista.usuario.imagen_perfil} />
                              </div>

                              <div style={{ lineHeight: '0.91rem' }}>
                                <div className="nombre">
                                  {transportista.usuario.first_name} {transportista.usuario.last_name}
                                </div>
                                <small style={{ lineHeight: '0.91rem' }}>id#{pedido.id}</small>
                              </div>
                            </a>
                          )}
                        </>
                      )
                    })}
                </div>
              </IonContent>
            )}
          </div>
          {MostrarChat && (
            <div className="chat-interno-seleccionado" style={{position: 'absolute', left: '20rem'}}>
              <div className="d-flex titulo px-2 py-3" onClick={() => setMostrarMensajes(!MostrarMensajes)}>
                <div className="d-flex align-items-center justify-content-start" style={{ flex: 1 }}>
                  <div>
                    {PedidoSeleccionado && (
                      <h4>
                        {PedidoSeleccionado.transportista.usuario.first_name}{' '}
                        {PedidoSeleccionado.transportista.usuario.last_name}
                      </h4>
                    )}
                  </div>
                </div>
                <div>
                  {!MostrarMensajes && <FIcon.ChevronUp />}
                  {MostrarMensajes && <FIcon.ChevronDown />}
                </div>
                <a onClick={() => setMostrarChat(false)}>
                  <FIcon.XCircle />
                </a>
              </div>
              {MostrarMensajes && (
                <div className="chat-component-container d-flex flex-column">
                  {PedidoSeleccionado && (
                    <ChatPedido containerStyle={{ flex: 1 }} pedido={PedidoSeleccionado} ocultarHeader />
                  )}
                </div>
              )}
            </div>
          )}
        </div>
      )}
    </>
  )
})
export default ChatInterno
