import './SideMenu.css'

import { IonAlert, IonModal } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import { SnapshotOut } from 'mobx-state-tree'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { AiOutlineHeart, AiOutlineShoppingCart } from 'react-icons/ai'
import { BiBug } from 'react-icons/bi'
import { useHistory } from 'react-router'

import { RELEASE } from '../..'
import { RUTAS } from '../../constants'
import { useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { CategoriaArbolModel } from '../../models/types'

interface SideMenuProps {
  isOpen: boolean
  setShowModal: any
  verPerfil: any
}

export const SideMenu: React.FC<SideMenuProps> = observer((props) => {
  const history = useHistory()
  const rootStore = useStores()
  const addToast = useToaster()
  const { loginStore } = rootStore
  const [showDropAyuda, setShowDropAyuda] = useState(false)
  const [showDropMisCompras, setShowDropMisCompras] = useState(false)
  const [showDropCategorias, setShowDropCategorias] = useState(false)
  const [Alert, setAlert] = useState(false)
  const showModal = props.isOpen
  const setShowModal = props.setShowModal

  const cerrarSesion = () => {
    setShowModal(false)
    history.push(RUTAS.LOGOUT)
  }

  return (
    <IonModal
      isOpen={showModal}
      cssClass="modal-side-menu-estilo"
      swipeToClose={true}
      backdropDismiss={true}
      onDidDismiss={() => setShowModal(false)}
    >
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={'Cerrar sesión'}
        message={'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => cerrarSesion(),
          },
        ]}
      />
      <div className={'d-flex flex-column h-100 overflow-auto'}>
        <div className={'row w-100 mx-0 pb-5'} style={{ overflowX: 'hidden' }}>
          <div
            style={{
              display: 'flex',
              width: '100%',
              background: 'var(--naranja-abi)',
              paddingTop: 20,
              height: 128,
            }}
          >
            <p
              className={'mb-0 d-flex align-items-center'}
              style={{ color: 'white', fontWeight: 'bolder', marginLeft: 10 }}
            >
              <a>
                <div
                  className="centrado-flex"
                  style={{
                    height: 60,
                    width: 60,
                    backgroundColor: '#fff',
                    borderRadius: '100%',
                    overflow: 'hidden',
                  }}
                  onClick={
                    loginStore.isLoggedIn
                      ? props.verPerfil
                      : () => {
                          let currentUrlParams = new URLSearchParams(window.location.search)
                          currentUrlParams.set('login', '1')
                          history.push(window.location.pathname + '?' + currentUrlParams.toString())
                        }
                  }
                >
                  <img
                    src={loginStore.imagen_perfil ? loginStore.imagen_perfil : 'assets/icon/person-line.png'}
                    className={
                      'imagen-perfil ' +
                      (loginStore.imagen_perfil ? 'imagen-perfil-side-menu' : 'contorno-person-side-menu')
                    }
                  />
                </div>
              </a>

              {!loginStore.isLoggedIn && (
                <a
                  style={{ fontWeight: 'bolder', marginLeft: 10 }}
                  onClick={() => {
                    let currentUrlParams = new URLSearchParams(window.location.search)
                    currentUrlParams.set('login', '1')
                    setShowModal(false)
                    history.push(window.location.pathname + '?' + currentUrlParams.toString())
                  }}
                >
                  Iniciar sesión
                </a>
              )}
              {loginStore.isLoggedIn && (
                <>
                  <div
                    className="d-flex flex-column"
                    style={{ marginLeft: 10 }}
                    onClick={() => {
                      history.push(RUTAS.CLIENTE)
                      setShowModal(false)
                    }}
                  >
                    <strong
                      style={{
                        lineHeight: 1,
                      }}
                    >
                      ¡Hola,{' '}
                      <span>
                        {loginStore.first_name} {loginStore.last_name}
                      </span>
                      !
                    </strong>
                    <span style={{ fontSize: 12 }}>Id#{loginStore.id}</span>
                  </div>
                </>
              )}
            </p>

            <hr className={'mt-3 mb-1'} style={{ borderColor: 'white' }} />
          </div>
          <div style={{ color: 'var(--gris-oscuro-abi)', width: '99%' }}>
            <div className={'col-12 mt-3 px-0'}>
              <a
                onClick={() => {
                  history.push(RUTAS.ROOT)
                  setShowModal(false)
                }}
                style={{ fontWeight: 'bolder', marginLeft: 10 }}
              >
                Inicio
              </a>
            </div>
            <div style={{ marginLeft: 10, marginTop: '1rem' }}>
              <a
                onClick={() => {
                  setShowDropCategorias(!showDropCategorias)
                }}
              >
                <span style={{ fontWeight: showDropCategorias ? 'bold' : 'bolder' }}>Categorías</span>
                {showDropCategorias ? <FIcon.ChevronDown /> : <FIcon.ChevronRight />}
              </a>
            </div>
            {showDropCategorias && <RenderCategorias setShowModal={setShowModal as any} />}

            {loginStore.isLoggedIn && (
              <div className={'col-12 mt-3 px-0'}>
                <a
                  onClick={() => {
                    setShowDropMisCompras(!showDropMisCompras)
                  }}
                  style={{ marginLeft: 10 }}
                >
                  <span style={{ fontWeight: showDropMisCompras ? 'bold' : 'bolder' }}>Mis Compras</span>
                  {showDropMisCompras ? <FIcon.ChevronDown /> : <FIcon.ChevronRight />}
                  {showDropMisCompras && (
                    <div className="drop-side-menu">
                      <a>
                        <div onClick={() => history.push(RUTAS.PEDIDOS)}>
                          <img src="assets/icon/historial.png" height={20} width={20} />
                          <span style={{ marginLeft: 10 }}>Mis pedidos</span>
                        </div>
                      </a>

                      <a
                        onClick={() => {
                          if (!loginStore.isLoggedIn) {
                            addToast('Inicie sesión para ver sus pedidos.')
                            let currentUrlParams = new URLSearchParams(window.location.search)
                            currentUrlParams.set('login', '1')
                            history.push(window.location.pathname + '?' + currentUrlParams.toString())
                          } else {
                            history.push(RUTAS.FAVORITOS)
                          }
                        }}
                      >
                        <AiOutlineHeart size={20} />
                        <span style={{ marginLeft: 10 }}>Favoritos </span>
                      </a>
                      <a
                        className="boton-header-cliente"
                        onClick={() => {
                          history.push('/carrito')
                        }}
                      >
                        <AiOutlineShoppingCart size={20} style={{ fontWeight: 'lighter' }} />

                        <span style={{ marginLeft: 10 }}>Carrito</span>
                      </a>
                    </div>
                  )}
                </a>
              </div>
            )}
            <div className={'col-12 mt-3 px-0'}>
              <a
                onClick={() => {
                  setShowDropAyuda(!showDropAyuda)
                }}
                style={{ marginLeft: 10 }}
              >
                <span style={{ fontWeight: showDropAyuda ? 'bold' : 'bolder' }}>Ayuda</span>
                {showDropAyuda ? <FIcon.ChevronDown /> : <FIcon.ChevronRight />}
              </a>
              {showDropAyuda && (
                <div className="drop-side-menu" style={{ height: 250 }}>
                  <a href={RUTAS.PREGUNTAS_FRECUENTES} style={{ color: 'var(--gris-oscuro-abi)' }}>
                    <img
                      alt={'Preguntas frecuentes'}
                      height={20}
                      style={{ marginRight: 5 }}
                      src={'assets/icon/info.png'}
                    />
                    <span>Preguntas frecuentes</span>
                  </a>

                  <a>
                    <img
                      alt={'Comentarios y Sugerencias'}
                      height={20}
                      style={{ marginRight: 5 }}
                      src={'assets/icon/comentarios.png'}
                    />
                    <span>Comentarios y Sugerencias</span>
                  </a>

                  <a
                    onClick={() => {
                      history.push(RUTAS.ROOT)
                    }}
                  >
                    <img
                      alt={'Políticas de privacidad'}
                      height={20}
                      src={'assets/icon/documento.png'}
                      style={{ marginRight: 5 }}
                    />
                    <span>Políticas de privacidad</span>
                  </a>
                  <a href="https://wa.me/595981840840" style={{ color: 'var(--gris-oscuro-abi)' }}>
                    <BiBug size={20} style={{ marginRight: 5 }} />
                    <span>Reportar un error</span>
                  </a>

                  <a href="https://wa.me/595981840840" style={{ color: 'var(--gris-oscuro-abi)' }}>
                    <img
                      alt={'Atención al cliente'}
                      height={20}
                      src={'assets/icon/atencion-cliente.png'}
                      style={{ marginRight: 5 }}
                    />
                    <span>Atención al cliente</span>
                  </a>
                </div>
              )}

              <div className={'col-12 mt-3 px-0'} style={{ marginLeft: 10, fontWeight: 'bolder' }}>
                <a>
                  <img alt={'Términos y Condiciones'} height={20} src={'assets/icon/documento.png'} />
                  <span>Términos y Condiciones</span>
                </a>
              </div>

              {loginStore.isLoggedIn && (
                <div
                  className={'col-12 mt-3 px-0'}
                  style={{ marginLeft: 10, fontWeight: 'bolder' }}
                  onClick={() => setAlert(true)}
                >
                  <span>Cerrar sesión</span>
                </div>
              )}

              <div id={'release-container'}>v{RELEASE.split('@')[1]}</div>
            </div>
          </div>
        </div>
      </div>
    </IonModal>
  )
})

interface RenderCategoriaI {
  setShowModal: any
}

const RenderCategorias: React.FC<RenderCategoriaI> = observer((props) => {
  const history = useHistory()
  const { arbol } = useStores()

  const { setShowModal } = props

  const renderSubCategoria: any = (categoria: SnapshotOut<typeof CategoriaArbolModel>) => {
    return (
      <>
        <a
          onClick={() => {
            history.push(RUTAS.CATEGORIA_LISTADO_PRODUCTOS(categoria.id, categoria.nombre))
            setShowModal(false)
          }}
          className="ml-3"
          key={categoria.id}
        >
          {categoria.icono && (
            <img alt={categoria.nombre} src={categoria.icono} height={20} style={{ marginRight: 10 }} />
          )}
          {categoria.nombre}
        </a>

        {!!categoria.hijos && categoria.hijos.length > 0 && categoria.hijos.map((hijo) => renderSubCategoria(hijo))}
      </>
    )
  }
  return (
    <div className="drop-side-menu" style={{ height: 300 }}>
      {arbol.map((categoria) => {
        return (
          <>
            <a
              onClick={() => {
                history.push(RUTAS.CATEGORIA_LISTADO_PRODUCTOS(categoria.id, categoria.nombre))
                setShowModal(false)
              }}
              className="ml-1"
              key={categoria.id}
            >
              {categoria.icono && (
                <img alt={categoria.nombre} src={categoria.icono} height={20} style={{ marginRight: 10 }} />
              )}
              {categoria.nombre}
            </a>
            {!!categoria.hijos && categoria.hijos.length > 0 && categoria.hijos.map((hijo) => renderSubCategoria(hijo))}
          </>
        )
      })}
    </div>
  )
})
