import './ModalFacturacion.css'

import * as FIcon from "react-feather";
import {IonModal} from "@ionic/react";
import React, {useState} from "react";
import {DatoFacturacion as DatoFacturacionApi} from "../services/api";
import {observer} from "mobx-react-lite";
import {useStores} from "../models/root-store/root-store-context";
import { useApiToaster} from "../helpers-ts";
import AdminItemFieldRegistro from "./Admin/AdminItemFieldRegistro";

type ModalFacturacionProps = {
    visible: boolean
    successCallback: any
}
export const ModalFacturacion: React.FC<ModalFacturacionProps> = observer ((props) => {
    const rootStore = useStores()
    const [Errors, setErrors] = useState({} as any)
    const [Loading, setLoading] = useState(false)
    const apiToaster = useApiToaster()
    const [Visible, setVisible] = useState(false)

    React.useEffect(() => {
        setVisible(props.visible)
    }, [props.visible])

    const [DatoFacturacion, setDatofacturacion] = useState<DatoFacturacionApi>({
        id: null,
        razon_social: '',
        ruc: '',
        default: false,
    })

    const setDatoFacturacionInput = (campo: string, dato: string) => {
        const datos = { ...DatoFacturacion }
        //@ts-ignore
        datos[campo] = dato
        setDatofacturacion(datos)
    }


    const guardarDatoFacturacion = (dato: DatoFacturacionApi) => {
        setErrors({} as any)
        rootStore.environment.api.guardarDatoFacturacionUsuario(dato).then((result) => {
            apiToaster(result, 'Datos de facturación guardardos correctamente', 'guardar')
            if (result.kind === 'ok') {
                if (props.successCallback) {
                    props.successCallback()
                }
                setDatofacturacion({ id: null, razon_social: '', ruc: '', default: false })
                setVisible(false)
            } else if (result.kind === 'bad-data') {
                setErrors(result.errors)
            }
        })
    }

    if (!Visible) {
        return null
    }

    return (
        <IonModal
            isOpen={Visible}
            cssClass="modal-datos-facturacion-short"
            onDidDismiss={() =>  setVisible(false)}
        >
            <>
                <div className="heather-info-registro background-color-naranja-abi">
                    <div>
                        <span>Añadir datos de facturación</span>
                    </div>

                    <div onClick={() => setVisible(false)}>
                        <FIcon.XCircle style={{ width: 26, marginRight: 5, color: '#fff' }} />
                    </div>
                </div>
                <div
                    style={{
                        width: '100%',
                        height: '100%',
                        display: 'flex',
                        alignItems: 'center',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        paddingTop: 20,
                    }}
                >
                    <div style={{ width: '80%' }}>
                        <AdminItemFieldRegistro
                            deshabilitado={!Loading}
                            titulo={'RUC o Cédula de identidad'}
                            tipo={'texto'}
                            campo={'ruc'}
                            valor={DatoFacturacion.ruc ? DatoFacturacion.ruc : ''}
                            error={Errors && Errors.ruc}
                            set_dato={setDatoFacturacionInput}
                            campo_blur
                            set_dato_blur={() => {
                                let params =
                                    DatoFacturacion.ruc.indexOf('-') > 0
                                        ? { ruc: DatoFacturacion.ruc }
                                        : { documento: DatoFacturacion.ruc }
                                rootStore.environment.api
                                    .consultarRuc(params)
                                    .then((result: any) => {
                                        if (result.kind === 'ok') {
                                            let resp = result.response.result
                                            if (resp) {
                                                setDatofacturacion(
                                                    Object.assign({}, DatoFacturacion, {
                                                        id: DatoFacturacion.id,
                                                        ruc: `${resp.documento}-${resp.dv}`,
                                                        razon_social: resp.razon_social,
                                                        default: DatoFacturacion.default,
                                                    }),
                                                )
                                            }
                                        }
                                    })
                                    .catch(() => {})
                            }}
                            columnas={12}
                            columnas_sm={12}
                        />
                    </div>
                    <div style={{ width: '80%' }}>
                        <AdminItemFieldRegistro
                            deshabilitado={!Loading}
                            titulo={'Razón social'}
                            tipo={'texto'}
                            campo={'razon_social'}
                            valor={DatoFacturacion.razon_social ? DatoFacturacion.razon_social : ''}
                            error={Errors && Errors.razon_social}
                            set_dato={setDatoFacturacionInput}
                            columnas={12}
                            columnas_sm={12}
                        />
                    </div>

                    <div className={'button-anadir-editar-perfil-modal-container'}>
                        <button
                            className={'button-anadir-editar-perfil-modal'}
                            title={'Agregar guardar datos de facturación'}
                            onClick={() => {
                                guardarDatoFacturacion(DatoFacturacion)
                            }}
                        >
                            Guardar
                        </button>
                    </div>
                </div>
            </>
        </IonModal>
    )
})

export default ModalFacturacion
