import './Header.css'

import { Capacitor } from '@capacitor/core'
import { IonAlert, IonButtons, IonHeader, IonModal, IonTitle, IonToolbar } from '@ionic/react'
import MenuIcon from '@material-ui/icons/Menu'
import { AiOutlineHeart } from 'react-icons/ai'
import WhatsAppIcon from '@material-ui/icons/WhatsApp'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { useHistory } from 'react-router-dom'
import { openPage } from '../helpers'
import { ICONOS_DIRECCION, RUTAS, RUTAS_APP_FABRICANTE, RUTAS_WEB_TRANSPORTISTA } from '../constants'
import { cambiarModo, formatTextoURL, useClearStore, useToaster } from '../helpers-ts'
import { useStores } from '../models/root-store/root-store-context'
import { SideMenu } from './Cliente/SideMenu'
import { CategoriaArbol, Direccion } from '../services/api'
import ModalDireccionPredeterminada from './ModalDireccionPredeterminada'
import { ICONOS } from '../helpers-ts/iconos'

type HeaderProps = {
  datos_busqueda?: any
  datos_filtros?: any
  obtener_datos?: any
}

export const Header: React.FC<HeaderProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore, carritoStore, favoritoStore } = rootStore
  const [showModal, setShowModal] = useState(false)
  const [modalDirecciones, setModalDirecciones] = useState(false)
  const [Alert, setAlert] = useState(false)
  const history = useHistory()
  const Movil = /Mobi/.test(navigator.userAgent)
  const [Direccion, setDireccion] = useState<Direccion>()
  const addToast = useToaster()
  const clearStore = useClearStore()
  const [show, setShow] = useState({})
  const [ModalContacto, setModalContacto] = useState<boolean>(false)
  const [TextoBusqueda, setTextoBusqueda] = useState('')

  const parentCategoria = (categoria: CategoriaArbol): CategoriaArbol => {
    if (categoria.superior) {
      const padre = rootStore.arbolFlat.find((cat) => cat.id === categoria.superior)
      if (padre) return parentCategoria(padre)
    }
    return categoria
  }

  useEffect(() => {
    setDireccion(direccionDefault() as any)
  }, [Direccion])

  const direccionDefault = () => {
    const defaultDir = loginStore.direcciones.find((e) => e.default)
    if (defaultDir === undefined) {
      return loginStore.direcciones[0]
    }
    return defaultDir
  }

  const showMenuGetSet = (id: number, showp?: boolean, recursive_show?: any): boolean => {
    // getter setter de visibilidad
    if (showp === undefined) {
      // @ts-ignore
      return show[id] || false
    }
    // @ts-ignore
    const show_copy = recursive_show ? recursive_show : { ...show }
    // @ts-ignore
    show_copy[id] = showp
    if (!showp) {
      // ocultar hijos
      const find = rootStore.arbolFlat.find((cat) => cat.id === id)
      if (find) {
        find.hijos.forEach((cat) => {
          showMenuGetSet(cat.id, false, show_copy)
        })
      }
    }
    if (showp) {
      const find = rootStore.arbol.find((cat) => cat.id === id)
      if (find) {
        // es padre
        // ocultar otros padres
        rootStore.arbol.filter((cat) => cat.id !== id).forEach((cat) => showMenuGetSet(cat.id, false, show_copy))
      }
    }
    if (!recursive_show) setShow(show_copy)
    return showp
  }

  const menuclick = (categoria: CategoriaArbol) => {
    history.push(RUTAS.CATEGORIA_LISTADO_PRODUCTOS(categoria.id, formatTextoURL(categoria.nombre)))
  }

  const CustomToggleCategoriaBase: React.FC<{ onClick: any; ref: any }> = React.forwardRef(
    ({ children, onClick }, ref) => (
      <a
        href=""
        ref={ref as any}
        className="toggle-secundario-categoria-header"
        onClick={(e) => {
          e.preventDefault()
          onClick(e)
        }}
      >
        {children}
      </a>
    ),
  )

  const renderMenu = (categoria: CategoriaArbol) => {
    if (categoria.hijos.length) {
      return (
        <li className="dropdown">
          <a onClick={() => menuclick(categoria)}>
            {categoria.icono && <img src={categoria.icono} alt={categoria.nombre} className="icon-categoria" />}
            {categoria.nombre}
          </a>
          <ul className="submenu">
            <li className="dropdown">{categoria.hijos.map(renderMenu)}</li>
          </ul>
        </li>
      )
    }
    return (
      <li className="dropdown">
        <a onClick={() => menuclick(categoria)}>
          {categoria.icono && <img src={categoria.icono} alt={categoria.nombre} className="icon-categoria" />}
          {categoria.nombre}
        </a>
      </li>
    )
  }

  React.useEffect(() => {
    let currentUrlParams = new URLSearchParams(window.location.search)
    if (currentUrlParams.get('s')) {
      setTextoBusqueda(currentUrlParams.get('s') || '')
    }
  }, [])

  const cerrarSesion = () => {
    if (Capacitor.getPlatform() !== 'web') {
      setShowModal(false)
      history.push(RUTAS.LOGOUT)
    } else {
      setShowModal(false)
      addToast('Sesión finalizada', 'info')
      clearStore()
      document.location.href = '/'
    }
  }

  const verPerfil = () => {
    setShowModal(false)
    if (loginStore.administrador) {
      history.push(RUTAS.ADMIN)
    } else if (loginStore.modo_uso_app === 20) {
      history.push(RUTAS_APP_FABRICANTE.PERFIL)
    } else if (loginStore.modo_uso_app === 10) {
      history.push(RUTAS.CLIENTE)
    } else {
      history.push(RUTAS_WEB_TRANSPORTISTA.INICIO)
    }
  }

  const buscar = (catid?: number) => {
    history.push(`${RUTAS.BUSCAR}${catid ? '?c=' + catid : ''}`)
    setShowModal(false)
  }

  const searchKeyDown = (evt: any) => {
    if (evt.keyCode === 13) {
      doSearch(evt.target.value.trim())
    }
  }

  const doSearch = (texto: any) => {
    history.push(`${RUTAS.BUSCAR}${texto ? '?s=' + encodeURIComponent(texto) : ''}`)
  }

  return (
    <div style={{ height: Movil ? 136 : 'auto' }}>
      <ModalDireccionPredeterminada
        visible={modalDirecciones}
        setDireccionPredeterminada={(direccion: any) => {
          setDireccion(direccion)
        }}
        onDismiss={(visible: any) => setModalDirecciones(visible)}
      />
      <IonModal
        isOpen={ModalContacto}
        cssClass="modal-contacto"
        swipeToClose={true}
        backdropDismiss={true}
        onDidDismiss={() => setModalContacto(false)}
      >
        <IonHeader>
          <IonToolbar>
            <IonTitle slot="start">Contacto</IonTitle>
            <IonButtons slot={'end'} onClick={() => setModalContacto(false)}>
              <FIcon.X color={'#000'} className={'mr-2'} />
            </IonButtons>
          </IonToolbar>
        </IonHeader>

        <div className={'h-100 mt-4'}>
          <div className={'d-flex flex-column align-items-left justify-content-center h-75 mx-4 mt-2'}>
            <a
              className={'mb-4 d-flex align-items-center justify-content-start btn btn-primary'}
              href={'tel:+595981840840'}
            >
              <FIcon.Phone size={18} className={'text-white'} />
              <span style={{ fontSize: 16 }} className={'text-white ml-2'}>
                Llamar
              </span>
            </a>
            <a
              className={'mb-3 d-flex align-items-center justify-content-start btn btn-primary'}
              target={'_blank'}
              href="https://wa.me/595981840840"
            >
              <WhatsAppIcon style={{ fontSize: 20 }} className={'text-white'} />
              <span style={{ fontSize: 16 }} className={'text-white ml-2'}>
                Contactar por whatsapp
              </span>
            </a>
          </div>
        </div>
      </IonModal>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={'Cerrar sesión'}
        message={'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => {
              setShowModal(false)
              cerrarSesion()
            },
          },
        ]}
      />
      {!Movil && (
        <>
          <div className={'header'} style={{ position: 'fixed', zIndex: 999, width: '100%', backgroundColor: '#FFF' }}>
            <div className="background-color-naranja-abi" style={{ display: 'flex', justifyContent: 'center' }}>
              <div className="container-lg top-cliente">
                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-start' }}>
                  <div
                    className="boton-header-cliente"
                    onClick={() => {
                      if (loginStore.isLoggedIn && loginStore.transportista) {
                        cambiarModo(rootStore, history, 30)
                      } else {
                        let currentUrlParams = new URLSearchParams(window.location.search)
                        currentUrlParams.set('registrot', '1')
                        history.push(window.location.pathname + '?' + currentUrlParams.toString())
                      }
                    }}
                  >
                    <span className="icn-driver">{ICONOS.driver}</span>
                    <span className="texto-header" style={{marginLeft: "1px!important"}}>Driver</span>
                  </div>
                  <div
                    className={'boton-header-cliente'}
                    onClick={() => {
                      if (loginStore.isLoggedIn && loginStore.fabricante) {
                        cambiarModo(rootStore, history, 20)
                      } else {
                        if (Movil) {
                          let currentUrlParams = new URLSearchParams(window.location.search)
                          currentUrlParams.set('registrop', '1')
                          history.push(window.location.pathname + '?' + currentUrlParams.toString())
                        } else {
                          history.push(RUTAS_APP_FABRICANTE.HOME)
                        }
                      }
                    }}
                  >
                    <span className="icn-proveedor">{ICONOS.proveedor}</span>
                    <span className="texto-header">Proveedor</span>
                  </div>
                </div>
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-end',
                  }}
                >
                  <a>
                    <div className="boton-header-cliente" onClick={openPage(RUTAS.PEDIDOS, history)}>
                      <span className="icn-mis-pedidos">{ICONOS.mis_pedidos}</span>
                      <span className="texto-header">Mis Pedidos</span>
                    </div>
                  </a>

                  <a
                    className="boton-header-cliente"
                    onClick={() => {
                      if (!loginStore.isLoggedIn) {
                        addToast('Inicie sesión para ver sus favoritos.')
                        let currentUrlParams = new URLSearchParams(window.location.search)
                        currentUrlParams.set('login', '1')
                        history.push(window.location.pathname + '?' + currentUrlParams.toString())
                      } else {
                        history.push(RUTAS.FAVORITOS)
                      }
                    }}
                  >
                    <div style={{ position: 'relative' }}>
                      <span className="icn-favoritos">
                        {ICONOS.favorito}
                      </span>
                      <span
                        className={
                          favoritoStore.cantidadFavoritos < 10
                            ? 'cantidad-favoritos-min-top background-color-naranja-abi fuente-azul-abi'
                            : 'cantidad-favoritos-max-top background-color-naranja-abi fuente-azul-abi'
                        }
                      >
                        {!!favoritoStore.cantidadFavoritos ? favoritoStore.cantidadFavoritos : null}
                      </span>
                    </div>
                    <span className="texto-header">Favoritos </span>
                  </a>
                  <a
                    className="boton-header-cliente"
                    onClick={() => {
                      history.push('/carrito')
                    }}
                  >
                    <div id={'icon-carrito'} style={{ position: 'relative' }}>
                      <span className="icn-carrito">{ICONOS.carrito}</span>
                      <span
                        className={
                          carritoStore.cantidadProductosCarrito < 10
                            ? 'cantidad-items-carrito-min-top background-color-naranja-abi '
                            : 'cantidad-items-carrito-max-top background-color-naranja-abi '
                        }
                      >
                        {!!carritoStore.cantidadProductosCarrito ? carritoStore.cantidadProductosCarrito : null}
                      </span>
                    </div>
                    <span className="texto-header">Carrito</span>
                  </a>
                  {loginStore.isLoggedIn && (
                    <>
                      <a
                        onClick={verPerfil}
                        className="boton-header-cliente"
                        style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}
                      >
                        <div className="nombre-usuario-header">
                          {loginStore.first_name} {loginStore.last_name}
                        </div>
                        <div style={{ fontWeight: 'bold', fontSize: 12 }}>id#{loginStore.id}</div>
                      </a>
                      <a
                        className="centrado-flex"
                        onClick={verPerfil}
                        style={{
                          height: 50,
                          width: 50,
                          backgroundColor: '#fff',
                          borderRadius: '100%',
                          overflow: 'hidden',
                        }}
                      >
                        <img
                          alt={'Imagen de perfil'}
                          className={'imagen-perfil'}
                          src={loginStore.imagen_perfil ? loginStore.imagen_perfil : 'assets/icon/person-line.png'}
                          width={loginStore.imagen_perfil ? 50 : 30}
                          height={loginStore.imagen_perfil ? 50 : 30}
                          style={loginStore.imagen_perfil ? {} : { position: 'relative', bottom: -5 }}
                        />
                      </a>
                    </>
                  )}
                  {!loginStore.isLoggedIn && (
                    <>
                      <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', paddingRight: 10 }}>
                        <a
                          onClick={() => {
                            let currentUrlParams = new URLSearchParams(window.location.search)
                            currentUrlParams.set('login', '1')
                            history.push(window.location.pathname + '?' + currentUrlParams.toString())
                          }}
                          style={{ fontSize: 15, fontWeight: 'bold' }}
                        >
                          <span className="texto-header">Iniciar Sesión</span>
                        </a>
                        <a
                          onClick={() => {
                            let currentUrlParams = new URLSearchParams(window.location.search)
                            currentUrlParams.set('sregistro', '1')
                            history.push(window.location.pathname + '?' + currentUrlParams.toString())
                          }}
                          style={{ fontSize: 12 }}
                        >
                          <span className="texto-header">Crear cuenta</span>
                        </a>
                      </div>
                      <div
                        className="centrado-flex"
                        style={{
                          height: 50,
                          width: 50,
                          backgroundColor: '#fff',
                          borderRadius: '100%',
                          overflow: 'hidden',
                        }}
                      >
                        <img
                          alt={'Usuario'}
                          src={'assets/icon/person-line.png'}
                          width={30}
                          height={30}
                          style={{ position: 'relative', bottom: -5 }}
                        />
                      </div>
                    </>
                  )}
                </div>
              </div>
            </div>
            <div className="container-lg" style={{ background: '#FFF' }}>
              <div className="mid-header-container" style={{ height: 110 }}>
                <div style={{ display: 'flex', justifyContent: 'flex-start' }}>
                  <a
                    title={'Ir al inicio'}
                    href={RUTAS.ROOT}
                    onClick={(evt) => {
                      evt.preventDefault()
                      history.push(RUTAS.ROOT)
                    }}
                  >
                    <img src={'assets/abi-v.svg'} alt={'Logo de ABi'} style={{ width: 70, height: 70 }} />
                  </a>
                </div>

                <div style={{ flex: 2, justifyContent: 'flex-end', display: 'flex', alignItems: 'center' }}>
                  <div className="input-group" style={{ marginRight: 20 }}>
                    <input
                      type="search"
                      className="form-control"
                      placeholder={'Qué estás buscando?'}
                      value={TextoBusqueda}
                      onChange={(evt) => {
                        setTextoBusqueda(evt.target.value)
                      }}
                      onKeyDown={searchKeyDown}
                    />
                    <div className="input-group-append">
                      <a className="btn btn-outline-secondary" type="button" onClick={() => doSearch(TextoBusqueda)}>
                        <FIcon.Search size={20} />
                      </a>
                    </div>
                  </div>
                </div>
                <div
                  style={{
                    height: '100%',
                    flex: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'flex-end',
                  }}
                >
                  {!!loginStore.isLoggedIn && !!Direccion?.nombre && (
                    <a>
                      <div
                        style={{
                          display: 'flex',
                          flexDirection: 'column',
                          justifyContent: 'center',
                          marginLeft: 20,
                        }}
                        onClick={() => {
                          setModalDirecciones(true)
                        }}
                      >
                        <span className="color-fuente-naranja-abi" style={{ fontWeight: 'bold', height: 25 }}>
                          Enviar a
                        </span>
                        <div style={{ display: 'flex' }}>
                          <img
                            alt={'Dirección'}
                            src={
                              !!ICONOS_DIRECCION[Direccion.icono]
                                ? ICONOS_DIRECCION[Direccion.icono]
                                : 'assets/icon/ubicacion-gray.png'
                            }
                            style={{ width: 30, height: 30, marginRight: 3 }}
                          />
                          <span className="color-fuente-naranja-abi" style={{ fontWeight: 'bolder', height: 22 }}>
                            {Direccion?.nombre}
                          </span>
                        </div>
                      </div>
                    </a>
                  )}
                </div>
              </div>
            </div>

            <div style={{ backgroundColor: '#e0dbe2' }}>
              <div className={'top container-lg mid-header-container'}>
                <div className={'categorias-header'}>
                  <ul id="mainmenu" className="menu">
                    <li className="dropdown">
                      <a className="toggle-principal-categoria-header" onClick={() => history.push(RUTAS.CATEGORIAS)}>
                        Categorías <FIcon.ChevronDown size={16} />
                      </a>
                      <ul className="submenu">
                        <li className="dropdown">{rootStore.arbol.map(renderMenu)}</li>
                      </ul>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div style={{ height: 206 }} />
        </>
      )}
      {/* Header modo app*/}
      {Movil && (
        <>
          <div className={'header w-100'} style={{ position: 'fixed', zIndex: 999 }}>
            <div className="top-cliente-movil background-color-naranja-abi">
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'flex-start',
                  alignItems: 'center',
                  width: window.innerWidth / 2 - 42,
                }}
              >
                <div onClick={() => setShowModal(true)} className="centrado-flex">
                  <MenuIcon style={{ fontSize: 30 }} />
                </div>
                {loginStore.isLoggedIn && !!Direccion?.nombre && (
                  <div
                    className={'bloque-direccion'}
                    style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', marginLeft: 10 }}
                    onClick={() => {
                      setModalDirecciones(true)
                    }}
                  >
                    <span style={{ fontWeight: 'bold', height: 25, color: '#fff' }}>Enviar a</span>
                    <div style={{ display: 'flex' }}>
                      <img
                        alt={'Dirección'}
                        src="assets/icon/ubicacion-gray.png"
                        className="filter-invert-imagen"
                        style={{ width: Movil ? 20 : 30, height: Movil ? 20 : 30, marginRight: 3 }}
                      />
                      <span className="texto-direccion" style={{ fontWeight: 'bolder', height: 22, color: '#fff' }}>
                        {Direccion.nombre}
                      </span>
                    </div>
                  </div>
                )}
              </div>

              <div className="logo-no-direccion-header-movil">
                <a className="boton-header-cliente logo-abi-fabricante" onClick={() => history.push(RUTAS.ROOT)}>
                  {ICONOS.abi}
                </a>
              </div>

              <div
                style={{
                  display: 'flex',
                  justifyContent: 'flex-end',
                  alignItems: 'center',
                  width: window.innerWidth / 2 - 42,
                }}
              >
                <a className="boton-header-cliente" onClick={() => history.push(RUTAS.FAVORITOS)}>
                  <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}> </div>
                  <div style={{ position: 'relative' }}>
                    <span className="icn-favoritos">
                      {ICONOS.favorito}
                    </span>
                    <span
                      className={
                        favoritoStore.cantidadFavoritos < 10
                          ? 'cantidad-favoritos-min-top-movil background-color-naranja-abi fuente-azul-abi'
                          : 'cantidad-favoritos-max-top-movil background-color-naranja-abi fuente-azul-abi'
                      }
                    >
                      {!!favoritoStore.cantidadFavoritos ? favoritoStore.cantidadFavoritos : null}
                    </span>
                  </div>
                </a>
                <a
                  className="boton-header-cliente"
                  onClick={() => {
                    history.push('/carrito')
                  }}
                >
                  <div id={'icon-carrito'} style={{ position: 'relative' }}>
                    <span className="icn-carrito">
                      {ICONOS.carrito}
                    </span>
                    <span
                      className={
                        carritoStore.cantidadProductosCarrito < 10
                          ? 'cantidad-items-carrito-min-top-movil background-color-naranja-abi fuente-azul-abi'
                          : 'cantidad-items-carrito-max-top-movil background-color-naranja-abi fuente-azul-abi'
                      }
                    >
                      {!!carritoStore.cantidadProductosCarrito ? carritoStore.cantidadProductosCarrito : null}
                    </span>
                  </div>
                </a>
              </div>
            </div>
            <div className={'top-cliente-movil background-color-naranja-abi'}>
              <div onClick={() => history.goBack()}>
                <FIcon.ChevronLeft />
              </div>
              <div className="input-group">
                <input
                  type="search"
                  className="form-control"
                  placeholder={'Qué estás buscando?'}
                  value={TextoBusqueda}
                  style={{}}
                  onChange={(evt) => {
                    setTextoBusqueda(evt.target.value)
                  }}
                  onClick={() => {
                    setTextoBusqueda('')
                  }}
                  onKeyDown={searchKeyDown}
                />
                <div className="input-group-append">
                  <button
                    className="btn btn-outline-secondary"
                    style={{ height: 38, backgroundColor: '#fff' }}
                    type="button"
                    onClick={() => doSearch(TextoBusqueda)}
                  >
                    <FIcon.Search size={20} style={{ color: '#4e4e4eff', marginBottom: 2 }} />
                  </button>
                </div>
              </div>
            </div>
          </div>
          <SideMenu
            isOpen={showModal}
            setShowModal={(set: boolean) => {
              setShowModal(set)
            }}
            verPerfil={verPerfil}
          />
        </>
      )}
      <div className={'btn-whatsapp'}>
        <a href="https://wa.me/595981840840/?text=" target="_blank">
          <img src={'assets/btn_whatsapp.png'} alt="" />
        </a>
      </div>
    </div>
  )
})

export default Header
