import { IonModal } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'

import { cantidad_fmt, plata_fmt } from '../../helpers'
import { useStores } from '../../models/root-store/root-store-context'

type AdminItemDetailProps = {
  titulo?: string
  texto?: string | null | number
  columnas: number
  imagen?: string | null
  restriccion?: boolean | null
  extra_class?: string
  img_extra_class?: string
  html?: any
  separador_dinero?: boolean
  separador_miles?: boolean
  show_image_alert?: true
  info_icon?: any
  es_protegido?: boolean
}

export const AdminItemDetail: React.FC<AdminItemDetailProps> = observer((props) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const img_extra_class = props.img_extra_class || ''
  const extra_class = props.extra_class || ''
  const separador_dinero = props.separador_dinero || false
  const separador_miles = props.separador_miles || false
  const [modalVisible, setmodalVisible] = useState<boolean>(false)
  const heightScreen = window.screen.height

  if (props.restriccion === false) {
    return null
  }

  let color
  if (loginStore.modo_uso_app === 30) {
    color = '#ff6f33'
  } else if (loginStore.modo_uso_app === 20) {
    color = '#303AB2'
  } else {
    color = '#000000'
  }
  return (
    <div className={'col-' + props.columnas + '  ' + extra_class}>
      {(props.show_image_alert && props.imagen) || (props.es_protegido && props.imagen) && (
        <IonModal
          animated={true}
          isOpen={modalVisible}
          cssClass="modal-imagen-grande"
          swipeToClose={true}
          backdropDismiss={true}
          onDidDismiss={() => setmodalVisible(false)}
        >
          <div className={'transportista-header'} style={{ backgroundColor: color }}>
            <span className={'text-truncate'} style={{ maxWidth: 330, color: '#fff', paddingLeft: 10 }}>
              {props.titulo}
            </span>
            <FIcon.X
                className={'ml-auto mr-3'}
                size={30}
                onClick={() => setmodalVisible(false)}
                color={'white'}
                style={{float: 'right'}}
            />
          </div>
          <div className={'text-center mb-auto mt-auto'}>
            <a target={'_blank'} href={props.imagen}>
              <img alt={props.titulo} src={props.imagen} style={{maxHeight: heightScreen * 0.7  }}/>
            </a>
          </div>
        </IonModal>
      )}
      {props.titulo && (
        <div className={'text-gray mb-2 d-flex align-items-center'}>
          <strong>{props.titulo}</strong>{' '}
          {props.show_image_alert && props.imagen && (
            <a target={'_blank'} onClick={() => setmodalVisible(true)}>
              <FIcon.Eye size={18} className={'ml-2'} />
            </a>
          )}
        </div>
      )}
      <div
        className="detalle-admin-item"
        style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}
      >
        {props.html && <p dangerouslySetInnerHTML={{ __html: props.html }} />}
        {separador_dinero && <p className={'text-gray'}>{plata_fmt(props.texto)}</p>}
        {separador_miles && <p className={'text-gray'}>{cantidad_fmt(props.texto)}</p>}
        {props.texto !== undefined && !separador_miles && !separador_dinero && (
          <p className={'text-gray'}>{props.texto}</p>
        )}
        {props.info_icon && <>{props.info_icon}</>}
      </div>

      {props.imagen && !props.show_image_alert && !props.es_protegido && (
        <p className={img_extra_class}>
          <a target={'_blank'} href={props.imagen}>
            <img alt={props.titulo} src={props.imagen} className={'max-min-img-admin'} />
          </a>
        </p>
      )}

      {props.imagen && !props.show_image_alert && props.es_protegido && (
          <p className={img_extra_class}>
            <a target={'_blank'} onClick={() => setmodalVisible(true)}>
              <img alt={props.titulo} src={props.imagen} className={'max-min-img-admin'} />
            </a>
          </p>
      )}
    </div>
  )
})

export default AdminItemDetail
