import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { observer } from 'mobx-react-lite'
import { useStores } from '../../models/root-store/root-store-context'
import { useHistory } from 'react-router-dom'
import { IonAlert, useIonViewWillEnter } from '@ionic/react'
import { RUTAS, RUTAS_WEB_TRANSPORTISTA } from '../../constants'
import LinkButton from '../LinkButton'
import { cambiarModo, useApiToaster, useClearStore } from '../../helpers-ts'
import { HojaRutaApi } from '../../services/api'
import { Capacitor } from '@capacitor/core'
import ChatModal from '../Chat/ChatModal'

type AdminSidebarProps = {}

export const AdminSidebar: React.FC<AdminSidebarProps> = observer(() => {
  const rootStore = useStores()
  const { loginStore, adminStore, transportistaStore } = rootStore
  const [showAlert, setShowAlert] = useState(false)
  const history = useHistory()
  const url = history.location.pathname
  const [ModalChatVisible, setModalChatVisible] = useState(false)
  const [Url, setUrl] = useState(url)
  const apiToaster = useApiToaster()
  const clearStore = useClearStore()

  const url_matches_any = (...args: string[]) => {
    return Array.prototype.some.call(args, (value) => url.indexOf(value) !== -1)
  }

  const active_if_url_matches = (ruta: string) => {
    return url_matches_any(ruta) ? 'active' : ''
  }

  const isAdministracionOpened = () => {
    return url_matches_any(
      'admin/configuracion',
      'admin/staff',
      'admin/tipo-vehiculo',
      RUTAS.ADMIN_RUBRO,
      RUTAS.ADMIN_CATEGORIA,
      RUTAS.ADMIN_PREGUNTAS_FRECUENTES,
    )
  }

  const isMovimientosOpened = () => {
    return url_matches_any('admin/pedido', 'admin/orden_venta', 'admin/hoja_ruta', RUTAS.ADMIN_BALANCE)
  }

  const isFacturacionOpened = () => {
    return url_matches_any(
      RUTAS.ADMIN_RENDICION,
      RUTAS.ADMIN_PAGO_PROVEEDOR,
      RUTAS.ADMIN_PAGO_CLIENTE,
      RUTAS.ADMIN_FACTURA_PROVEEDOR,
      RUTAS.ADMIN_FACTURA_CLIENTE,
    )
  }

  const isModeracionOpened = () => {
    return url_matches_any(
      'admin/driver/pendiente',
      'admin/proveedor/pendiente',
      'admin/producto/pendiente',
      'admin/vehiculo/pendiente',
    )
  }

  const isMaestrosOpened = () => {
    return url_matches_any(
      'admin/cliente',
      'admin/proveedor/moderado',
      'admin/driver/moderado',
      'admin/vehiculo/moderado',
    )
  }

  const isProductosOpened = () => {
    return url_matches_any('admin/precio', 'admin/inventario', 'admin/producto/moderado')
  }

  const isApiOpened = () => {
    return url_matches_any(RUTAS.ADMIN_API_CONFIG, RUTAS.ADMIN_API_VIEW, RUTAS.ADMIN_API_DOC, RUTAS.ADMIN_API_LOG)
  }

  const defaultValues = {
    administracion: isAdministracionOpened(),
    movimientos: isMovimientosOpened(),
    moderacion: isModeracionOpened(),
    facturacion: isFacturacionOpened(),
    maestros: isMaestrosOpened(),
    productos: isProductosOpened(),
    api: isApiOpened(),
  }

  const [opened, setOpened] = useState(defaultValues)

  const toggleOpened = (e: any) => {
    e.preventDefault()
    const openedC = { ...opened }
    const item = e.target.dataset.menu
    // @ts-ignore
    openedC[item] = !openedC[item]
    setOpened(openedC)
    return false
  }

  useEffect(() => {
    if (adminStore.sidebarType === 'full') {
      setOpened({
        administracion: isAdministracionOpened(),
        movimientos: isMovimientosOpened(),
        moderacion: isModeracionOpened(),
        facturacion: isFacturacionOpened(),
        maestros: isMaestrosOpened(),
        productos: isProductosOpened(),
        api: isApiOpened(),
      })
    } else {
      setOpened({
        administracion: false,
        movimientos: false,
        moderacion: false,
        facturacion: false,
        maestros: false,
        productos: false,
        api: false,
      })
    }
  }, [Url])

  useIonViewWillEnter(async () => {
    setUrl(history.location.pathname)
  })

  return (
    <aside className="left-sidebar" data-sidebarbg="skin5">
      <ChatModal
        pedido={transportistaStore.hojaRutaActiva?.pedido as any}
        setVisible={() => setModalChatVisible(false)}
        visible={ModalChatVisible}
      />
      <div className="scroll-sidebar">
        <IonAlert
          isOpen={showAlert}
          onDidDismiss={() => setShowAlert(false)}
          header={'Cambiar a modo cliente'}
          message={'Confirma que desea cambiar a modo cliente?'}
          buttons={[
            {
              text: 'Cancelar',
              role: 'cancel',
              cssClass: 'btn-secondary',
            },
            {
              text: 'Cambiar',
              cssClass: 'btn-primary',
              handler: () => {
                cambiarModo(rootStore, history, 10)
              },
            },
          ]}
        />
        <nav className="sidebar-nav">
          {loginStore.modo_uso_app !== 30 && (
            <ul id="sidebarnav">
              <li className="sidebar-item">
                <a
                  onClick={() => {
                    history.push('/admin')
                    if (url == '/admin') {
                      window.location.reload()
                    }
                  }}
                  aria-expanded="false"
                >
                  <FIcon.Home />
                  <span className="hide-menu ml-3">Inicio</span>
                </a>
              </li>

              <li className={opened.movimientos ? 'active' : ''}>
                <a
                  className={'has-arrow waves-effect waves-dark '}
                  data-menu={'movimientos'}
                  onClick={toggleOpened}
                  aria-expanded="false"
                  href={'#'}
                >
                  <FIcon.TrendingUp />
                  <span className="hide-menu ml-3" data-menu={'movimientos'} onClick={toggleOpened}>
                    Movimientos
                  </span>
                </a>
                <ul aria-expanded="false" className={'collapse ' + (opened.movimientos ? 'in' : '')}>
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={'/admin/pedido'} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Pedidos</span>
                      </LinkButton>
                    </li>
                  )}
                  <li className="sidebar-item">
                    <LinkButton href={'/admin/orden_venta'} urlConditionalClassName={'active'}>
                      <span className="hide-menu">Ordenes de Venta</span>
                    </LinkButton>
                  </li>
                  {!loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_BALANCE} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Balance</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={'/admin/hoja_ruta'} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Hojas de ruta</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_TORRE_DE_CONTROL} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Torre de Control</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_BUSQUEDA} urlConditionalClassName={'active'}>
                        <span className="hide-menu">No encontrados</span>
                      </LinkButton>
                    </li>
                  )}
                </ul>
              </li>

              {loginStore.administrador && (
                <li className={opened.facturacion ? 'active' : ''}>
                  <a
                    className={'has-arrow waves-effect waves-dark '}
                    data-menu={'facturacion'}
                    onClick={toggleOpened}
                    aria-expanded="false"
                    href={'#'}
                  >
                    <FIcon.FileText />
                    <span className="hide-menu ml-3" data-menu={'facturacion'} onClick={toggleOpened}>
                      Facturación
                    </span>
                  </a>
                  <ul aria-expanded="false" className={'collapse ' + (opened.facturacion ? 'in' : '')}>
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_FACTURA_CLIENTE} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Facturas cliente</span>
                      </LinkButton>
                    </li>

                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_FACTURA_PROVEEDOR} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Facturas proveedor</span>
                      </LinkButton>
                    </li>

                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_PAGO_CLIENTE} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Pagos cliente</span>
                      </LinkButton>
                    </li>

                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_PAGO_PROVEEDOR} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Pagos proveedor</span>
                      </LinkButton>
                    </li>

                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_RENDICION} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Rendición</span>
                      </LinkButton>
                    </li>
                  </ul>
                </li>
              )}
              {loginStore.administrador && (
                <li className="sidebar-item">
                  <a
                    className={active_if_url_matches(RUTAS.ADMIN_REPORTES)}
                    onClick={() => history.push(RUTAS.ADMIN_REPORTES)}
                    aria-expanded="false"
                  >
                    <FIcon.PieChart />
                    <span className="hide-menu ml-3">Reportes</span>
                  </a>
                </li>
              )}

              <li className={opened.moderacion ? 'active' : ''}>
                <a
                  className={'has-arrow waves-effect waves-dark '}
                  data-menu={'moderacion'}
                  onClick={toggleOpened}
                  aria-expanded="false"
                  href={'#'}
                >
                  <FIcon.Sliders />
                  <span className="hide-menu ml-3" data-menu={'moderacion'} onClick={toggleOpened}>
                    Moderación
                  </span>
                </a>
                <ul aria-expanded="false" className={'collapse ' + (opened.moderacion ? 'in' : '')}>
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <a
                        className={active_if_url_matches('admin/proveedor/pendiente/')}
                        onClick={() => {
                          setOpened({
                            administracion: false,
                            movimientos: false,
                            moderacion: true,
                            maestros: false,
                            facturacion: false,
                            productos: false,
                            api: false,
                          })
                          history.push('/admin/proveedor/pendiente/')
                        }}
                        aria-expanded="false"
                      >
                        <span className="hide-menu">Proveedores</span>
                      </a>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={'/admin/transportista/pendiente'} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Drivers</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={'/admin/vehiculo/pendiente'} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Vehículos</span>
                      </LinkButton>
                    </li>
                  )}
                  <li className="sidebar-item">
                    <LinkButton href={'/admin/producto/pendiente'} urlConditionalClassName={'active'}>
                      <span className="hide-menu">Productos</span>
                    </LinkButton>
                  </li>
                </ul>
              </li>

              {loginStore.administrador && (
                <li className={opened.maestros ? 'active' : ''}>
                  <a
                    className={'has-arrow waves-effect waves-dark '}
                    data-menu={'maestros'}
                    onClick={toggleOpened}
                    aria-expanded="false"
                    href={'#'}
                  >
                    <FIcon.Globe />
                    <span className="hide-menu ml-3" data-menu={'maestros'} onClick={toggleOpened}>
                      Maestros
                    </span>
                  </a>
                  <ul aria-expanded="false" className={'collapse ' + (opened.maestros ? 'in' : '')}>
                    {loginStore.administrador && (
                      <li className="sidebar-item">
                        <LinkButton href={'/admin/cliente'} urlConditionalClassName={'active'}>
                          <span className="hide-menu">Clientes</span>
                        </LinkButton>
                      </li>
                    )}
                    {loginStore.administrador && (
                      <li className="sidebar-item">
                        <LinkButton href={RUTAS.ADMIN_FABRICANTES('moderado')} urlConditionalClassName={'active'}>
                          <span className="hide-menu">Proveedores</span>
                        </LinkButton>
                      </li>
                    )}
                    {loginStore.administrador && (
                      <li className="sidebar-item">
                        <LinkButton href={'/admin/transportista/moderado'} urlConditionalClassName={'active'}>
                          <span className="hide-menu">Drivers</span>
                        </LinkButton>
                      </li>
                    )}
                    {loginStore.administrador && (
                      <li className="sidebar-item">
                        <LinkButton href={'/admin/vehiculo/moderado'} urlConditionalClassName={'active'}>
                          <span className="hide-menu">Vehículos</span>
                        </LinkButton>
                      </li>
                    )}
                  </ul>
                </li>
              )}

              <li className={opened.productos ? 'active' : ''}>
                <a
                  className={'has-arrow waves-effect waves-dark '}
                  data-menu={'productos'}
                  onClick={toggleOpened}
                  aria-expanded="false"
                  href={'#'}
                >
                  <FIcon.Package />
                  <span className="hide-menu ml-3" data-menu={'productos'} onClick={toggleOpened}>
                    Catálogo
                  </span>
                </a>
                <ul aria-expanded="false" className={'collapse ' + (opened.productos ? 'in' : '')}>
                  <li className="sidebar-item">
                    <LinkButton href={'/admin/producto/moderado'} urlConditionalClassName={'active'}>
                      <span className="hide-menu">Productos</span>
                    </LinkButton>
                  </li>
                  <li className="sidebar-item">
                    <LinkButton href={'/admin/precio'} urlConditionalClassName={'active'}>
                      <span className="hide-menu">Precios</span>
                    </LinkButton>
                  </li>
                  <li className="sidebar-item">
                    <LinkButton href={'/admin/inventario'} urlConditionalClassName={'active'}>
                      <span className="hide-menu">Inventario</span>
                    </LinkButton>
                  </li>
                  <li className="sidebar-item">
                    <LinkButton href={RUTAS.ADMIN_COMENTARIO_PRODUCTO} urlConditionalClassName={'active'}>
                      <span className="hide-menu">Comentarios de productos</span>
                    </LinkButton>
                  </li>
                </ul>
              </li>

              <li className={opened.administracion ? 'active' : ''}>
                <a
                  className={'has-arrow waves-effect waves-dark '}
                  data-menu={'administracion'}
                  onClick={toggleOpened}
                  aria-expanded="false"
                  href={'#'}
                >
                  <FIcon.Tool />
                  <span className="hide-menu ml-3" data-menu={'administracion'} onClick={toggleOpened}>
                    Configuración
                  </span>
                </a>
                <ul aria-expanded="false" className={'collapse ' + (opened.administracion ? 'in' : '')}>
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={'/admin/staff'} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Usuarios</span>
                      </LinkButton>
                    </li>
                  )}
                  {!loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton
                        href={RUTAS.ADMIN_FABRICANTE_DETAIL('pendiente', loginStore.fabricante_id)}
                        urlConditionalClassName={'active'}
                      >
                        <span className="hide-menu">Mi perfil</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item active">
                      <LinkButton href={'/admin/configuracion'} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Parámetros</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={'/admin/tipo-vehiculo'} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Tipos de vehículo</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_RUBRO} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Rubros</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_CATEGORIA} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Categorías</span>
                      </LinkButton>
                    </li>
                  )}
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_PREGUNTAS_FRECUENTES} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Preguntas frecuentes</span>
                      </LinkButton>
                    </li>
                  )}
                </ul>
              </li>
              <li className={opened.api ? 'active' : ''}>
                <a
                  className={'has-arrow waves-effect waves-dark '}
                  data-menu={'api'}
                  onClick={toggleOpened}
                  aria-expanded="false"
                  href={'#'}
                >
                  <FIcon.GitPullRequest />
                  <span className="hide-menu ml-3" data-menu={'api'} onClick={toggleOpened}>
                    API
                  </span>
                </a>
                <ul aria-expanded="false" className={'collapse ' + (opened.api ? 'in' : '')}>
                  {loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_API_CONFIG} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Configuración</span>
                      </LinkButton>
                    </li>
                  )}
                  <li className="sidebar-item">
                    <LinkButton href={RUTAS.ADMIN_API_DOC} urlConditionalClassName={'active'}>
                      <span className="hide-menu">Documentación</span>
                    </LinkButton>
                  </li>
                  {/*<li className="sidebar-item">*/}
                  {/*  <LinkButton href={RUTAS.ADMIN_API_LOG} urlConditionalClassName={'active'}>*/}
                  {/*    <span className="hide-menu">Registro / Log</span>*/}
                  {/*  </LinkButton>*/}
                  {/*</li>*/}
                  {!loginStore.administrador && (
                    <li className="sidebar-item">
                      <LinkButton href={RUTAS.ADMIN_API_VIEW} urlConditionalClassName={'active'}>
                        <span className="hide-menu">Mi Acceso</span>
                      </LinkButton>
                    </li>
                  )}
                </ul>
              </li>
              {!loginStore.administrador && (
                <li className="sidebar-item">
                  <a className="" onClick={() => setShowAlert(true)} aria-expanded="false">
                    <FIcon.ShoppingCart />
                    <span className="hide-menu ml-3">La tienda</span>
                  </a>
                </li>
              )}
              {loginStore.administrador && (
                <li className="sidebar-item">
                  <a className="" onClick={() => history.push(RUTAS.ROOT)} aria-expanded="false">
                    <FIcon.ShoppingCart />
                    <span className="hide-menu ml-3">La tienda</span>
                  </a>
                </li>
              )}
              <li className="sidebar-item">
                <a
                  onClick={() => {
                    if (!window.confirm('Confirma que desea cerrar sesión?')) {
                      return
                    }
                    if (Capacitor.getPlatform() !== 'web') {
                      history.push(RUTAS.LOGOUT)
                    } else {
                      clearStore()
                      document.location.href = '/'
                    }
                  }}
                >
                  <FIcon.LogOut />
                  <span className="ml-3">Cerrar sesión</span>
                </a>
              </li>
            </ul>
          )}
          {loginStore.modo_uso_app === 30 && (
            <ul id="sidebarnav">
              {rootStore.isDebug && (
                <li className={'sidebar-item mt-3'}>
                  <div
                    onClick={() => {
                      rootStore.environment.api.getHojaRutaActiva().then((result) => {
                        if (result.kind === 'ok') {
                          if (result.data) {
                            let hoja = result.data as HojaRutaApi
                            transportistaStore.setHojaRutaActiva(hoja)
                            if (hoja && hoja.next) {
                              transportistaStore.setItinerarioActivo(hoja.next)
                              // history.push(RUTAS.CHAT(hoja.next.pedidos[0].id))
                            }
                          }
                        }
                      })
                    }}
                  >
                    <FIcon.User />
                    <span onClick={() => setModalChatVisible(true)} className="ml-3">
                      Hoja de ruta activa
                    </span>
                  </div>
                </li>
              )}
              <li className={'sidebar-item mt-3'}>
                <LinkButton href={RUTAS_WEB_TRANSPORTISTA.INICIO} urlConditionalClassName={'active'}>
                  <FIcon.User />
                  <span className="ml-3">Mi perfil</span>
                </LinkButton>
              </li>
              <li className={'sidebar-item'}>
                <LinkButton href={RUTAS_WEB_TRANSPORTISTA.BALANCE} urlConditionalClassName={'active'}>
                  <FIcon.DollarSign />
                  <span className="ml-3">Balance</span>
                </LinkButton>
              </li>
              <li className={'sidebar-item'}>
                <LinkButton href={RUTAS_WEB_TRANSPORTISTA.VEHICULOS} urlConditionalClassName={'active'}>
                  <FIcon.Truck />
                  <span className="ml-3">Vehículos</span>
                </LinkButton>
              </li>
              <li className="sidebar-item">
                <a
                  onClick={() => {
                    setShowAlert(true)
                  }}
                >
                  <FIcon.ShoppingCart />
                  <span className="ml-3">La tienda</span>
                </a>
              </li>
              <li className="sidebar-item">
                <a
                  onClick={() => {
                    if (!window.confirm('Confirma que desea cerrar sesión?')) {
                      return
                    }
                    if (Capacitor.getPlatform() !== 'web') {
                      history.push(RUTAS.LOGOUT)
                    } else {
                      clearStore()
                      document.location.href = '/'
                    }
                  }}
                >
                  <FIcon.LogOut />
                  <span className="ml-3">Cerrar sesión</span>
                </a>
              </li>
            </ul>
          )}
        </nav>
      </div>
    </aside>
  )
})

export default AdminSidebar
