import React, {useImperativeHandle, useState} from 'react';
import {DashboardModal} from "@uppy/react";
import {Uppy} from "@uppy/core";
import Webcam from "@uppy/webcam";

const ImageEditor = require('@uppy/image-editor')
const Espanol = require('@uppy/locales/lib/es_ES')

type DashboardModalComponentProps = {
  ref: any
  id_input: string
  file: any
  file_preview: any
  facingMode?: string
};

export const DashboardModalComponent: React.FC<DashboardModalComponentProps> = React.forwardRef((props, ref) => {
  const [Open, setOpen] = useState(false)
  const facingMode = props.facingMode || 'user'

  useImperativeHandle(ref, () => ({
    toggle() {
      setOpen(!Open)
    },
    open() {
      setOpen(true)
    },
    close() {
      setOpen(false)
    }
  }));

  const uppy = new Uppy({
    allowMultipleUploads: false,
    locale: Espanol,
    restrictions: {allowedFileTypes: ['image/*'], maxNumberOfFiles: 1},
  })

  uppy.use(Webcam, {
    videoConstraints: {
      facingMode: facingMode,
    }
  }).use(ImageEditor, {
    id: 'ImageEditor',
    quality: 0.8,
    cropperOptions: {
      viewMode: 1,
      background: false,
      autoCropArea: 1,
      responsive: true,
    },
  })

  uppy.on('complete', (result: any) => {
    let data = result.successful[0]
    const file = uppy.getFile(data.id)
    props.file(file.data)
    props.file_preview(file.preview)
  })

  React.useEffect(() => {
    return () => uppy.close()
  }, [])


  return <DashboardModal
    id={props.id_input}
    uppy={uppy}
    closeModalOnClickOutside
    open={Open}
    onRequestClose={() => {
      setOpen(!Open)
      uppy.reset()
    }}
    plugins={['Webcam', 'ImageEditor']}
    metaFields={[{id: 'name', name: 'Nombre', placeholder: 'Nombre del archivo'},]}/>
});

export default DashboardModalComponent;
