import './SubHeader.css'

import React from 'react'
import * as FIcon from 'react-feather'
import { useHistory } from 'react-router'
import { RUTAS } from '../constants'
import { ICONOS } from '../helpers-ts/iconos'

export interface Link {
  titulo: string
  ruta?: string
  callback?: any
}
export interface SubHeaderProps {
  links?: Link[]
  Movil?: boolean
  noContainer?: boolean
}

export const SubHeader: React.FC<SubHeaderProps> = (props) => {
  const history = useHistory()
  const links = props.links ? props.links : []
  const Movil = props.Movil ? props.Movil : false

  const Flecha = (index: number) => {
    if (index < links.length - 1) {
      return <FIcon.ChevronRight size={Movil ? 20 : 15} />
    }
    return null
  }

  const abiLogo: any = (
    <a className="link-sub-header">
      <div className="" onClick={() => history.push(RUTAS.ROOT)}>
        {ICONOS.abi}
        <FIcon.ChevronRight size={Movil ? 20 : 15} />
      </div>
    </a>
  )

  return (
    <div className="margen-subheader sub-header-container">
      <div className={`d-flex align-items-center ${Movil || props.noContainer ? 'px-3' : 'container-lg'}`}>
        {abiLogo}
        {!!props.links &&
          props.links.map((link, index) => (
            <a
              key={index}
              onClick={() => {
                if (link.ruta) {
                  history.push(link.ruta)
                }
                if (!!link.callback) {
                  link.callback()
                }
              }}
              style={
                index === links.length - 1
                  ? { display: 'flex', alignItems: 'center', fontWeight: 'bold', color: 'var(--gris-oscuro-abi)' }
                  : {
                      display: 'flex',
                      alignItems: 'center',
                      fontWeight: 'bolder',
                      color: 'var(--gris-oscuro-abi)',
                    }
              }
            >
              <div className="link-sub-header text-subheader-cliente ">{link.titulo}</div>
              {Flecha(index)}
            </a>
          ))}
      </div>
    </div>
  )
}
