import React, {useEffect, useRef, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {registerLocale} from 'react-datepicker'
import es from 'date-fns/locale/es'
import esPhone from 'react-phone-input-2/lang/es.json'
import PhoneInput from 'react-phone-input-2'
import {CKEditor} from 'ckeditor4-react'
import Select from 'react-select'
import AsyncSelect from 'react-select/async'
import InputMask from 'react-input-mask'
import {fecha_fmt} from '../../helpers'
import ReactDOM from 'react-dom' // No eliminar esto (Cleave)!
import 'cleave.js/dist/addons/cleave-phone.py'
import Cleave from 'cleave.js/react'
import {useDebounce} from "../../helpers-ts"
import * as FIcon from 'react-feather'

registerLocale('es', es)
const numeral = require('numeral')

type AdminItemFieldProps = {
  titulo: string
  tipo: string
  campo: string
  valor: any
  error?: any
  set_dato: any
  columnas: number
  columnas_sm?: number
  restriccion?: boolean | null
  deshabilitado?: boolean | null
  horizontal?: boolean | null
  textAreaRows?: number
  help_text?: string
  opciones_select?: any
  placeholder?: string
  select_placeholder?: string
  select_no_options_text?: string
  select_multiple?: boolean
  select_onchange?: any
  numero_minimo?: number
  extra_css?: string
  set_dato_blur?: any
  campo_blur?: boolean
  verificar_firebase_habilitado?: boolean
  verificar_firebase_autenticado?: boolean
  verificar_firebase_func?: any
  verificar_firebase_error?: string
  verificar_firebase_reset?: any
  verificar_boton_clase?: string
}

// Tipos de formatos disponibles - solo algunos están disponibles en formato horizontal
// tipo: texto - sin formato especifico
// tipo: numero - le agrega separador de miles
// tipo: decimales - le agrega separador de decimales
// tipo: fecha - agrega un datepicker
// tipo: telefono - agrega un phoneinput
// tipo: boolean - agrega un checkbox
// tipo: textarea - agrega un text area y se le puede pasar una cantidad de rows con la propiedad textAreaRows
// tipo: boolean - agrega un checkbox
// tipo: email - formato email
// tipo: password - formato pasword
// tipo: ckeditor-mini - ckeditor con 3 propiedades
// tipo: ckeditor-full - ckeditor con todas las propiedades por defecto

//numero_minimo: Es el valor mínimo aceptado por el input

// campo_blur: Campos que disparan el evento onBlur
// set_dato_blur: Función que se dispara al hacer onBlur sobre el campo marcado

export const AdminItemField: React.FC<AdminItemFieldProps> = observer((props) => {
  const textAreaRows = props.textAreaRows || 2
  const extra_css = props.extra_css || ''
  const columnas_sm = props.columnas_sm || props.columnas
  const [ShowFirebaseMessageButton, setShowFirebaseMessageButton] = useState(false)
  const [FirebaseAutenticado, setFirebaseAutenticado] = useState(false)
  const [FirebaseHabilitado, setFirebaseHabilitado] = useState(true)
  const verificar_boton_clase = props.verificar_boton_clase || 'btn-outline-primary'
  // select async
  const [Value, setValue] = useState(null as any)
  const [SelectedValue, setSelectedValue] = useState(null as any)
  const callbackRef = useRef(null as any)
  const selectRef = useRef(null as any)
  const debouncedValue = useDebounce(Value, 500)
  const selectPlaceHolder = props.select_placeholder
    ? props.select_placeholder
    : 'Seleccionar ' + props.titulo.toLocaleLowerCase()

  if (props.restriccion === false) {
    return null
  }

  let deshabilitado = false
  if (props.deshabilitado !== undefined && props.deshabilitado !== null && !props.deshabilitado) {
    deshabilitado = true
  }

  const setDato = (campo: string, valor: any, extra_data?: any) => {
    props.set_dato(campo, valor, extra_data)
  }

  const setDatoBlur = (campo: string) => {
    props.set_dato_blur(campo)
  }

  const selectOnChange = (val: any) => {
    if (val) {
      setSelectedValue(val.value)
      //
      setDato(props.campo, val.value)
    } else {
      setDato(props.campo, null)
    }

    if (props.select_onchange) {
      props.select_onchange(val)
    }
  }

  useEffect(() => {
    setFirebaseAutenticado(props.verificar_firebase_autenticado === true)
  }, [props.verificar_firebase_autenticado])

  useEffect(() => {
    setFirebaseHabilitado(props.verificar_firebase_habilitado === true)
  }, [props.verificar_firebase_habilitado])

  useEffect(() => {
    if (props.tipo === 'select2async' && props.valor && props.valor.value) {
      setSelectedValue(props.valor.value)
    }
    setValue('') // Para mostrar el componente
  }, [props.valor])

  useEffect(() => {
    // select async
    if (props.opciones_select && callbackRef.current) {
      props.opciones_select(debouncedValue).then((opciones: any) => {
        callbackRef.current(opciones)
      })
    }
  }, [debouncedValue])

  if (props.horizontal !== undefined && props.horizontal) {
    return (
      <div className={'col-12 col-lg-6 col-xl-' + props.columnas + ' ' + extra_css}>
        <div className={'row my-2'}>
          <div className={'col-6'}>
            <label className={'font-weight-bold text-gray'} id={props.campo}>
              {props.titulo}
            </label>
          </div>
          <div className={'col-6'}>
            {props.tipo === 'numero' && (
              <input
                autoComplete={'off'}
                disabled={deshabilitado}
                type={'text'}
                className={'form-control'}
                id={props.campo}
                value={numeral(props.valor).format('0,0')}
                onBlur={(evt) => {
                  let numero = numeral(evt.target.value).value()
                  if (props.numero_minimo !== undefined && props.numero_minimo > numero) {
                    numero = props.numero_minimo
                    setDato(props.campo, numero)
                  }
                }}
                onChange={(evt) => {
                  let numero = numeral(evt.target.value).value()
                  setDato(props.campo, numero)
                }}
              />
            )}
            {props.tipo === 'texto' && (
              <input
                autoComplete={'off'}
                disabled={deshabilitado}
                type={props.tipo}
                className={'form-control'}
                id={props.campo}
                value={props.valor !== undefined && props.valor !== null ? props.valor + '' : ''}
                onChange={(evt) => setDato(props.campo, evt.target.value)}
                placeholder={props.placeholder}
              />
            )}
            {props.tipo === 'fecha' && (
              <div style={{position: 'relative'}}>
                <InputMask
                  placeholder={props.titulo}
                  disabled={deshabilitado}
                  className={'form-control'}
                  mask="99/99/9999"
                  onChange={(date: any) => setDato(props.campo, date.target.value)}
                  value={props.valor}
                />
                <FIcon.Calendar size={22} color={'#4e4e4e'} style={{position: 'absolute', top: 'calc(50% - 11px)', right: '10px'}} />
              </div>
            )}
            {props.tipo === 'hora' && (
              <div style={{position: 'relative'}}>
                <InputMask
                  placeholder={props.titulo}
                  disabled={deshabilitado}
                  className={'form-control'}
                  mask="99:99"
                  onChange={(date: any) => setDato(props.campo, date.target.value)}
                  value={props.valor}
                />
                <FIcon.Clock size={22} color={'#4e4e4e'} style={{position: 'absolute', top: 'calc(50% - 11px)', right: '10px'}} />
              </div>
            )}
            {props.tipo === 'telefono' && (
              <PhoneInput
                disabled={deshabilitado}
                country={'py'}
                localization={esPhone}
                value={props.valor ? props.valor.toString() : ''}
                placeholder={'595 981 123 456'}
                onChange={(phone) => setDato(props.campo, phone)}
              />
            )}
            {props.tipo === 'boolean' && (
              <div>
                <input
                  autoComplete={'off'}
                  disabled={deshabilitado}
                  type={'checkbox'}
                  id={props.campo}
                  checked={props.valor === true}
                  onClick={() => {
                    setDato(props.campo, !props.valor)
                  }}
                />
                <label
                  id={props.campo}
                  onClick={() => {
                    setDato(props.campo, !props.valor)
                  }}
                >
                  Aprobado
                </label>
              </div>
            )}
          </div>
          {props.error && <div className={'text-danger form-error mb-3'}>{props.error}</div>}
        </div>
      </div>
    )
  } else {
    return (
      <div className={'col-12 col-lg-' + props.columnas + ' ' + extra_css}>
        <div className={'form-group'}>
          {props.titulo && <label className={'font-weight-bold text-gray'} id={props.campo}>
            {props.titulo}
          </label>}
          {props.tipo === 'numero' && (
            <input
              autoComplete={'off'}
              disabled={deshabilitado}
              type={'text'}
              className={'form-control'}
              id={props.campo}
              value={props.valor !== '' ? numeral(props.valor).format('0,0') : ''}
              onBlur={(evt) => {
                let numero = numeral(evt.target.value).value()
                if (props.numero_minimo !== undefined && props.numero_minimo > numero) {
                  numero = props.numero_minimo
                  setDato(props.campo, numero)
                }
              }}
              onChange={(evt) => {
                let numero = numeral(evt.target.value).value()
                setDato(props.campo, numero)
              }}
            />
          )}
          {props.tipo === 'decimales' && (
            <input
              autoComplete={'off'}
              disabled={deshabilitado}
              type={'text'}
              className={'form-control'}
              id={props.campo}
              value={props.valor}
              onBlur={(evt) => {
                setDato(props.campo, numeral(evt.target.value.replace('.', ',')).format('0.00'))
              }}
              onChange={(evt) => {
                setDato(props.campo, evt.target.value)
              }}
            />
          )}
          {(props.tipo === 'texto' || props.tipo === 'email' || props.tipo === 'password') && (
            <input
              autoComplete={'off'}
              disabled={deshabilitado}
              type={props.tipo}
              className={'form-control'}
              id={props.campo}
              value={props.valor !== undefined && props.valor !== null ? props.valor + '' : ''}
              onChange={(evt) => setDato(props.campo, evt.target.value)}
              onBlur={() => {
                if (props.campo_blur !== undefined && props.campo_blur) {
                  setDatoBlur(props.campo)
                }
              }}
              placeholder={props.placeholder}
            />
          )}
          {props.tipo === 'textarea' && (
            <textarea
              disabled={deshabilitado}
              className={'form-control'}
              rows={textAreaRows}
              id={props.campo}
              onChange={(evt) => setDato(props.campo, evt.target.value)}
              value={props.valor !== undefined ? props.valor + '' : ''}
              placeholder={props.placeholder}
            />
          )}
          {props.tipo === 'ckeditor-mini' && (
            <CKEditor
              initData={props.valor}
              onChange={(evt: any) => setDato(props.campo, evt.editor.getData())}
              config={{
                toolbar: [['Bold', 'Italic', 'Strike', 'Underline', '-', 'Subscript', 'Superscript']],
              }}
              readOnly={deshabilitado}
            />
          )}
          {props.tipo === 'ckeditor-full' && (
            <CKEditor
              initData={props.valor}
              onChange={(evt: any) => setDato(props.campo, evt.editor.getData())}
              readOnly={deshabilitado}
            />
          )}
          {props.tipo === 'fecha' && (
            <div style={{position: 'relative'}}>
              <InputMask
                placeholder={props.titulo}
                disabled={deshabilitado}
                className={'form-control'}
                mask="99/99/9999"
                onChange={(date: any) => setDato(props.campo, date.target.value)}
                value={props.valor}
              />
              <FIcon.Calendar size={22} color={'#4e4e4e'} style={{position: 'absolute', top: 'calc(50% - 11px)', right: '10px'}} />
            </div>
          )}
          {props.tipo === 'hora' && (
            <div style={{position: 'relative'}}>
              <InputMask
                placeholder={props.titulo}
                disabled={deshabilitado}
                className={'form-control'}
                mask="99:99"
                onChange={(date: any) => setDato(props.campo, date.target.value)}
                value={props.valor}
              />
              <FIcon.Clock size={22} color={'#4e4e4e'} style={{position: 'absolute', top: 'calc(50% - 11px)', right: '10px'}} />
            </div>
          )}
          {props.tipo === 'telefono-simple' && (
            <Cleave
              className={'form-control'}
              placeholder={props.titulo}
              disabled={deshabilitado}
              options={{ phone: true, phoneRegionCode: 'PY' }}
              value={props.valor}
              onChange={(event) => {
                setDato(props.campo, event.target.rawValue)
              }}
            />
          )}
          {props.tipo === 'telefono' && (
            <div className={'row'}>
              <div className={'col-8'}>
                <InputMask
                  disabled={deshabilitado}
                  className={'form-control'}
                  mask="0\999-999-999"
                  onChange={(date: any) => {
                    setDato(props.campo, date.target.value)
                  }}
                  value={props.valor}
                />
              </div>
              {!FirebaseAutenticado && (
                <div className={'col-4 d-flex justify-content-start align-items-center pl-0'}>
                  <button
                    className={
                      'd-flex btn justify-content-center ' +
                      verificar_boton_clase +
                      ' py-1' +
                      (FirebaseHabilitado ? '' : ' disabled')
                    }
                    title={'Verificar número de teléfono'}
                    onClick={() => {
                      if (FirebaseHabilitado) {
                        props.verificar_firebase_func()
                      }
                    }}
                  >
                    <span>Verificar</span>
                  </button>
                </div>
              )}
              {FirebaseAutenticado && (
                <div className={'col-4 d-flex justify-content-start align-items-center pl-0'}>
                  <button
                    className={'d-flex btn justify-content-center  ' + verificar_boton_clase + ' py-1'}
                    title={'Cambiar número de teléfono'}
                    onClick={() => props.verificar_firebase_reset()}
                  >
                    <span>Cambiar</span>
                  </button>
                </div>
              )}
              {(ShowFirebaseMessageButton || props.verificar_firebase_error) && (
                <div className={'text-danger ml-3 mt-2 pr-2'}>
                  {props.verificar_firebase_error
                    ? props.verificar_firebase_error
                    : 'Debe ingresar un número válido para poder verificar'}
                </div>
              )}
            </div>
          )}
          {props.tipo === 'select2' && props.opciones_select !== undefined && (
            <div>
              <Select
                isDisabled={deshabilitado}
                value={props.opciones_select.filter((opc: any) => opc.value == props.valor)[0]}
                onChange={selectOnChange}
                options={props.opciones_select}
                placeholder={
                  props.select_placeholder !== undefined
                    ? props.select_placeholder
                    : 'Seleccionar ' + props.titulo.toLocaleLowerCase()
                }
                isClearable
                isMulti={props.select_multiple}
                noOptionsMessage={
                  props.select_no_options_text ? (obj) => props.select_no_options_text || null : undefined
                }
                theme={(theme) => ({
                  ...theme,
                  colors: {
                    ...theme.colors,
                    primary: '#303AB2',
                    neutral50: 'black',
                  },
                })}
              />
            </div>
          )}
          {props.tipo === 'select2async' && props.opciones_select !== undefined && (
            <div>
              <AsyncSelect
                ref={selectRef}
                value={props.valor && props.valor.value ? props.valor.value : ''}
                onChange={selectOnChange}
                loadingMessage={() => 'Cargando..'}
                loadOptions={(val: string, callback: any) => {
                  setValue(val)
                  callbackRef.current = callback
                }}
                defaultOptions={props.opciones_select(null)}
                placeholder={SelectedValue || selectPlaceHolder}
                isClearable
                noOptionsMessage={
                  props.select_no_options_text ? (obj) => props.select_no_options_text || null : undefined
                }
                theme={(theme) => ({
                  ...theme,
                  colors: {
                    ...theme.colors,
                    primary: '#303AB2',
                    neutral50: 'black',
                  },
                })}
                onBlur={() => {
                  if (props.campo_blur !== undefined && props.campo_blur) {
                    setDatoBlur(props.campo)
                  }
                }}
              />
            </div>
          )}
          {props.tipo === 'boolean' && (
            <div>
              <input
                autoComplete={'off'}
                disabled={deshabilitado}
                type={'checkbox'}
                id={props.campo}
                checked={props.valor === true}
                onClick={() => {
                  setDato(props.campo, !props.valor)
                }}
              />
              <label
                id={props.campo}
                onClick={() => {
                  setDato(props.campo, !props.valor)
                }}
              >
                {props.titulo}
              </label>
            </div>
          )}
          {props.help_text !== undefined && <small>{props.help_text}</small>}
        </div>
        {props.error && <div className={'text-danger form-error mb-3'}>{props.error}</div>}
      </div>
    )
  }
})

export default AdminItemField
